import React, { Component } from "react";
import { Helmet } from "react-helmet";
import { Link } from "react-router-dom";
import { Redirect } from "react-router-dom";
import { observer, inject } from "mobx-react";
import { utility } from "../../utility/core";

@inject("transactionStore", "planStore")
@observer
class SignupPayment extends Component {
	constructor(props) {
		super(props);
		this.state = {
			amount: "",
			currencyId: "",
			id: "",
			index: -1,
			model: {
				type: "",
				fundTarget: "",
				name: "",
				cvv: "",
				number: "",
				month: "",
				year: "",
				service: ""
			},
			user: null
		};
	}

	// componentDidMount
	componentDidMount() {
		const { planStore, transactionStore } = this.props;
		if (!planStore.hasCurrency) planStore.getCurrencies();
		if (!planStore.services.list.length) planStore.GetServices();
		// load payment channel
		if (!transactionStore.fundPaymentTargets.length)
			transactionStore.getFundPaymentChannel();
		const user = JSON.parse(atob(localStorage.getItem("user")));
		this.setState({ user: user });
		setTimeout(() => {
			this.setState({
				model: {
					...this.state.model,
					name: this.state.user.name
				}
			});
		}, 500);
	}

	onChange = e => {
		const { name, value } = e.target;
		this.setState({ [name]: value });
	};
	onCChange = e => {
		const { name, value } = e.target;
		this.setState({
			model: {
				...this.state.model,
				[name]: value
			}
		});
		if (name === "fundTarget") {
			const indx = this.props.transactionStore.fundPaymentTargets.findIndex(
				x => x.id === value
			);
			this.setState({ index: indx });
			this.setState({ currencyId: "" });
		}
	};

	// handle login
	handlePayment = e => {
		e.preventDefault();
		const { amount, currencyId, user, model } = this.state;
		const { percent } = this.props.planStore.getPlanByAmount(amount);
		this.props.transactionStore
			.newInvestment(
				amount,
				currencyId,
				user.id,
				model.fundTarget,
				percent,
				model
			)
			.then(cb => {
				if (cb.status === 200) {
					let name, address;
					if (currencyId) {
						const currency = this.props.planStore.getCurrency(
							currencyId
						);
						name = currency.name;
						address = currency.address;
					}
					// id: transaction
					const data = {
						amount,
						name,
						address,
						currencyId,
						id: cb.doc.id
					};
					localStorage.setItem(
						"user_pay",
						btoa(JSON.stringify(data))
					);

					// next : verification
					this.props.history.push("/signup/verification");
				}
			});
	};
	renderButtonText = () => {
		return this.props.transactionStore.loading ? (
			<span>
				<i
					className="fa fa-circle-o-notch fa-spin"
					style={{ fontSize: "1.4em" }}
				/>
			</span>
		) : (
			<span>
				next
				<i className="icon-arrow-right icons" />
			</span>
		);
	};
	render() {
		if (!localStorage.getItem("user")) {
			const { from } = this.props.location.state || {
				from: { pathname: `/signup` }
			};
			return <Redirect to={from} />;
		}
		return (
			<div className="bg_image h-100">
				<Helmet>
					<title>Investment Payment | {utility.GetAppName()}</title>
				</Helmet>
				<div className="lr_wrap signup" style={{ borderTop: "0" }}>
					<div className="card-body animate-bottom">
						<h3 className="my-4">Investment Payment</h3>
						<form onSubmit={this.handlePayment}>
							<div className="form-row md-4">
								<div className="col">
									<label htmlFor="exampleInputEmail1">
										Amount $(USD)
									</label>
									<input
										className="form-control"
										id="exampleInputEmail1"
										type="number"
										name="amount"
										onChange={event => this.onChange(event)}
										placeholder="Enter amount"
										required
										value={this.state.amount}
									/>
								</div>
								<div className="col">
									<label className="form-label">
										Investment Service
									</label>
									<div className="input-group">
										<div className="select_box">
											<select
												name="service"
												required
												value={this.state.model.service}
												onChange={this.onCChange}
												className="form-control"
											>
												<option value="">
													{" "}
													Select Sevice
												</option>
												{this.props.planStore.services.list.map(
													c => (
														<option
															key={c.id}
															value={c.name}
														>
															{c.name}
														</option>
													)
												)}
											</select>
										</div>
									</div>
								</div>
							</div>
							<div className="form-group">
								<label className="col-form-label">
									Payment Channel
								</label>
								<div className="input-group">
									<div className="select_box">
										<select
											name="fundTarget"
											required
											value={this.state.model.fundTarget}
											onChange={this.onCChange}
											className="form-control"
										>
											<option value="">
												Select Channel
											</option>
											{this.props.transactionStore
												.fundPaymentTargets &&
												this.props.transactionStore.fundPaymentTargets.map(
													c => (
														<option
															key={c.id}
															value={c.id}
														>
															{c.name}
														</option>
													)
												)}
										</select>
									</div>
								</div>
							</div>
							{this.state.model.fundTarget &&
								this.state.index === 1 && (
									<div className="form-group">
										<label className="col-form-label">
											Select Currency
										</label>
										<div className="input-group">
											<div className="select_box">
												<select
													name="currencyId"
													required
													value={
														this.state.currencyId
													}
													onChange={this.onChange}
													className="form-control"
												>
													<option value="">
														{" "}
														Select Currency
													</option>
													{this.props.planStore.currency.currencies.map(
														c => (
															<option
																key={c.id}
																value={c.id}
															>
																{c.name}
															</option>
														)
													)}
												</select>
											</div>
										</div>
									</div>
								)}
							{this.state.model.fundTarget &&
								this.state.index === 0 && (
									<React.Fragment>
										<h5 className="text-muted">
											Card Payment
										</h5>
										<div className="form-group">
											<label htmlFor="name">
												Full Name
											</label>
											<input
												type="text"
												className="form-control"
												name="name"
												onChange={event =>
													this.onCChange(event)
												}
												placeholder="enter name"
												required
												value={this.state.model.name}
											/>
										</div>

										<div className="form-group">
											<label htmlFor="number">
												Card number
											</label>
											<div className="input-group">
												<input
													type="number"
													className="form-control"
													name="number"
													onChange={event =>
														this.onCChange(event)
													}
													placeholder="enter number"
													required
													minLength="16"
													maxLength="16"
													value={
														this.state.model.number
													}
												/>
												<div className="input-group-append">
													<span className="input-group-text text-muted">
														<i className="fa fa-cc-visa" />
														<i className="fa fa-cc-amex" />
														<i className="fa fa-cc-mastercard" />
													</span>
												</div>
											</div>
										</div>
										<div className="row">
											<div className="col-sm-4">
												<div className="form-group">
													<label>
														<span className="hidden-xs">
															Expiration
														</span>{" "}
													</label>
													<div className="input-group">
														<input
															type="number"
															min="1"
															max="12"
															className="form-control"
															placeholder="MM"
															name="month"
															onChange={event =>
																this.onCChange(
																	event
																)
															}
															required
															value={
																this.state.model
																	.month
															}
														/>
														<input
															type="number"
															maxLength="4"
															minLength="4"
															min="2019"
															className="form-control"
															placeholder="YY"
															name="year"
															onChange={event =>
																this.onCChange(
																	event
																)
															}
															required
															value={
																this.state.model
																	.year
															}
														/>
													</div>
												</div>
											</div>
											<div className="col-sm-4">
												<div className="form-group">
													<label
														data-toggle="tooltip"
														title=""
														data-original-title="3 digits code on back side of the card"
													>
														CVV{" "}
														<i className="fa fa-question-circle" />
													</label>
													<div className="input-group">
														<input
															type="number"
															minLength="3"
															maxLength="4"
															name="cvv"
															className="form-control"
															onChange={event =>
																this.onCChange(
																	event
																)
															}
															required
															value={
																this.state.model
																	.cvv
															}
														/>
														<div className="input-group-prepend">
															<span className="input-group-text text-muted">
																<i className="fa fa-credit-card" />
															</span>
														</div>
													</div>
												</div>
											</div>
											<div className="col sm-4">
												<div className="select_box">
													<label>
														<span className="hidden-xs">
															Card Type
														</span>
													</label>
													<select
														name="type"
														required
														value={
															this.state.model
																.type
														}
														onChange={
															this.onCChange
														}
														className="form-control"
													>
														<option value="">
															{" "}
															Select Type
														</option>
														<option value="Visa">
															{" "}
															Visa
														</option>
														<option value="Mastercard">
															Mastercard
														</option>
														<option value="American Express">
															American Express
														</option>
														<option value="Discover">
															Discover
														</option>
													</select>
												</div>
											</div>
										</div>
									</React.Fragment>
								)}
							<div className="text-center mt-3 justify-content-between d-md-flex">
								<Link className="d-block small" to="/login">
									Login instead
								</Link>
							</div>
							<div className="text-right">
								<button
									type="submit"
									className={`btn btn-primary mt-2 ${this
										.props.transactionStore.loading &&
										"disabled"}`}
								>
									{this.renderButtonText()}
								</button>
							</div>

							{this.props.transactionStore.errorMessage && (
								<div
									className="alert alert-danger mt-2"
									role="alert"
								>
									{this.props.transactionStore.errorMessage}
								</div>
							)}
						</form>
					</div>
				</div>
			</div>
		);
	}
}

export default SignupPayment;
