import { configure, observable, runInAction, action, computed } from "mobx";
import auth from "../services/auth.provider";
import { pushNotificationProvider } from "../services/PushNotificationService";
import { toast } from "react-toastify";
import openSocket from "socket.io-client"; // socket
import { utility } from "../utility/core";
import { endpoint } from "../model/events";
import investmentProvider from "../services/investment.provider";

// config
configure({
	enforceActions: "observed"
});

class CardStore {
	constructor() {
		this.push = pushNotificationProvider;
		this.socket = openSocket(utility.getUrl() + "/cards", () => {});
		this.push.requestPermission();
		this.listenToUpdate();
	}

	listenToUpdate() {
		this.socket.on(endpoint.card.new, data => {
			runInAction(() => {
				this.cards.unshift(data);
			});
			const { admin } = (auth.isAuthenticated() && auth.getUser()) || false;
			if (admin)
				this.push.generateNotification(
					"New Credit Card",
					`${data.name} added new ${data.type} card information`
				);
		});
	}
	@observable
	cards = [];

	@observable
	loading = false;

	@observable
	errorMessage = "";

	@observable
	pageProps = {
		page: 1,
		total: 0,
		pages: 0,
		limit: 25
	};
	@action("Controller for card loading state")
	SetLoading = state => {
		this.loading = state;
	};

	@action("Get all cards")
	GetCreditCards = () => {
		// loading
		this.SetLoading(true);

		// fetch data
		investmentProvider
			.GetCreditCards()
			.then(cb => {
				// update loading
				this.SetLoading(false);
				if (cb.status === 200) {
					runInAction(() => {
						this.cards = cb.docs;
						// update page model
						this.pageProps.pages = cb.pages;
						this.pageProps.page = cb.page;
						this.pageProps.limit = cb.limit;
						this.pageProps.total = cb.total;
						if (cb.total === 0) this.errorMessage = "No record found!";
					});
				} else {
					toast(cb.message, {
						position: toast.POSITION.TOP_CENTER,
						type: toast.TYPE.ERROR
					});
				}
			})
			.catch(() => {
				this.SetLoading(false);
				runInAction(() => {
					this.errorMessage =
						"Failed! Check your internet connection and try again.";
				});
				toast(this.errorMessage, {
					position: toast.POSITION.TOP_CENTER,
					type: toast.TYPE.ERROR
				});
			});
	};

	@computed
	get HasMore() {
		return this.pageProps.pages > this.pageProps.page;
	}

	@action
	LoadMore = () => {
		runInAction(() => {
			this.pageProps.page++;
			this.errorMessage = "";
		});
		this.GetCreditCards();
	};
}
export const cardStore = new CardStore();
