import React, { Component } from "react";
import auth from "../../../services/auth.provider";
import { inject, observer } from "mobx-react";

@inject("statisticsStore")
@observer
class WithdrawStatistics extends Component {
	componentDidMount() {
		if (
			!this.props.statisticsStore.users &&
			!this.props.statisticsStore.loading
		)
			this.props.statisticsStore.GetData();
	}

	render() {
		const { admin } = auth.getUser();
		return (
			<React.Fragment>
				{admin && (
					<div className="col-xl-4 col-sm-6 mb-4">
						<div className="box NEO">
							<div className="box-head d-flex align-items-center justify-content-between">
								<h4 title="withdraw request">Withdraw</h4>
								<i className="icon-paypal icon" />
							</div>
							<div className="box-body d-flex align-items-center justify-content-between">
								<h6 title="Investment">
									{Intl.NumberFormat("en-US").format(
										this.props.statisticsStore.withdrawalRequest
									)}
								</h6>
							</div>
						</div>
					</div>
				)}
			</React.Fragment>
		);
	}
}

export default WithdrawStatistics;
