import React, { Component } from "react";
import ActivePage from "../../partials/activepage";
import { Helmet } from "react-helmet";
import FundRequestList from "./list";
import { CSSTransition } from "react-transition-group";
import { utility } from "../../../utility/core";

class FundRequest extends Component {
	render() {
		const title = "Investment Request";
		return (
			<React.Fragment>
				<Helmet>
					<title>
						{title} | {utility.GetAppName()}
					</title>
				</Helmet>
				<ActivePage title={title} />
				<div className="row">
					<CSSTransition
						in={true}
						appear={true}
						timeout={1000}
						classNames="fade"
					>
						<FundRequestList title={title} />
					</CSSTransition>
				</div>
			</React.Fragment>
		);
	}
}

export default FundRequest;
