import React, { Component } from "react";
import { Helmet } from "react-helmet";
import logo from "../assets/images/logo_icon_72.png";
import { Link } from "react-router-dom";
import { Redirect } from "react-router-dom";
import auth from "../services/auth.provider";
import { utility } from "../utility/core";

// mobx
import { observer, inject } from "mobx-react";

@inject("userStore")
@observer
class Login extends Component {
	constructor(props) {
		super(props);
		this.state = {
			email: "",
			password: "",
			redirectPath: this.props
		};
	}
	componentWillMount() {
		this.props.userStore.checkAuthentication();
	}
	onChange = e => {
		this.setState({ [e.target.name]: e.target.value });
	};
	// handle login
	handleLogin = e => {
		e.preventDefault();
		const { email, password } = this.state;
		this.props.userStore.login(email, password);
	};
	renderButtonText = () => {
		return this.props.userStore.isloading ? (
			<span>
				<i
					className="fa fa-circle-o-notch fa-spin"
					style={{ fontSize: "1.4em" }}
				/>
			</span>
		) : (
			<span>
				Login
				<i className="fa fa-sign-in-alt" />
			</span>
		);
	};
	render() {
		if (this.props.userStore.isAuthenticated) {
			const { from } = this.props.location.state || {
				from: { pathname: `/account/${auth.getUser().id}/dashboard` }
			};
			return <Redirect to={from} />;
		}
		return (
			<div className="bg_image h-100">
				<Helmet>
					<title> Login | {utility.GetAppName()}</title>
				</Helmet>
				<div className="lr_wrap">
					<div className="card-body animate-bottom">
						<div className="lr_icon text-center">
							<a
								rel="noopener noreferrer"
								href={utility.getUrl()}
							>
								<img src={logo} alt="logo" />
							</a>
						</div>
						<h6 className="my-4 text-center text-uppercase">
							TESCO Login
						</h6>
						<form onSubmit={event => this.handleLogin(event)}>
							<div className="form-group">
								<label htmlFor="exampleInputEmail1">
									Email address
								</label>
								<input
									className="form-control"
									id="exampleInputEmail1"
									type="email"
									name="email"
									onChange={event => this.onChange(event)}
									placeholder="Enter email"
									required
								/>
							</div>
							<div className="form-group">
								<label htmlFor="exampleInputPassword1">
									Password
								</label>
								<input
									className="form-control"
									id="exampleInputPassword1"
									type="password"
									name="password"
									onChange={event => this.onChange(event)}
									placeholder="Password"
									required
								/>
							</div>
							<div className="form-group">
								<div className="form-check">
									<label className="form-check-label">
										<input
											className="form-check-input"
											type="checkbox"
										/>
										Remember Password
									</label>
								</div>
							</div>
							<button
								type="submit"
								className={`btn btn-default btn-block mt-2 ${this
									.props.userStore.isloading && "disabled"}`}
							>
								{this.renderButtonText()}
							</button>
							{this.props.userStore.errorMessage && (
								<div
									className="alert alert-danger mt-2"
									role="alert"
								>
									{this.props.userStore.errorMessage}
								</div>
							)}
						</form>
						<div className="text-center mt-3 justify-content-between d-md-flex">
							<Link className="d-block small" to="/signup">
								Register an account{" "}
								<i className="icon-arrow-right icon" />
							</Link>
							<Link
								className="d-block small"
								to="/reset-password"
							>
								Forgot Password?{" "}
								<i className="icon-arrow-right icon" />
							</Link>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Login;
