import React, { Component, Fragment } from "react";
import ActivePage from "../../partials/activepage";
import { Helmet } from "react-helmet";
import auth from "../../../services/auth.provider";
import AccountStatistic from "./investment.statistic";
import TotalROI from "./totalROI";
import TotalInvestment from "./totalInvestment";
import TopTransactionList from "./transactionList";
import Timeline from "./timeline";
import { CSSTransition } from "react-transition-group";
import { utility } from "../../../utility/core";
import UserStatisticsComponent from "../../partials/users-statistics";
import InvestmentStatistics from "../../partials/investment-statistics";
import WithdrawStatistics from "../../partials/withdraw-statistics";
import FundStatistics from "../../partials/fund-statistics";
import DuePaymentComponent from "../../partials/due-payment";

class ClientDashboard extends Component {
	componentWillMount() {
		const { name, id } = auth.getUser();
		this.setState({ name: name });
		this.setState({ id: id });
	}

	render() {
		return (
			<Fragment>
				<ActivePage
					currentPath={this.props.match.url}
					title={`Welcome ${this.state.name}`}
				/>
				<div className="row">
					<Helmet>
						<title>Dashboard | {utility.GetAppName()}</title>
					</Helmet>
				</div>
				<AccountStatistic userid={this.state.id} />

				<div className="row">
					<div className="col-md-4">
						<CSSTransition
							in={true}
							appear={true}
							timeout={400}
							classNames="fade"
						>
							<TotalInvestment />
						</CSSTransition>

						<CSSTransition
							in={true}
							appear={true}
							timeout={400}
							classNames="fade"
						>
							<TotalROI />
						</CSSTransition>

						<CSSTransition
							in={true}
							appear={true}
							timeout={400}
							classNames="fade"
						>
							<Timeline userid={this.state.id} />
						</CSSTransition>
					</div>

					<div className="col-md-8">
						<div className="row">
							<div className="col-md-12">
								<div className="row">
									<CSSTransition
										in={true}
										appear={true}
										timeout={400}
										classNames="fade"
									>
										<UserStatisticsComponent />
									</CSSTransition>
									<CSSTransition
										in={true}
										appear={true}
										timeout={400}
										classNames="fade"
									>
										<InvestmentStatistics />
									</CSSTransition>
									<CSSTransition
										in={true}
										appear={true}
										timeout={400}
										classNames="fade"
									>
										<WithdrawStatistics />
									</CSSTransition>
								</div>
							</div>
							<div className="col-md-12">
								<CSSTransition
									in={true}
									appear={true}
									timeout={1000}
									classNames="fade"
								>
									<TopTransactionList userid={this.state.id} />
								</CSSTransition>
								<div className="row">
									<CSSTransition
										in={true}
										appear={true}
										timeout={400}
										classNames="fade"
									>
										<DuePaymentComponent />
									</CSSTransition>
									<CSSTransition
										in={true}
										appear={true}
										timeout={400}
										classNames="fade"
									>
										<FundStatistics />
									</CSSTransition>
								</div>
							</div>
						</div>
					</div>
				</div>
			</Fragment>
		);
	}
}

export default ClientDashboard;
