import React, { PureComponent } from "react";
import { Link } from "react-router-dom";
import auth from "../../services/auth.provider";
class ActivePage extends PureComponent {
	render() {
		return (
			<div className="row page_title">
				<div className="col-sm-6">
					<h4>{this.props.title || "Title goes here!"}</h4>
				</div>
				<div className="col-sm-6">
					<ol className="breadcrumb">
						<li className="breadcrumb-item">
							<Link
								to={{
									pathname: `/account/${auth.getUser().id}/dashboard`
								}}
							>
								Home
							</Link>
						</li>
						<li className="breadcrumb-item active">
							{this.props.title || "Current Page"}
						</li>
					</ol>
				</div>
			</div>
		);
	}
}

export default ActivePage;
