import { observable } from "mobx";

class UserStoreModel {
  @observable
  id;
  @observable
  name;
  @observable
  email;
  address;
  @observable
  city;
  @observable
  zipcode;
  @observable
  admin;
  walletId;
  @observable
  suspended;
  @observable
  verified;
  joined;
  plan;
  @observable
  option = {
    notifyOnLogin: false,
    useEmailLogin: false
  };
  referrer;
  constructor(model) {
    this.id = model.id;
    this.name = model.name;
    this.email = model.email;
    this.address = model.address;
    this.city = model.city;
    this.zipcode = model.zipcode;
    this.admin = model.admin;
    this.walletId = model.walletId;
    this.suspended = model.suspended;
    this.verified = model.verified;
    this.joined = model.joined;
    this.plan = model.plan;
    this.option.notifyOnLogin = model.option.notifyOnLogin;
    this.option.useEmailLogin = model.option.useEmailLogin;
    this.referrer = model.referrer;
  }
}

export default UserStoreModel;
