import React, { Component } from "react";
import ActivePage from "../../partials/activepage";
import { Helmet } from "react-helmet";
import WithdrawRequestList from "./list";
import {utility} from "../../../utility/core";

class WithdrawRequest extends Component {
  state = {};
  render() {
    const title = "Withdraw Request";
    return (
      <React.Fragment>
        <Helmet>
          <title>{title} | {utility.GetAppName()}</title>
        </Helmet>
        <ActivePage title={title} />
        <div className="row">
          <WithdrawRequestList title={title} />
        </div>
      </React.Fragment>
    );
  }
}

export default WithdrawRequest;
