export const response = {
  asSingleResponse: body => {
    return {
      status: body.status,
      message: body.message,
      doc: body.doc,
      user: body.user,
      token: body.token
    };
  },
  asListResponse: body => {
    return {
      status: body.status,
      message: body.message,
      docs: body.docs,
      page: body.page,
      pages: body.pages,
      total: body.total,
      limit: body.limit
    };
  }
};
