import React, { Component, Fragment } from "react";
import ActivePage from "../../partials/activepage";
import { Helmet } from "react-helmet";
import { inject, observer } from "mobx-react";
import LoadingIndicator from "../../partials/loading/loading";
import NoContent from "../../partials/NoContent";
import $ from "jquery";
import { CSSTransition, TransitionGroup } from "react-transition-group";
import { utility } from "../../../utility/core";
import auth from "../../../services/auth.provider";
import PageNotFound from "../notfound";

@inject("planStore")
@observer
class ServiceComponent extends Component {
	state = {
		state: 0,
		name: "",
		id: ""
	};
	updateOrAdd = (state, model) => {
		this.setState({ state: state });
		this.setState({ name: model.name });
		this.setState({ id: model.id });
		$("#target").click();
	};
	componentDidMount() {
		if (auth.isAuthenticated() && auth.getUser().admin)
			this.props.planStore.GetServices();
	}

	content = () => {
		if (this.props.planStore.services.loading) {
			return <LoadingIndicator useBackdrop={true} />;
		} else {
			if (this.props.planStore.services.list.length === 0) {
				return (
					<NoContent message={this.props.planStore.services.errorMessage} />
				);
			} else {
				return (
					<TransitionGroup className="row">
						{this.props.planStore.services.list.map(item => (
							<CSSTransition
								key={item.id}
								appear={true}
								timeout={100}
								classNames="fade"
							>
								<div key={item.id} className="col-xl-3 col-sm-6 col-md-4">
									<div className="card mb-4 btn-shadow">
										<div id={item.id} className="card-body">
											<h6 className="mb-2">Service Name:</h6>
											<p>{item.name}</p>

											<button
												onClick={() => this.updateOrAdd(1, item)}
												type="button"
												className="btn btn-sm btn-light text-teal"
											>
												Update <i className="icon-note green icons" />
											</button>
										</div>
									</div>
								</div>
							</CSSTransition>
						))}
					</TransitionGroup>
				);
			}
		}
	};

	// handle value change
	onChange = e => {
		this.setState({ [e.target.name]: e.target.value });
	};

	//handle submit
	handleSubmit = () => {
		if (this.state.state === 0)
			this.props.planStore.addService(this.state.name);
		else this.props.planStore.UpdateService(this.state.id, this.state.name);
		// reset input
		this.setState({ name: "" });
		this.setState({ id: "" });
	};

	onRemove = id => {
		this.setState({ id: id });
		$("#removeTarget").click();
	};

	// render view
	render() {
		if (auth.isAuthenticated() && !auth.getUser().admin)
			return (
				<Fragment>
					<Helmet>
						<title>Resources not found | {utility.GetAppName()}</title>
					</Helmet>
					<PageNotFound />
				</Fragment>
			);
		const title = "Service";
		const action = this.state.state === 0 ? "Create New" : "Update";
		return (
			<Fragment>
				<ActivePage currentPath={this.props.match.url} title={title} />
				<Helmet>
					<title>
						{title} | {utility.GetAppName()}
					</title>
				</Helmet>
				<div className="row page_title" style={{ textAlign: "right" }}>
					<div className="col-sm-12">
						<button
							type="button"
							onClick={() => this.updateOrAdd(0, { name: "" })}
							className="btn btn-default"
						>
							<i className="icon-pencil icons" /> Add Service
						</button>
						<input
							type="hidden"
							data-target="#add-currency"
							data-toggle="modal"
							id="target"
						/>
					</div>
				</div>
				{this.content()}
				{/* new Service */}
				<div
					className="modal fade"
					id="add-currency"
					tabIndex="-1"
					role="dialog"
					aria-labelledby="hgk_08"
					aria-hidden="true"
				>
					<div className="modal-dialog" role="document">
						<div className="modal-content">
							<div className="modal-header">
								<h5 className="modal-title" id="hgk_08">
									{action} Service
								</h5>
							</div>
							<div className="modal-body">
								<form>
									<div className="row">
										<div className="form-group col-md-12">
											<label className="col-form-label">Name</label>
											<div className="input-group">
												<input
													type="text"
													autoFocus={true}
													name="name"
													required
													value={this.state.name}
													onChange={this.onChange}
													placeholder="enter name"
													className="form-control"
												/>
											</div>
										</div>
									</div>
								</form>
							</div>
							<div className="modal-footer">
								<button
									type="button"
									className="btn btn-light"
									data-dismiss="modal"
								>
									Cancel
								</button>
								<button
									className="btn btn-default"
									onClick={this.handleSubmit}
									type="button"
									data-dismiss="modal"
								>
									{action} Now <i className="icon-arrow-right icons" />
								</button>
							</div>
						</div>
					</div>
				</div>
				{/* Confirm box */}
				<input
					type="hidden"
					data-target="#cancelBox"
					data-toggle="modal"
					id="removeTarget"
				/>
				<div
					className="modal fade"
					id="cancelBox"
					tabIndex="-1"
					role="dialog"
					aria-labelledby="CancelTransaction"
					aria-hidden="true"
				>
					<div className="modal-dialog" role="document">
						<div className="modal-content">
							<div className="modal-header">
								<h5 className="modal-title" id="CancelTransaction">
									Remove Service!
								</h5>
							</div>
							<div className="modal-body">
								Are you sure you want to remove this service? It can't not be
								undone.
							</div>
							<div className="modal-footer">
								<button
									type="button"
									className="btn btn-default"
									data-dismiss="modal"
								>
									Cancel
								</button>
								<button
									onClick={this.handleApprove}
									type="button"
									data-dismiss="modal"
									className="btn btn-light"
								>
									Continue <i className="icon-arrow-right icons" />
								</button>
							</div>
						</div>
					</div>
				</div>
			</Fragment>
		);
	}
}

export default ServiceComponent;
