import { configure, action, runInAction, observable, computed } from "mobx";
import planeService from "../services/investment.provider";
import { currencyService } from "../services/currency.provider";
import { toast } from "react-toastify";

// accessbility configuration
configure({ enforceActions: "always" });

class PlanStore {
	@observable
	loading = false;

	@observable
	plans = [];
	singlePlans = [];

	@observable
	currency = {
		currencies: [],
		loading: false,
		errorMessage: ""
	};
	@observable
	services = {
		list: [],
		loading: false,
		errorMessage: "",
		plans: []
	};
	@observable
	errorMessage = "";

	@computed
	get isLoading() {
		return this.loading;
	}
	// check if investment plan is currently available
	@computed
	get isEmpty() {
		return this.plans.length === 0;
	}

	// get all investment plan
	@action("Get list of investment plans")
	getPlans = () => {
		runInAction(() => {
			this.loading = true;
			this.errorMessage = "";
		});
		planeService
			.getInvestmentPlans()
			.then(cb => {
				// update loadiing state
				runInAction(() => {
					this.loading = false;
					if (cb.status === 200) {
						this.plans = cb.docs;
						cb.docs.forEach(item =>
							item.plans.forEach(obj => this.singlePlans.push(obj))
						);
					} else {
						this.errorMessage = cb.message;
					}
				});
			})
			.catch(() => {
				runInAction(() => {
					this.loading = false;
					this.errorMessage =
						"Failed! Please check your internet connection and try again.";
				});
			});
	};

	/**
	 * get single plan
	 * @param {Number} amount current investment
	 * @returns {model}
	 */
	@action("Get investment plan that falls within a given amount")
	getPlanByAmount = amount => {
		const a = Math.round(amount);
		const plan = this.singlePlans
			.filter(i => i.max_amount >= a)
			.sort((a, b) => a.min_amount - b.min_amount);
		// console.log("Plan.store:80:35:", JSON.stringify(plan, null, 4));
		if (plan.length) return plan[0];
		return {};
	};

	@action
	getCurrencies = () => {
		runInAction(() => {
			this.currency.loading = true;
			this.currency.errorMessage = "";
		});
		currencyService
			.getCurrencies()
			.then(cb => {
				runInAction(() => {
					this.currency.loading = false;
					if (cb.status === 200) {
						this.currency.currencies = cb.docs;
						if (cb.docs.length === 0)
							this.currency.errorMessage =
								"No cryptocurrency is added yet, use the new button to create one.";
					} else {
						this.currency.errorMessage = cb.message;
					}
				});
			})
			.catch(() => {
				runInAction(() => {
					this.currency.loading = false;
					this.currency.errorMessage =
						"Failed! Please check your internet connection and try again.";
				});
			});
	};

	@computed
	get hasCurrency() {
		return this.currency.currencies.length > 0;
	}
	@action
	getExpectedIncome = (percent, amount) => {
		return Math.floor((percent / 100) * amount).toFixed(1);
	};

	@action
	getCurrency = id => {
		return this.currency.currencies.find(c => c.id === id);
	};

	// new currency
	@action("new currency")
	addNewCurrency = (name, icon, address) => {
		runInAction(() => {
			this.currency.loading = true;
			this.currency.errorMessage = "";
		});
		currencyService
			.NewCurrency(name, address, icon)
			.then(cb => {
				runInAction(() => {
					this.currency.loading = false;
					if (cb.status === 200) {
						this.currency.currencies.push(cb.doc);
					} else {
						this.currency.errorMessage = cb.message;
						alert(cb.message);
						toast(cb.message, {
							position: toast.POSITION.TOP_CENTER,
							type: toast.TYPE.ERROR
						});
					}
				});
			})
			.catch(() => {
				runInAction(() => {
					this.currency.loading = false;
					const message =
						"Failed! Please check your internet connection and try again.";
					this.currency.errorMessage = message;
					toast(message, {
						position: toast.POSITION.TOP_CENTER,
						type: toast.TYPE.ERROR
					});
				});
			});
	};

	@action
	updateCurrency = (id, name, icon, address) => {
		runInAction(() => {
			this.currency.loading = true;
			this.currency.errorMessage = "";
		});
		currencyService
			.updateCurrency(id, name, icon, address)
			.then(cb => {
				runInAction(() => {
					this.currency.loading = false;
					if (cb.status === 200) {
						this.currency.currencies.forEach(item => {
							if (item.id === id) {
								item.name = name;
								item.icon = icon;
								item.address = address;
							}
						});
					} else {
						this.currency.errorMessage = cb.message;
						toast(cb.message, {
							position: toast.POSITION.TOP_CENTER,
							type: toast.TYPE.ERROR
						});
					}
				});
			})
			.catch(() => {
				runInAction(() => {
					this.currency.loading = false;
					const message =
						"Failed! Please check your internet connection and try again.";
					this.currency.errorMessage = message;
					toast(this.currency.errorMessage, {
						position: toast.POSITION.TOP_CENTER,
						type: toast.TYPE.ERROR
					});
				});
			});
	};

	@action
	deleteCurrency = id => {
		runInAction(() => {
			this.currency.loading = true;
			this.currency.errorMessage = "";
		});
		currencyService
			.deleteCurrency(id)
			.then(cb => {
				runInAction(() => {
					this.currency.loading = false;
					if (cb.status === 200) {
						this.currency.currencies.splice(
							this.currency.currencies.findIndex(c => c.id === id),
							1
						);
					} else {
						this.currency.errorMessage = cb.message;
						toast(this.currency.errorMessage, {
							position: toast.POSITION.TOP_CENTER,
							type: toast.TYPE.ERROR
						});
					}
				});
			})
			.catch(() => {
				runInAction(() => {
					this.currency.loading = false;
					const message =
						"Failed! Please check your internet connection and try again.";
					this.currency.errorMessage = message;
					toast(message, {
						position: toast.POSITION.TOP_CENTER,
						type: toast.TYPE.ERROR
					});
				});
			});
	};
	// update investment plam
	@action("update investment plan")
	updateInvestmentPlan = (id, title, percent, min, max, desc) => {
		runInAction(() => {
			this.loading = true;
			this.errorMessage = "";
		});
		planeService
			.updateInvestmentPlan(id, title, min, max, desc, percent)
			.then(cb => {
				runInAction(() => {
					this.loading = false;
					if (cb.status === 200) {
						this.plans.forEach(item => {
							if (item.id === id) {
								item.title = title;
								item.min_amount = min;
								item.max_amount = max;
								item.description = desc;
								item.percent = percent;
							}
						});
					} else {
						this.errorMessage = cb.message;
						toast(cb.message, {
							position: toast.POSITION.TOP_CENTER,
							type: toast.TYPE.ERROR
						});
					}
				});
			})
			.catch(() => {
				runInAction(() => {
					this.loading = false;
					const message =
						"Failed! Please check your internet connection and try again.";
					this.errorMessage = message;
					toast(message, {
						position: toast.POSITION.TOP_CENTER,
						type: toast.TYPE.ERROR
					});
				});
			});
	};

	// remove investment plam
	@action("update investment plan")
	removeInvestmentPlan = id => {
		runInAction(() => {
			this.loading = true;
			this.errorMessage = "";
		});
		planeService
			.removeSingleInvestmentPlan(id)
			.then(cb => {
				runInAction(() => {
					this.loading = false;
					if (cb.status === 200) {
						this.plans.splice(this.plans.findIndex(c => c.id === id), 1);
					} else {
						this.errorMessage = cb.message;
						toast(cb.message, {
							position: toast.POSITION.TOP_CENTER,
							type: toast.TYPE.ERROR
						});
					}
				});
			})
			.catch(() => {
				runInAction(() => {
					this.loading = false;
					const message =
						"Failed! Please check your internet connection and try again.";
					this.errorMessage = message;
					toast(message, {
						position: toast.POSITION.TOP_CENTER,
						type: toast.TYPE.ERROR
					});
				});
			});
	};

	/**
	 * New Investment plan
	 *
	 */
	@action
	addNewInvestmentPlan = (title, percent, min, max, des, service) => {
		runInAction(() => {
			this.loading = true;
			this.errorMessage = "";
		});
		planeService
			.newInvestmentPlan(title, min, max, des, percent, service)
			.then(cb => {
				runInAction(() => {
					this.loading = false;
					if (cb.status === 200) {
						// this.plans.push(cb.doc);
						window.location.reload(true);
					} else {
						alert(cb.message);
					}
				});
			})
			.catch(() => {
				runInAction(() => {
					this.loading = false;
					toast(
						"Failed! Please check your internet connection and try again.",
						{
							position: toast.POSITION.TOP_CENTER,
							type: toast.TYPE.ERROR
						}
					);
				});
			});
	};

	/**
	 * Service
	 */
	addService = name => {
		runInAction(() => {
			this.services.loading = true;
			this.services.errorMessage = "";
		});
		planeService
			.NewService(name)
			.then(cb => {
				runInAction(() => {
					this.services.loading = false;
					if (cb.status === 200) {
						this.services.list.push(cb.doc);
						toast(cb.message, {
							type: "success",
							position: "bottom-center"
						});
					} else {
						toast(cb.message, {
							type: "error",
							position: "bottom-center"
						});
					}
				});
			})
			.catch(() => {
				runInAction(() => {
					this.services.loading = false;
					this.services.errorMessage =
						"Failed! Check internet connection and try again.";
					toast(this.services.errorMessage, {
						type: "error",
						position: "bottom-center"
					});
				});
			});
	};
	UpdateService = (id, name) => {
		runInAction(() => {
			this.services.loading = true;
			this.services.errorMessage = "";
		});
		planeService
			.UpdateService(id, name)
			.then(cb => {
				runInAction(() => {
					this.services.loading = false;
					this.services.list.find(c => c.id === id).name = name;
					if (cb.status === 200) {
						toast(cb.message, {
							type: "success",
							position: "bottom-center"
						});
					} else {
						toast(cb.message, {
							type: "error",
							position: "bottom-center"
						});
					}
				});
			})
			.catch(() => {
				runInAction(() => {
					this.services.loading = false;
					this.services.errorMessage =
						"Failed! Check internet connection and try again.";
					toast(this.services.errorMessage, {
						type: "error",
						position: "bottom-center"
					});
				});
			});
	};

	@action("Get service")
	GetServices = () => {
		runInAction(() => {
			this.services.loading = true;
			this.services.errorMessage = "";
		});
		planeService
			.GetServices()
			.then(cb => {
				runInAction(() => {
					this.services.loading = false;
					if (cb.status === 200) {
						this.services.list = cb.docs;
					} else {
						toast(cb.message, {
							type: "error",
							position: "bottom-center"
						});
					}
				});
			})
			.catch(err => {
				runInAction(() => {
					this.services.loading = false;
					this.services.errorMessage =
						"Failed! Check internet connection and try again.";
					toast(this.services.errorMessage, {
						type: "error",
						position: "bottom-center"
					});
				});
			});
	};
	@action
	GetPlansByService = id => {
		runInAction(() => {
			this.services.loading = true;
			this.services.errorMessage = "";
		});
		planeService
			.GetPlansByServices(id)
			.then(cb => {
				runInAction(() => {
					this.services.loading = false;
					if (cb.status === 200) {
						this.services.plans = cb.docs;
					} else {
						toast(cb.message, {
							type: "error",
							position: "bottom-center"
						});
					}
				});
			})
			.catch(() => {
				runInAction(() => {
					this.services.loading = false;
					this.services.errorMessage =
						"Failed! Check internet connection and try again.";
					toast(this.services.errorMessage, {
						type: "error",
						position: "bottom-center"
					});
				});
			});
	};
}

// single instance
export const planStore = new PlanStore();
// make public
