import React, { Component } from "react";
import auth from "../../../services/auth.provider";
import logo from "../../../assets/images/logo_icon_72.png";

class ProfileInfo extends Component {
	render() {
		const { name, email, joined, verified, admin } = auth.getUser();
		return (
			<div className="card mb-4 btn-shadow">
				<div className="card-body">
					<div className="row">
						<div className="col-md-12 text-center">
							<div className="user_pic_wrap">
								<img src={logo} alt="user_img" />
								{admin && (
									<span title="Admin" className="edit_pic">
										<label htmlFor="user_img" />
										<i class="icon-shield teal icons" />
									</span>
								)}
							</div>
							<div className="user_detail">
								<h5 className="mt-2">{name}</h5>
								<h6>
									<a href={`mailto:${email}`}> Email Address</a>
								</h6>
								<div>
									<span>Joined : </span>{" "}
									{Intl.DateTimeFormat("en-US", {
										year: "numeric",
										month: "short",
										day: "2-digit",
										weekday: "short"
									}).format(new Date(joined))}
								</div>
								{verified && (
									<span className="badge badge-pill badge-success">
										{" "}
										Account Verified
									</span>
								)}
								{!verified && (
									<span className="badge badge-pill badge-info">
										Verification Required!
									</span>
								)}
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default ProfileInfo;
