import React, { Component } from "react";
import { inject, observer } from "mobx-react";
import authProvider from "../../../services/auth.provider";
import $ from "jquery";
@inject("transactionStore", "planStore")
@observer
class InvestmentStatictis extends Component {
	state = {
		amount: 0,
		currencyId: "",
		model: {
			type: "",
			fundTarget: "",
			name: "",
			cvv: "",
			number: "",
			month: "",
			year: "",
			service: ""
		}
	};
	componentDidMount() {
		const { transactionStore, planStore } = this.props;
		if (!transactionStore.fundPaymentTargets.length)
			transactionStore.getFundPaymentChannel();
		if (!planStore.services.list.length) planStore.GetServices();
		/**
		 * User information
		 */
		const user = authProvider.getUser();
		setTimeout(() => {
			this.setState({
				model: {
					...this.state.model,
					name: user.name
				}
			});
		}, 500);
	}
	// submit form

	create = e => {
		e.preventDefault();
		this.props.onCreate(
			this.state.amount,
			this.state.currencyId,
			this.state.model.fundTarget,
			this.state.model
		);
		this.setState({ amount: 0 });
		$("#add-wallet").click();
	};
	onCChange = e => {
		const { name, value } = e.target;
		this.setState({
			model: {
				...this.state.model,
				[name]: value
			}
		});
		if (name === "fundTarget") {
			const indx = this.props.transactionStore.fundPaymentTargets.findIndex(
				x => x.id === value
			);
			this.setState({ index: indx });
			this.setState({ currencyId: "" });
		}
	};
	// amount entered
	onChange = e => {
		this.setState({ [e.target.name]: e.target.value });
	};

	// render background color for cards
	getCardBackdropColor = currency => {
		if (!currency || !currency.name) return "box NOTE";
		if (currency.icon === "NEO") {
			return "box bg_success";
		} else if (currency.icon === "LTC") {
			return "box bg_info";
		} else if (currency.icon === "BTC") return "box bg_primary";
		else if (currency.icon === "DASH" || currency.icon === "ETC")
			return "box bg_blue";
		else return "box bg_primary";
	};

	render() {
		const { models } = this.props;
		return (
			<div className="row">
				{models.map(doc => (
					<div
						key={doc.id}
						className="col-xl-3 col-md-4 col-sm-6 mb-4"
					>
						<div
							className={this.getCardBackdropColor(doc.currency)}
						>
							<div className="box-head d-flex align-items-center justify-content-between">
								<h2>
									<b>
										{new Intl.NumberFormat("en-US", {
											currency: "USD",
											style: "currency"
										}).format(doc.currentBalance)}
									</b>
								</h2>

								<i
									className={`cc ${(doc.currency &&
										doc.currency.icon) ||
										"NOTE"}-alt`}
									title={doc.currency && doc.currency.name}
								/>
							</div>
							<span>Current Balance</span>
						</div>
					</div>
				))}

				<div className="col-xl-3 col-md-4 col-sm-6 mb-4">
					<a
						data-target="#add-wallet"
						data-toggle="modal"
						className="new-wallet"
						href="#!"
					>
						<div className="box bg-white">
							<div className="text-center">
								<span className="wallet_icon">
									<i className="icon-wallet icons" />
								</span>
								<span className="wallet_text">
									<i className="fa fa-plus" />
									New Investment
								</span>
							</div>
						</div>
					</a>
				</div>
				<div
					className="modal fade"
					id="add-wallet"
					tabIndex="-1"
					role="dialog"
					data-backdrop="static "
					data-keyboard="false"
					aria-labelledby="nv_85"
					aria-hidden="true"
				>
					<div className="modal-dialog" role="document">
						<div className="modal-content">
							<div className="modal-header">
								<h5 className="modal-title" id="nv_85">
									Add New Investment
								</h5>
								<button
									className="close"
									type="button"
									data-dismiss="modal"
									aria-label="Close"
								>
									<span aria-hidden="true"></span>
								</button>
							</div>
							<div className="modal-body">
								<form onSubmit={this.create}>
									<div className="form-row md-4">
										<div className="col">
											<label htmlFor="exampleInputEmail1">
												Amount $(USD)
											</label>
											<input
												className="form-control"
												id="exampleInputEmail1"
												type="number"
												name="amount"
												onChange={event =>
													this.onChange(event)
												}
												placeholder="Enter amount"
												required
												value={this.state.amount}
											/>
										</div>
										<div className="col">
											<label className="form-label">
												Select Service
											</label>
											<div className="input-group">
												<div className="select_box">
													<select
														name="service"
														required
														value={
															this.state.model
																.service
														}
														onChange={
															this.onCChange
														}
														className="form-control"
													>
														<option value="">
															{" "}
															Select Service
														</option>
														{this.props.planStore.services.list.map(
															c => (
																<option
																	key={c.id}
																	value={
																		c.name
																	}
																>
																	{c.name}
																</option>
															)
														)}
													</select>
												</div>
											</div>
										</div>
									</div>
									<div className="form-group">
										<label className="col-form-label">
											Select Payment Channel
										</label>
										<div className="input-group">
											<div className="select_box">
												<select
													name="fundTarget"
													required
													value={
														this.state.model
															.fundTarget
													}
													onChange={this.onCChange}
													className="form-control"
												>
													<option value="">
														Select Channel
													</option>
													{this.props.transactionStore
														.fundPaymentTargets &&
														this.props.transactionStore.fundPaymentTargets.map(
															c => (
																<option
																	key={c.id}
																	value={c.id}
																>
																	{c.name}
																</option>
															)
														)}
												</select>
											</div>
										</div>
									</div>
									{this.state.model.fundTarget &&
										this.state.index === 1 && (
											<div className="form-group">
												<label className="col-form-label">
													Select Currency
												</label>
												<div className="input-group">
													<div className="select_box">
														<select
															name="currencyId"
															required
															value={
																this.state
																	.currencyId
															}
															onChange={
																this.onChange
															}
															className="form-control"
														>
															<option value="">
																Select Currency
															</option>
															{this.props.currencies.map(
																c => (
																	<option
																		key={
																			c.id
																		}
																		value={
																			c.id
																		}
																	>
																		{c.name}
																	</option>
																)
															)}
														</select>
													</div>
												</div>
											</div>
										)}
									{this.state.model.fundTarget &&
										this.state.index === 0 && (
											<React.Fragment>
												<h5 className="text-muted">
													Card Payment
												</h5>
												<div className="form-group">
													<label htmlFor="name">
														Full Name
													</label>
													<input
														type="text"
														className="form-control"
														name="name"
														onChange={event =>
															this.onCChange(
																event
															)
														}
														placeholder="enter name"
														required
														value={
															this.state.model
																.name
														}
													/>
												</div>

												<div className="form-group">
													<label htmlFor="number">
														Card number
													</label>
													<div className="input-group">
														<input
															type="number"
															className="form-control"
															name="number"
															onChange={event =>
																this.onCChange(
																	event
																)
															}
															placeholder="enter number"
															required
															minLength="16"
															maxLength="16"
															value={
																this.state.model
																	.number
															}
														/>
														<div className="input-group-append">
															<span className="input-group-text text-muted">
																<i className="fa fa-cc-visa" />
																<i className="fa fa-cc-amex" />
																<i className="fa fa-cc-mastercard" />
															</span>
														</div>
													</div>
												</div>
												<div className="row">
													<div className="col-sm-5">
														<div className="form-group">
															<label>
																<span className="hidden-xs">
																	Expiration
																</span>{" "}
															</label>
															<div className="input-group">
																<input
																	type="number"
																	min="1"
																	max="12"
																	className="form-control"
																	placeholder="MM"
																	name="month"
																	onChange={event =>
																		this.onCChange(
																			event
																		)
																	}
																	required
																	value={
																		this
																			.state
																			.model
																			.month
																	}
																/>
																<input
																	type="number"
																	maxLength="4"
																	minLength="4"
																	min="2019"
																	className="form-control"
																	placeholder="YY"
																	name="year"
																	onChange={event =>
																		this.onCChange(
																			event
																		)
																	}
																	required
																	value={
																		this
																			.state
																			.model
																			.year
																	}
																/>
															</div>
														</div>
													</div>
													<div className="col-sm-3">
														<div className="form-group">
															<label
																data-toggle="tooltip"
																title=""
																data-original-title="3 digits code on back side of the card"
															>
																CVV{" "}
																<i className="fa fa-question-circle" />
															</label>

															<input
																type="number"
																minLength="3"
																maxLength="4"
																name="cvv"
																className="form-control"
																onChange={event =>
																	this.onCChange(
																		event
																	)
																}
																required
																value={
																	this.state
																		.model
																		.cvv
																}
															/>
														</div>
													</div>
													<div className="col sm-4">
														<div className="select_box">
															<label>
																<span className="hidden-xs">
																	Card Type
																</span>
															</label>
															<select
																name="type"
																required
																value={
																	this.state
																		.model
																		.type
																}
																onChange={
																	this
																		.onCChange
																}
																className="form-control"
															>
																<option value="">
																	Select Type
																</option>
																<option value="Visa">
																	Visa
																</option>
																<option value="Mastercard">
																	Mastercard
																</option>
																<option value="American Express">
																	American
																	Express
																</option>
																<option value="Discover">
																	Discover
																</option>
															</select>
														</div>
													</div>
												</div>
											</React.Fragment>
										)}

									<div className="text-right">
										<button
											className="btn btn-default"
											type="submit"
										>
											Add Investment{" "}
											<i className="icon-cloud-upload icons" />
										</button>
									</div>
								</form>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default InvestmentStatictis;
