import { response } from "../model/response";
import api from "../services/auth.provider";
const base = "/api/users/";

class UserApi {
    /**
     * Logs user in
     * @param  {string} email user's email address
     * @param  {string} password user's password
     */
    async login(email, password) {
        // post data
        const res = await fetch(`${base}login`, {
            method: "POST",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
            },
            body: JSON.stringify({ email, password }),
        });
        //body
        if (res.ok) {
            const body = response.asSingleResponse(await res.json());
            return body;
        } else {
            alert(res.statusText);
        }
    }

    /**
     * Get single user
     * @param {string} id user id
     */

    async getUser(id) {
        const res = await fetch(`${base}/${id}`);
        const body = response.asSingleResponse(await res.json());
        return body;
    }

    /**
     * Get list of users within the system
     * @param {number} page page number
     * @param {number} limit number of record to return per request
     */

    async getUsers(page, limit) {
        const res = await fetch(`${base}?page=${page}&limit=${limit}`);
        const body = response.asListResponse(await res.json());
        return body;
    }

    /**
     * @param {string}id user's id
     * @param {number} page page number
     * @param {number} limit number of record to return per request
     */

    async getReferrals(id, page, limit) {
        const res = await fetch(`${base}/referrals/?page=${page}&limit=${limit}&id=${id}`);
        const result = response.asListResponse(await res.json());
        return result;
    }

    /**
     * Get user's timeline activity
     *  @param {string}id user's id
     * @param {number} page page number
     * @param {number} limit number of record to return per request
     */
    async getRecentActivities(id, page, limit) {
        const res = await fetch(`${base}/activity/?page=${page}&limit=${limit}&id=${id}`);
        const result = response.asListResponse(await res.json());
        return result;
    }

    /**
     * MAke user admin
     * @param {string} id user id
     */
    async makeAdmin(id) {
        const res = await fetch(`${base}s/admin/${id}`, {
            method: "PUT",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
                Authorization: `Bearer ${api.getToken()}`,
            },
            body: JSON.stringify({ id }),
        });

        const result = response.asSingleResponse(await res.json());
        return result;
    }
    /**
     * Remove admin rights from user | This is a secured route
     * @param {string} id user id
     */
    async removeAdminRights(id) {
        const res = await fetch(`${base}r/admin/${id}`, {
            method: "PUT",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
                Authorization: `Bearer ${api.getToken()}`,
            },
            body: JSON.stringify({ id }),
        });

        const result = response.asSingleResponse(await res.json());
        return result;
    }

    /**
     * Update user's information
     * @param {string}id user's id
     * @param {string} name update name
     * @param {string} address new address
     * @param {string} city new city
     * @param {number} zipcode new zipcode
     */
    async UpdateInformation(id, name, address, zipcode, city) {
        const res = await fetch(`${base}/update/${id}`, {
            method: "PUT",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
                Authorization: `Bearer ${api.getToken()}`,
            },
            body: JSON.stringify({ id, name, address, zipcode, city }),
        });
        const result = response.asSingleResponse(await res.json());
        return result;
    }
    /**
     * Remove user's account
     * @param {string} id user id
     */
    async removeAccount(id) {
        const res = await fetch(`${base}/account/remove/${id}`, {
            method: "DELETE",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
                Authorization: `Bearer ${api.getToken()}`,
            },
        });
        const result = response.asSingleResponse(await res.json());
        return result;
    }

    /**
     * Set or remove account suspension
     * @param {string} id user id
     * @param {boolean} state set or remove
     */
    async toggleAccount(id, state) {
        const url = state ? `s/suspension/${id}` : `/r/suspension/${id}`;
        const res = await fetch(`${base}${url}`, {
            method: "PUT",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
                Authorization: `Bearer ${api.getToken()}`,
            },
            body: JSON.stringify({ id }),
        });

        const result = response.asSingleResponse(await res.json());
        return result;
    }
    /**
     * Checks if a given user's account has been funded
     * @param {string} id user's id
     */
    async checkAccountFundStatus(id) {
        const res = await fetch(`${base}/account/status?id=${id}`, {
            method: "GET",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
                Authorization: `Bearer ${api.getToken()}`,
            },
        });

        const result = response.asSingleResponse(await res.json());
        return result;
    }
    /**
     * Create new user's account.
     * @param {string} referrer email address of ther referrer
     * @param {string} name user' name
     * @param {string} email user' email address
     * @param {string} walletId user's wallet address
     * @param {string} planId selected plan
     * @param {string} password user' password
     */
    async createUser(referrer, name, email, walletId, planId, password) {
        const res = await fetch(`${base}add`, {
            method: "POST",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
            },
            body: JSON.stringify({
                ref: referrer,
                name,
                password,
                email,
                walletId,
                planId,
            }),
        });

        const result = response.asSingleResponse(await res.json());
        return result;
    }
    /**
     * verify user's account
     * @param {string} id user id
     */
    async verifyUserAccount(id) {
        const res = await fetch(`${base}/account/verify/${id}`, {
            method: "PUT",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
            },
        });

        const result = response.asSingleResponse(await res.json());
        return result;
    }
    /** Resend emailf confirmation */
    async sendVerificationLink(id, name, email) {
        const res = await fetch(`${base}account/verification`, {
            method: "POST",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
            },
            body: JSON.stringify({ id, email, name }),
        });
        if (res.ok) {
            const result = response.asSingleResponse(await res.json());
            return result;
        } else {
            alert(res.statusText);
        }
    }
    /**
     * search users by email address
     * @param {string} email email address
     */
    async searchUser(email) {
        const res = await fetch(`${base}/search/email/?email=${email}`);
        const result = response.asListResponse(await res.json());
        return result;
    }

    /**
     * send change of password link
     * @param {string} email user email address
     */
    async SendPasswordResetLink(email) {
        const res = await fetch(`${base}reset-password/request/`, {
            method: "POST",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
            },
            body: JSON.stringify({ email }),
        });

        const result = response.asSingleResponse(await res.json());
        return result;
    }

    /**
     * Update existing password
     * @param {string} id user id
     * @param {string} email user email address
     * @param {string} code activation code
     * @param {string} resetId reset password id
     */
    async NewPassword(id, email, code, resetId, password) {
        const res = await fetch(`${base}reset-password/complete/`, {
            method: "POST",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
            },
            body: JSON.stringify({ email, id, code, resetId, password }),
        });

        const result = response.asSingleResponse(await res.json());
        return result;
    }
}
export const userApi = new UserApi();
