import React, { Component } from "react";
import AppEditor from "../../partials/editor";
import { EditorState } from "draft-js";
import { toast } from "react-toastify";

/**
 * @requires active a boolean value indicatiing if account is still active or not.
 */
class UserOption extends Component {
	// local state
	state = {
		target: 0,
		editorState: EditorState.createEmpty(),
		content: null,
		subject: ""
	};

	// update action target
	updateTarget = action => {
		this.setState({ target: action });
	};
	// on send clicked
	onSend = () => {
		if (this.state.subject && this.state.content) {
		} else {
			toast("Subject and message are required!", { type: "error" });
		}
	};
	onChange = (name, value) => {
		this.setState({ [name]: value });
	};
	editorChange = (editorState, val) => {
		this.setState({ editorState });
		this.setState({ content: val });
	};
	// confirm
	confirm = () => {
		const { target } = this.state;
		if (target === 0) {
			// remove
			this.props.onRemove();
		} else if (target === 1) {
			//suspend
			this.props.onSuspend();
		} else {
			// active
			this.props.onActivate();
		}
	};

	// render user account state option
	renderAccount = state => {
		return !state ? (
			<a
				data-target="#prompt"
				data-toggle="modal"
				className="dropdown-item text-warning"
				href="javacript:void(0)"
				onClick={() => this.updateTarget(1)}
			>
				Suspend Account
			</a>
		) : (
			<a
				data-target="#prompt"
				data-toggle="modal"
				onClick={() => this.updateTarget(2)}
				className="dropdown-item text-success"
				href="javacript:void(0)"
			>
				Activate Account
			</a>
		);
	};

	render() {
		const { active } = this.props;
		return (
			<React.Fragment>
				<div
					className="modal fade"
					id="send-mail"
					tabIndex="-1"
					role="dialog"
					aria-labelledby="sendMail"
					aria-hidden="true"
				>
					<div className="modal-dialog modal-lg" role="document">
						<div className="modal-content ">
							<div className="modal-header">
								<h5 className="modal-title" id="sendMail">
									Compose New Mail
								</h5>
							</div>
							<div className="modal-body pl-5 pr-5">
								<div className="row">
									<div className="form-group col-md-12">
										<label className="col-form-label">Subject</label>
										<div className="input-group">
											<input
												name="subject"
												type="text"
												value={this.state.subject}
												placeholder="enter subject"
												autoFocus={true}
												onChange={ev =>
													this.onChange(ev.target.name, ev.target.value)
												}
												className="form-control"
											/>
										</div>
									</div>
									<div className="form-group col-md-12">
										<label className="col-form-label">Message</label>
										<AppEditor
											onChange={this.editorChange}
											editorState={this.state.editorState}
										/>
									</div>
								</div>
							</div>
							<div className="modal-footer pb-2">
								<button data-dismiss="modal" className="btn btn-light">
									Cancel
								</button>
								<button
									className="btn btn-default"
									onClick={this.onSend}
									type="button"
									data-dismiss="modal"
								>
									Send Now <i className="icon-arrow-right icons" />
								</button>
							</div>
						</div>
					</div>
				</div>
				<div className="input-group-append">
					<button
						className="btn btn-outline dropdown-toggle btn-sm"
						type="button"
						data-toggle="dropdown"
						aria-haspopup="true"
						aria-expanded="false"
						style={{ padding: "0.6em" }}
					>
						Actions
					</button>
					<div className="dropdown-menu">
						{/* <a
							className="dropdown-item"
							data-target="#send-mail"
							data-toggle="modal"
							data-backdrop="static"
							href="javacript:void(0)"
						>
							Send Mail
						</a> */}
						{this.renderAccount(active)}
						<div role="separator" className="dropdown-divider" />
						<a
							onClick={() => this.updateTarget(0)}
							className="dropdown-item text-danger"
							data-target="#prompt"
							data-toggle="modal"
							href="javacript:void(0)"
						>
							Remove
						</a>
					</div>
				</div>
				<div
					className="modal fade"
					id="prompt"
					tabIndex="-1"
					role="dialog"
					aria-labelledby="sde_8"
					aria-hidden="true"
				>
					<div className="modal-dialog" role="document">
						<div className="modal-content">
							<div className="modal-header">
								<h5 className="modal-title" id="sde_8">
									Account Action!
								</h5>
							</div>
							<div className="modal-body">
								Are you sure you want to procceed? This action may not be
								undone.
							</div>
							<div className="modal-footer">
								<button data-dismiss="modal" className="btn btn-light">
									Cancel
								</button>
								<button
									className="btn btn-primary"
									type="button"
									onClick={this.confirm}
									data-dismiss="modal"
								>
									Continue <i className="icon-arrow-right icons" />
								</button>
							</div>
						</div>
					</div>
				</div>
			</React.Fragment>
		);
	}
}

export default UserOption;
