import React, { Component } from "react";
import { Helmet } from "react-helmet";
import ActivePage from "../partials/activepage";
import $ from "jquery";
import LoadingIndicator from "../partials/loading/loading";
import NoContent from "../partials/NoContent";
import { inject, observer } from "mobx-react";
import { CSSTransition } from "react-transition-group";
import { utility } from "../../utility/core";
import auth from "../../services/auth.provider";
import PageNotFound from "./notfound";

@inject("planStore")
@observer
class InvestmentPlan extends Component {
	state = {
		id: "",
		title: "",
		minAmount: 0,
		maxAmount: 0,
		percent: 0,
		description: "",
		state: 0,
		service: ""
	};

	//
	handleApprove = () => {
		this.props.planStore.removeInvestmentPlan(this.state.id);
	};
	// get plans
	componentDidMount() {
		if (auth.isAuthenticated() && auth.getUser().admin) {
			this.props.planStore.getPlans();
			if (!this.props.planStore.services.list.length)
				this.props.planStore.GetServices();
		}
	}
	// handle value change
	onChange = e => {
		this.setState({ [e.target.name]: e.target.value });
	};
	// add or update
	updateOrAdd = (state, model) => {
		this.setState({ state: state });
		this.setState({ title: model.title });
		this.setState({ minAmount: model.min_amount });
		this.setState({ maxAmount: model.max_amount });
		this.setState({ percent: model.percent });
		this.setState({ description: model.description });
		this.setState({ id: model.id });
		$("#target").click();
	};

	// remove investment plan
	onRemove = id => {
		this.setState({ id: id });
		$("#removeTarget").click();
	};
	handleSubmit = () => {
		if (this.state.state === 0)
			this.props.planStore.addNewInvestmentPlan(
				this.state.title,
				this.state.percent,
				this.state.minAmount,
				this.state.maxAmount,
				this.state.description,
				this.state.service
			);
		else
			this.props.planStore.updateInvestmentPlan(
				this.state.id,
				this.state.title,
				this.state.percent,
				this.state.minAmount,
				this.state.maxAmount,
				this.state.description,
				this.state.service
			);
	};
	plans = items => {
		return (
			<div className="row">
				{items.map(item => (
					<div key={item.id} className="col-xl-4 col-sm-6">
						<div className="card mb-4 btn-shadow">
							<div className="card-sm-header">
								<span className="wallet-heading">
									<h4>{item.title}</h4>
								</span>
							</div>
							<div id={item.id} className="card-body">
								<h6 className="mb-2">Description</h6>
								<p className="mb-4">{item.description}</p>
								<ul className="list_none p-0 wallet_info">
									<li>
										<span>Percent</span>
										<span>{item.percent}%</span>
									</li>
									<li>
										<span>Minimum Amount</span>
										<span>
											{Intl.NumberFormat("en-US", {
												currency: "USD",
												style: "currency"
											}).format(item.min_amount)}
										</span>
									</li>
									<li>
										<span>Maximum Amount</span>
										<span>
											{Intl.NumberFormat("en-US", {
												currency: "USD",
												style: "currency"
											}).format(item.max_amount)}
										</span>
									</li>
								</ul>
								<button
									onClick={() => this.updateOrAdd(1, item)}
									type="button"
									className="btn btn-light"
								>
									Update <i className="icon-note icons" />
								</button>
								<button
									type="button"
									onClick={() => this.onRemove(item.id)}
									className="btn btn-default pull-right"
								>
									Remove <i className="icon-trash icons" />
								</button>
							</div>
						</div>
					</div>
				))}
			</div>
		);
	};

	// content
	content = () => {
		if (this.props.planStore.loading) {
			return <LoadingIndicator useBackdrop={true} />;
		} else {
			if (this.props.planStore.plans.length === 0) {
				return <NoContent message={this.props.planStore.errorMessage} />;
			} else {
				return (
					<React.Fragment>
						{this.props.planStore.plans.map(item => (
							<React.Fragment key={item.id}>
								<h5 className="text-uppercase mb-3">{item.service}</h5>
								{this.plans(item.plans)}
							</React.Fragment>
						))}
					</React.Fragment>
				);
			}
		}
	};

	render() {
		if (auth.isAuthenticated() && !auth.getUser().admin)
			return (
				<React.Fragment>
					<Helmet>
						<title>Resources not found | {utility.GetAppName()}</title>
					</Helmet>
					<PageNotFound />
				</React.Fragment>
			);
		const title = "Investment Plan";
		const action = this.state.state === 0 ? "New" : "Update";
		return (
			<React.Fragment>
				<ActivePage currentPath={this.props.match.url} title={title} />
				<Helmet>
					<title>
						{title} | {utility.GetAppName()}
					</title>
				</Helmet>
				<div className="row page_title" style={{ textAlign: "right" }}>
					<div className="col-sm-12">
						<button
							type="button"
							onClick={() =>
								this.updateOrAdd(0, {
									title: "",
									min_amount: 0,
									max_amount: 0,
									percent: 0,
									description: "",
									id: "",
									service: ""
								})
							}
							className="btn btn-default"
						>
							<i className="icon-pencil icons" /> Add new Plan
						</button>
						<input
							type="hidden"
							data-target="#add-investmentPlan"
							data-toggle="modal"
							id="target"
						/>
					</div>
				</div>
				<CSSTransition in={true} appear={true} timeout={1000} classNames="fade">
					{this.content()}
				</CSSTransition>
				{/* new investmentPlan */}
				<div
					className="modal fade"
					id="add-investmentPlan"
					tabIndex="-1"
					role="dialog"
					aria-labelledby="df_89"
					aria-hidden="true"
				>
					<div className="modal-dialog" role="document">
						<div className="modal-content">
							<div className="modal-header">
								<h5 className="modal-title" id="df_89">
									{action} Investment Plan
								</h5>
							</div>
							<div className="modal-body">
								<form>
									<div className="row">
										<div className="form-group col-md-6">
											<label className="col-form-label">Title</label>
											<div className="input-group">
												<input
													type="text"
													autoFocus={true}
													name="title"
													required
													value={this.state.title}
													onChange={this.onChange}
													placeholder="enter title"
													className="form-control"
												/>
											</div>
										</div>
										<div className="form-group col-md-6">
											<label className="col-form-label">Percent</label>
											<div className="input-group">
												<input
													type="number"
													placeholder="enter percent"
													name="percent"
													value={this.state.percent}
													onChange={this.onChange}
													required
													className="form-control"
												/>
											</div>
										</div>
									</div>
									<div className="row">
										<div className="form-group col-md-6">
											<label className="col-form-label">Minimum Amount</label>
											<div className="input-group">
												<input
													type="number"
													autoFocus={true}
													name="minAmount"
													required
													value={this.state.minAmount}
													onChange={this.onChange}
													placeholder="enter minimum amount"
													className="form-control"
												/>
											</div>
										</div>
										<div className="form-group col-md-6">
											<label className="col-form-label">Maximum Amount</label>
											<div className="input-group">
												<input
													type="number"
													placeholder="enter maximum amount"
													name="maxAmount"
													value={this.state.maxAmount}
													onChange={this.onChange}
													required
													className="form-control"
												/>
											</div>
										</div>
									</div>
									<div className="row">
										<div className="form-group col-md-12">
											<label className="col-form-label">Select Service</label>
											<div className="input-group">
												<div className="select_box">
													<select
														name="service"
														required
														value={this.state.service}
														onChange={this.onChange}
														className="form-control"
													>
														<option value="">Select Service</option>
														{this.props.planStore.services.list &&
															this.props.planStore.services.list.map(c => (
																<option key={c.id} value={c.id}>
																	{c.name}
																</option>
															))}
													</select>
												</div>
											</div>
										</div>
										<div className="form-group col-md-12">
											<label className="col-form-label">Description</label>
											<div className="input-group">
												<input
													type="text"
													name="description"
													onChange={this.onChange}
													value={this.state.description}
													placeholder="enter description (optional)"
													className="form-control"
												/>
											</div>
										</div>
									</div>
								</form>
							</div>
							<div className="modal-footer">
								<button
									type="button"
									className="btn btn-light"
									data-dismiss="modal"
								>
									Cancel
								</button>
								<button
									className="btn btn-default"
									onClick={this.handleSubmit}
									type="button"
									data-dismiss="modal"
								>
									{action} Now <i className="icon-arrow-right icons" />
								</button>
							</div>
						</div>
					</div>
				</div>
				{/* Confirm box */}
				<input
					type="hidden"
					data-target="#cancelBox"
					data-toggle="modal"
					id="removeTarget"
				/>
				<div
					className="modal fade"
					id="cancelBox"
					tabIndex="-1"
					role="dialog"
					aria-labelledby="xcv_r"
					aria-hidden="true"
				>
					<div className="modal-dialog" role="document">
						<div className="modal-content">
							<div className="modal-header">
								<h5 className="modal-title" id="xcv_r">
									Remove Investment Plan!
								</h5>
							</div>
							<div className="modal-body">
								Are you sure you want to remove this investment plan? It can't
								not be undone.
							</div>
							<div className="modal-footer">
								<button
									type="button"
									className="btn btn-default"
									data-dismiss="modal"
								>
									Cancel
								</button>
								<button
									onClick={this.handleApprove}
									type="button"
									data-dismiss="modal"
									className="btn btn-light"
								>
									Continue <i className="icon-arrow-right icons" />
								</button>
							</div>
						</div>
					</div>
				</div>
			</React.Fragment>
		);
	}
}

export default InvestmentPlan;
