import React, { Component } from "react";
import {
	BrowserRouter as Router,
	Route,
	Switch,
	Redirect
} from "react-router-dom";
// import Home from "./components/home";
import Login from "./components/login";
import Auth from "./components/containers/authComponent";
import authProvider from "./services/auth.provider";
import AccountVerification from "./components/account-verification";
import Signup from "./components/signup";
import SignupPayment from "./components/signup/payment";
import SignupVerification from "./components/signup/verification";
import CompletedSignup from "./components/signup/completed";
import ForgotPassword from "./components/forgot-password";
import NewPasswordComponent from "./components/new-password";

// this
const Secured = ({ component: Component, ...rest }) => (
	<Route
		{...rest}
		render={props =>
			authProvider.isAuthenticated() === true ? (
				<Component {...props} />
			) : (
				<Redirect
					to={{
						pathname: "/login",
						state: { from: props.location },
						search: `?redirect=${props.location.pathname}`
					}}
				/>
			)
		}
	/>
);

// const SwitchWithSlide = animateSwitch(Switch, SlideOut);

class App extends Component {
	render() {
		return (
			<Router>
				<Switch>
					<Route exact path="/" component={Login} />
					<Route path="/login" component={Login} />
					<Route path="/reset-password" component={ForgotPassword} />
					<Route
						path="/account/new-password"
						component={NewPasswordComponent}
					/>
					<Route path="/signup" exact={true} component={Signup} />
					<Route path="/signup/payment" component={SignupPayment} />
					<Route path="/signup/verification" component={SignupVerification} />
					<Route path="/signup/completed" component={CompletedSignup} />
					<Route path="/verifyaccount" component={AccountVerification} />
					<Secured path="/account/:id" component={Auth} />
					<Route component={Login} />
				</Switch>
			</Router>
		);
	}
}

export default App;
