import React, { Component, Fragment } from "react";
import { observer, inject } from "mobx-react";
import $ from "jquery";
@inject("appStore")
@observer
class Notification extends Component {
	constructor() {
		super();
		this.state = { heading: "", message: "" };
	}
	componentDidMount() {
		this.props.appStore.getNotification();
	}
	getClassname = type => {
		switch (type) {
			case 1:
				return "primary";
			case 2:
				return "danger";
			case 3:
				return "info";
			case 4:
				return "warning";
			default:
				break;
		}
	};
	getIconClassname = type => {
		switch (type) {
			case 1:
				return "icon-wallet";
			case 2:
				return "icon-refresh";
			case 3:
				return "icon-info";
			case 4:
				return "fa fa-exclamation-triangle";
			default:
				return "icon-refresh";
		}
	};
	//handleView
	handleView = item => {
		this.setState({ heading: item.heading });
		this.setState({ message: item.message });

		$("#moreNoti").click();
		setTimeout(() => {
			if (!item.seen) this.props.appStore.updateSeenState(item.id);
		}, 500);
	};

	render() {
		return (
			<Fragment>
				<li className="nav-item dropdown menu-notification">
					<a
						href="#!"
						className="nav-link dropdown-toggle"
						id="alertsDropdown"
						data-toggle="dropdown"
						aria-haspopup="true"
						aria-expanded="false"
					>
						<i className="fa fa-fw fa-bell" />
						<span>
							<span className="d-none d-lg-inline-block">Notification </span>
							{this.props.appStore.totalUnSeen > 0 && (
								<span className="badge badge-pill badge-danger">
									{this.props.appStore.totalUnSeen}
								</span>
							)}
							{this.props.appStore.loading && (
								<i
									className="fa fa-circle-o-notch fa-spin"
									style={{ color: "#313D85 " }}
								/>
							)}
						</span>
						<span className="indicator text-danger d-none d-lg-block">
							<i className="fa fa-fw fa-circle align-middle" />
						</span>
					</a>
					<div
						className="dropdown-menu dropdown-notification dropdown-menu-right dropdown-content-body p-0"
						aria-labelledby="alertsDropdown"
					>
						<h6 className="dropdown-header">Notification:</h6>
						<ul className="list_none fc-scroll">
							{this.props.appStore.notifications.map((item, index) => (
								<li key={item.id}>
									<a
										className="dropdown-item"
										onClick={() => this.handleView(item)}
										href="#!"
									>
										<i
											className={`icon_box ${this.getIconClassname(
												index
											)} icons bg-${this.getClassname(item.type)}`}
										/>
										<div className="notification-content">
											<span className="small float-right text-muted">
												{Intl.DateTimeFormat("en-US").format(
													new Date(item.date).getTime()
												)}
											</span>
											<h6 className={`text-${this.getClassname(item.type)}`}>
												{item.heading || "No header specified!"}{" "}
												{!item.seen && (
													<span className="badge badge-pill badge-success">
														new
													</span>
												)}
											</h6>
											<div className="dropdown-message small">
												{item.message}
											</div>
										</div>
									</a>
								</li>
							))}
						</ul>
						<div className="dropdown-content-footer text-center">
							{this.props.appStore.hasMore && (
								<a
									onClick={() => this.props.appStore.loadMoreRecord()}
									className="small"
									href="#!"
								>
									View more notification
								</a>
							)}
						</div>
					</div>
				</li>

				<input
					id="moreNoti"
					type="hidden"
					data-toggle="modal"
					data-target=".bd-example-modal-sm"
				/>

				<div
					className="modal fade bd-example-modal-sm"
					tabIndex="-1"
					role="dialog"
					aria-labelledby="mySmallModalLabel"
					aria-hidden="true"
				>
					<div className="modal-dialog modal-sm">
						<div className="modal-content">
							<div className="modal-body">
								<p className="h5">
									{this.state.heading || "No heading is specified!"}
								</p>
								<hr />
								<p>{this.state.message}</p>
							</div>
							<div className="modal-footer">
								<button
									data-dismiss="modal"
									className="btn btn-light"
									type="button"
								>
									close
								</button>
							</div>
						</div>
					</div>
				</div>
			</Fragment>
		);
	}
}

export default Notification;
