import React, { Component } from "react";
import { Helmet } from "react-helmet";
import { Redirect } from "react-router-dom";
import { utility } from "../../utility/core";

class CompletedSignup extends Component {
	constructor(props) {
		super(props);
		this.state = {
			email: "",
			name: ""
		};
	}

	componentDidMount() {
		const raw = localStorage.getItem("user");
		const user = JSON.parse(atob(raw));
		this.setState({ email: user.email });
		this.setState({ name: user.name });
	}
	//handleLogin
	handleLogin = () => {
		localStorage.clear();
		setTimeout(() => {
			this.props.history.push("/login");
		}, 500);
	};

	render() {
		// if (!localStorage.getItem("user_pay")) {
		// 	const { from } = this.props.location.state || {
		// 		from: { pathname: `/signup/payment` }
		// 	};
		// 	return <Redirect to={from} />;
		// }
		return (
			<div className="bg_image h-100">
				<Helmet>
					<title>Sign up completed | {utility.GetAppName()}</title>
				</Helmet>
				<div className="lr_wrap signup " style={{ borderTop: "0" }}>
					<div className="card-body animate-bottom">
						<h2 className="text-center">Signup Completed!</h2>
						<hr />
						<div className="text-center m-4">
							<p className="lead">
								Thank you, your account is pending confirmation
								and verification.
							</p>
						</div>
						<p>
							You will receive an email once your payment is
							confirmed. Usually, this doesn't take more than{" "}
							<b>10 mins.</b> Check <b>{this.state.email}</b> in{" "}
							<b>5</b> to <b>10</b> mins time.
						</p>
						<p>Thank you for investing with us.</p>
						<div className="text-right">
							<button
								onClick={this.handleLogin}
								className={`btn btn-primary mt-2 `}
							>
								goto login page
								<i className="icon-arrow-right icons" />
							</button>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default CompletedSignup;
