import React, { Component, Fragment } from "react";
import { inject, observer } from "mobx-react";

@inject("planStore")
@observer
class PlanStatistic extends Component {
	componentDidMount() {
		this.props.planStore.getPlans();
	}
	render() {
		if (this.props.planStore.loading)
			return (
				<div className="text-center">
					<i
						style={{ fontSize: "2em", marginTop: "4em", color: "#00796b" }}
						className="fa fa-circle-o-notch fa-spin "
					/>
				</div>
			);
		return this.props.planStore.plans.map(item => (
			<Fragment key={item.id}>
				<h6 className="ml-3">{item.service}</h6>
				{item.plans.map(i => (
					<div key={i.id} className="list-group">
						<div className="list-group-item list-group-item-action flex-column align-items-startcard-body btn-shadow">
							<div className="d-flex w-100 justify-content-between">
								<h6 className="mb-1 text-primary">{i.title}</h6>
								<small>
									<span className="badge badge-primary">{i.percent}%</span>
								</small>
							</div>

							<h6 className="text-muted small">Minimum Amount</h6>
							<small className="balance_text">
								{Intl.NumberFormat("en-US", {
									currency: "USD",
									style: "currency"
								}).format(i.min_amount)}
							</small>

							<h6 className="text-muted small">Maximum Amount</h6>
							<small className="balance_text">
								{Intl.NumberFormat("en-US", {
									currency: "USD",
									style: "currency"
								}).format(i.max_amount)}
							</small>
						</div>
					</div>
				))}
			</Fragment>
		));
	}
}

export default PlanStatistic;
