import React, { Component } from "react";
import { inject, observer } from "mobx-react";
import LoadingIndicator from "../../partials/loading/loading";

@inject("transactionStore")
@observer
class AccountStatistic extends Component {
	componentDidMount() {
		this.props.transactionStore.getInvestment(this.props.userid);
	}

	render() {
		if (this.props.transactionStore.loading) {
			return <LoadingIndicator />;
		} else {
			return (
				<div className="row">
					{this.props.transactionStore.investments.slice(0, 4).map(item => {
						return (
							<div key={item.id} className="col-xl-3 col-sm-6 mb-4">
								<div
									className={`box ${(item.currency && item.currency.icon) ||
										"NOTE"}`}
								>
									<div className="box-head d-flex align-items-center justify-content-between">
										<h3>
											{(item.currency && item.currency.name) || "Credit Card"}
										</h3>
										<i
											title={item.currency && item.currency.icon}
											className={`cc ${(item.currency && item.currency.icon) ||
												"NOTE"}-alt`}
										/>
									</div>
									<div className="box-body d-flex align-items-center justify-content-between">
										<span title="Investment">
											{Intl.NumberFormat("en-US", {
												currency: "USD",
												style: "currency"
											}).format(item.investmentMade)}
										</span>
										<span title="ROI">
											{Intl.NumberFormat("en-US", {
												currency: "USD",
												style: "currency"
											}).format(item.currentBalance)}
										</span>
									</div>
								</div>
							</div>
						);
					})}
				</div>
			);
		}
	}
}

export default AccountStatistic;
