import React, { Component } from "react";
import { inject, observer } from "mobx-react";

@inject("transactionStore")
@observer
class TotalROI extends Component {
	render() {
		return (
			<div className="card mb-4 btn-shadow">
				<div className="card-header text_md_center">
					RETURN ON INVESTMENT
					<span className="badge badge-warning">ROI</span>
				</div>
				<div className="card-body">
					<div className="row align-items-center text_md_center">
						<div className="col-xl-12 col-lg-12">
							<h5 className="text-muted small">Total ROI</h5>
							<h2 className="balance_text">
								{Intl.NumberFormat("en-US", {
									currency: "USD",
									style: "currency"
								}).format(
									this.props.transactionStore.getTotalROI
								)}
							</h2>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default TotalROI;
