import React, { Component, Fragment } from "react";
import { Helmet } from "react-helmet";
import { inject, observer } from "mobx-react";
import LoadingIndicator from "../../partials/loading/loading";
import { CSSTransition } from "react-transition-group";
import NoContent from "../../partials/NoContent";
import PageNotFound from "../notfound";
import auth from "../../../services/auth.provider";
import { utility } from "../../../utility/core";
import $ from "jquery";

@inject("investmentStore")
@observer
class AllUserInvestment extends Component {
	state = {
		keyword: "",
		currentBalance: 0,
		id: "",
		index: -1,
		investmentMade: 0
	};
	handleForm = e => {
		e.preventDefault();
		this.props.investmentStore.SearchUserInvestment(this.state.keyword);
	};

	onChange = e => {
		this.setState({ [e.target.name]: e.target.value });
	};
	copyToClipboard = item => {
		utility.copyToClipboard("cont", item);
	};

	// triger current balance form
	openUpdateBal = (id, newBalance, index) => {
		this.setState({ id: id });
		this.setState({ currentBalance: newBalance });
		this.setState({ index: index });
		$("#w_t").click();
	};
	// triger current balance form
	openUpdateInvestment = (id, newBalance, index) => {
		this.setState({ id: id });
		this.setState({ investmentMade: newBalance });
		this.setState({ index: index });
		$("#w_u").click();
	};

	// triger re-invest window
	openReinvest = id => {
		this.setState({ id: id });
		$("#w_i").click();
	};

	// POST: Update balance
	PostUpdateBal = () => {
		$("#w_t").click();
		const { id, currentBalance, index } = this.state;
		this.props.investmentStore.UpdateBalance(id, currentBalance, index);
	};

	// POST: Update balance
	PostUpdateInvestment = () => {
		$("#w_u").click();
		const { id, investmentMade, index } = this.state;
		this.props.investmentStore.UpdateInvestmentMade(id, investmentMade, index);
	};
	// POST: re-invest
	PostReinvest = () => {
		$("#w_i").click();
		const { id, keyword } = this.state;
		this.props.investmentStore.ReInvest(id, keyword);
	};

	render() {
		const { investmentStore } = this.props;
		const title = "User's investments";
		if (auth.isAuthenticated() && !auth.getUser().admin)
			return <PageNotFound />;
		return (
			<Fragment>
				<Helmet>
					<title>
						{title} | {utility.GetAppName()}
					</title>
				</Helmet>

				<div className="row justify-content-center">
					<div className="col-12 col-md-10 col-lg-8">
						<form
							onSubmit={event => this.handleForm(event)}
							className="card card-sm"
						>
							<div className="card-body row no-gutters align-items-center">
								<div className="col-auto">
									<i className="fa fa-search h4 text-body" />
								</div>

								<div className="col">
									<input
										className="form-control form-control-lg form-control-borderless"
										type="search"
										onChange={event => this.onChange(event)}
										name="keyword"
										placeholder="Search email address only"
									/>
								</div>

								<div className="col-auto">
									<button className="btn btn-default" type="submit">
										Search
									</button>
								</div>
							</div>
						</form>
					</div>
					{investmentStore.loading && <LoadingIndicator />}
					<div className="col-12 col-md-10 col-lg-12">
						<div className="row">
							{investmentStore.investments.map((doc, index) => (
								<CSSTransition
									in={true}
									key={doc.id}
									appear={true}
									timeout={200}
									classNames="slide"
								>
									<div className="col-xl-4">
										<div className="card mb-4">
											<div className="card-sm-header">
												<span className="wallet-heading">
													<h4 title="Person">
														{doc.user.verified && (
															<i
																title="Account verified"
																class="icon-check icons text-success"
															/>
														)}
														{!doc.user.verified && (
															<i
																title="Account not verified"
																class="icon-close icons text-danger"
															/>
														)}
														{doc.user.name}
													</h4>
													<span className="text-small text-muted">
														{doc.user.email}
													</span>
												</span>
											</div>
											<div className="card-body">
												<ul className="list_none p-0 wallet_info">
													<li>
														<span className="text-muted">Investment Made</span>
														<span className="h5 text-success">
															{Intl.NumberFormat("en-US", {
																currency: "USD",
																style: "currency"
															}).format(doc.investmentMade)}
														</span>
													</li>

													<li>
														<span className="text-muted">Current Balance</span>
														<span className="h6">
															{Intl.NumberFormat("en-US", {
																currency: "USD",
																style: "currency"
															}).format(doc.currentBalance)}
														</span>
													</li>
													<li>
														<span className="text-muted">Selected Service</span>
														<span>{doc.targetService}</span>
													</li>
													<li>
														<span className="text-muted">Payment Source</span>

														<span className="h5 text-warning">
															{(doc.currency && doc.currency.name) ||
																"Credit Card"}
														</span>
													</li>
												</ul>
												{!doc.closed && (
													<Fragment>
														<div className="text-center m-2">
															<div id="cont" className="copy-button mb-1">
																<input
																	type="text"
																	value={doc.user.walletId}
																	placeholder={doc.user.walletId}
																	readOnly={true}
																	className="form-control copy_input"
																	name="walletId"
																/>
																<span
																	className="copy-link btn-dark"
																	onClick={() =>
																		this.copyToClipboard(doc.user.walletId)
																	}
																	data-toggle="tooltip"
																	title="Copy to Clipboard"
																>
																	Copy
																</span>
															</div>
														</div>
														<div
															className="p-2"
															style={{
																display: "flex",
																justifyContent: " space-between"
															}}
														>
															<button
																onClick={() =>
																	this.openUpdateBal(
																		doc.id,
																		doc.currentBalance,
																		index
																	)
																}
																title="Edit Balance"
																className="btn btn-link"
															>
																Balance
															</button>
															<button
																title="Edit Investment"
																className="btn btn-link"
																onClick={() =>
																	this.openUpdateInvestment(
																		doc.id,
																		doc.investmentMade,
																		index
																	)
																}
															>
																Investment
															</button>
															{doc.currentBalance > 0 && (
																<button
																	onClick={() => this.openReinvest(doc.id)}
																	title="Re-investment"
																	className="btn btn-primary"
																>
																	<i className="icon-refresh icons" />
																</button>
															)}
														</div>
													</Fragment>
												)}
												{doc.closed && (
													<div className="text-center">
														<h4 className="text-danger">Closed</h4>
													</div>
												)}
											</div>
										</div>
									</div>
								</CSSTransition>
							))}
						</div>
					</div>
				</div>
				{!investmentStore.investments.length && !investmentStore.loading && (
					<NoContent
						message={
							investmentStore.errorMessage || "Enter user's email address!"
						}
					/>
				)}
				<input
					type="hidden"
					data-target="#balanceForm"
					data-toggle="modal"
					id="w_t"
				/>
				<div
					className="modal fade"
					id="balanceForm"
					tabIndex="-1"
					role="dialog"
					data-backdrop="static "
					data-keyboard="false"
					aria-labelledby="zwf_4"
					aria-hidden="true"
				>
					<div className="modal-dialog " role="document">
						<div className="modal-content">
							<div className="modal-header">
								<h5 className="modal-title" id="zwf_4">
									Update Balance Form
								</h5>
							</div>
							<div className="modal-body">
								<div className="row">
									<div className="form-group col-md-12">
										<label className="col-form-label">Current Balance</label>
										<div className="input-group">
											<input
												type="number"
												name="currentBalance"
												min="0"
												value={this.state.currentBalance}
												placeholder="e.g 5000.00"
												autoFocus={true}
												onChange={this.onChange}
												className="form-control"
											/>
										</div>
									</div>
								</div>
							</div>
							<div className="modal-footer">
								<button data-dismiss="modal" className="btn btn-light">
									Cancel
								</button>
								<button
									className="btn btn-default"
									type="button"
									onClick={this.PostUpdateBal}
									data-dismiss="modal"
								>
									Update Balance
									<i className="icon-arrow-right icons" />
								</button>
							</div>
						</div>
					</div>
				</div>
				<input
					type="hidden"
					data-target="#InvestedForm"
					data-toggle="modal"
					id="w_u"
				/>
				<div
					className="modal fade"
					id="InvestedForm"
					tabIndex="-1"
					role="dialog"
					data-backdrop="static "
					data-keyboard="false"
					aria-labelledby="zwf_5"
					aria-hidden="true"
				>
					<div className="modal-dialog " role="document">
						<div className="modal-content">
							<div className="modal-header">
								<h5 className="modal-title" id="zwf_5">
									Update Investment Made
								</h5>
							</div>
							<div className="modal-body">
								<div className="row">
									<div className="form-group col-md-12">
										<label className="col-form-label">Investment Made</label>
										<div className="input-group">
											<input
												type="number"
												name="investmentMade"
												min="0"
												value={this.state.investmentMade}
												placeholder="e.g 5000.00"
												autoFocus={true}
												onChange={this.onChange}
												className="form-control"
											/>
										</div>
									</div>
								</div>
							</div>
							<div className="modal-footer">
								<button data-dismiss="modal" className="btn btn-light">
									Cancel
								</button>
								<button
									className="btn btn-default"
									type="button"
									onClick={this.PostUpdateInvestment}
									data-dismiss="modal"
								>
									Update Investment
									<i className="icon-arrow-right icons" />
								</button>
							</div>
						</div>
					</div>
				</div>
				<input
					type="hidden"
					data-target="#reinvest"
					data-toggle="modal"
					id="w_i"
				/>
				<div
					className="modal fade"
					id="reinvest"
					tabIndex="-1"
					role="dialog"
					aria-labelledby="eLabel"
					aria-hidden="true"
				>
					<div className="modal-dialog" role="document">
						<div className="modal-content">
							<div className="modal-header">
								<h5 className="modal-title" id="eLabel">
									Re-invest ?
								</h5>
							</div>
							<div className="modal-body">
								Re-investing would close the current investment, move the
								current balance and total investment made in the current
								investment to a new investment.
							</div>
							<div className="modal-footer">
								<button
									className="btn btn-light"
									type="button"
									data-dismiss="modal"
								>
									Cancel
								</button>
								<button
									onClick={this.PostReinvest}
									data-dismiss="modal"
									className="btn btn-default"
								>
									<i className="icon-arrow-right icons" /> Procceed
								</button>
							</div>
						</div>
					</div>
				</div>
			</Fragment>
		);
	}
}

export default AllUserInvestment;
