import React, { PureComponent } from "react";
import { Editor, RichUtils } from "draft-js";
import "./editor.css";

class AppEditor extends PureComponent {
	constructor(props) {
		super(props);
		this.focus = () => this.refs.editor.focus();
		this.onChange = editorState => {
			this.props.onChange(editorState, this.refs.editor.editor.innerHTML);
		};

		this.handleKeyCommand = command => this._handleKeyCommand(command);
		this.onTab = e => this._onTab(e);
		this.toggleBlockType = type => this._toggleBlockType(type);
		this.toggleInlineStyle = style => this._toggleInlineStyle(style);
	}

	_handleKeyCommand(command) {
		const { editorState } = this.props;
		// console.log(this.ref.editor);
		const newState = RichUtils.handleKeyCommand(editorState, command);
		if (newState) {
			this.onChange(newState);
			return true;
		}
		return false;
	}

	_onTab(e) {
		const maxDepth = 4;
		this.onChange(RichUtils.onTab(e, this.props.editorState, maxDepth));
	}

	_toggleBlockType(blockType) {
		this.onChange(RichUtils.toggleBlockType(this.props.editorState, blockType));
	}

	_toggleInlineStyle(inlineStyle) {
		this.onChange(
			RichUtils.toggleInlineStyle(this.props.editorState, inlineStyle)
		);
	}
	render() {
		const { editorState } = this.props;

		let className = "RichEditor-editor";
		var contentState = editorState.getCurrentContent();
		if (!contentState.hasText()) {
			if (
				contentState
					.getBlockMap()
					.first()
					.getType() !== "unstyled"
			) {
				className += " RichEditor-hidePlaceholder";
			}
		}
		return (
			<div className="RichEditor-root">
				<BlockStyleControls
					editorState={editorState}
					onToggle={this.toggleBlockType}
				/>
				<InlineStyleControls
					editorState={editorState}
					onToggle={this.toggleInlineStyle}
				/>
				<div className={className} onClick={this.focus}>
					<Editor
						blockStyleFn={getBlockStyle}
						customStyleMap={styleMap}
						editorState={editorState}
						handleKeyCommand={this.handleKeyCommand}
						onChange={this.onChange}
						onTab={this.onTab}
						placeholder="message body ..."
						ref="editor"
						spellCheck={true}
					/>
				</div>
			</div>
		);
	}
}

const styleMap = {
	CODE: {
		backgroundColor: "rgba(0, 0, 0, 0.05)",
		fontFamily: '"Inconsolata", "Menlo", "Consolas", monospace',
		fontSize: 16,
		padding: 2
	}
};

function getBlockStyle(block) {
	switch (block.getType()) {
		case "blockquote":
			return "RichEditor-blockquote";
		default:
			return null;
	}
}

class StyleButton extends PureComponent {
	constructor() {
		super();
		this.onToggle = e => {
			e.preventDefault();
			this.props.onToggle(this.props.style);
		};
	}

	render() {
		let className = "btn btn-light btn-sm";
		if (this.props.active) {
			className += " active";
		}

		return (
			<button type="button" className={className} onMouseDown={this.onToggle}>
				<i className={this.props.display} />{" "}
				{!this.props.display && this.props.label} {this.props.prefix}
			</button>
		);
	}
}

const BLOCK_TYPES = [
	{ label: "H1", style: "header-one", display: "fa fa-header", prefix: "1" },
	{ label: "H2", style: "header-two", display: "fa fa-header", prefix: "2" },
	{ label: "H3", style: "header-three", display: "fa fa-header", prefix: "3" },
	{ label: "H4", style: "header-four", display: "fa fa-header", prefix: "4" },
	{ label: "H5", style: "header-five", display: "fa fa-header", prefix: "5" },
	{ label: "H6", style: "header-six", display: "fa fa-header", prefix: "6" },
	{ label: "Quote", style: "blockquote", display: "fa fa-quote-right" },
	{ label: "ul", style: "unordered-list-item", display: "" },
	{ label: "ol", style: "ordered-list-item", display: "" },
	{ label: "Code Block", style: "code-block", display: "fa fa-code" }
];

const BlockStyleControls = props => {
	const { editorState } = props;
	const selection = editorState.getSelection();
	const blockType = editorState
		.getCurrentContent()
		.getBlockForKey(selection.getStartKey())
		.getType();

	return (
		<div className="btn-group mb-2" role="group" aria-label="Toolsbar">
			{BLOCK_TYPES.map(type => (
				<StyleButton
					key={type.label}
					active={type.style === blockType}
					label={type.label}
					display={type.display}
					onToggle={props.onToggle}
					style={type.style}
					prefix={type.prefix}
				/>
			))}
		</div>
	);
};

var INLINE_STYLES = [
	{ label: "B", style: "BOLD", display: "fa fa-bold" },
	{ label: "I", style: "ITALIC", display: "fa fa-italic" },
	{ label: "U", style: "UNDERLINE", display: "" },
	{ label: "Monospace", style: "CODE", display: "fa fa-code", prefix: "mono" }
];

const InlineStyleControls = props => {
	var currentStyle = props.editorState.getCurrentInlineStyle();
	return (
		<div className="btn-group mb-2" role="group" aria-label="Toolsbar">
			{INLINE_STYLES.map(type => (
				<StyleButton
					key={type.label}
					active={currentStyle.has(type.style)}
					label={type.label}
					display={type.display}
					onToggle={props.onToggle}
					style={type.style}
					prefix={type.prefix}
				/>
			))}
		</div>
	);
};
export default AppEditor;
