import React, { Component, Fragment } from "react";
import auth from "../../../services/auth.provider";
import { inject, observer } from "mobx-react";

@inject("statisticsStore")
@observer
class FundStatistics extends Component {
	componentDidMount() {
		if (
			!this.props.statisticsStore.users &&
			!this.props.statisticsStore.loading
		)
			this.props.statisticsStore.GetData();
	}

	render() {
		const { admin } = auth.getUser();
		return (
			<Fragment>
				{admin && (
					<div className="col-xl-6 col-sm-6 mb-4">
						<div className="box BH">
							<div className="box-head d-flex align-items-center justify-content-between">
								<h4 title="Fund request">Fund Request</h4>
								<i className="icon-cloud-upload icon" />
							</div>
							<div className="box-body d-flex align-items-center justify-content-between">
								<h6 title="Investment">
									{Intl.NumberFormat("en-US").format(
										this.props.statisticsStore.fundRequests
									)}
								</h6>
							</div>
						</div>
					</div>
				)}
			</Fragment>
		);
	}
}

export default FundStatistics;
