const toast = require("react-toastify").toast;
const dotdev = require("dotenv");
dotdev.config();

exports.utility = {
	getParameterByName(name, url) {
		if (!url) {
			url = window.location.href;
		}
		name = name.replace(/[[\]]/g, "\\$&");
		const regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
			results = regex.exec(url);
		if (!results) {
			return null;
		}
		if (!results[2]) {
			return "";
		}
		return decodeURIComponent(results[2].replace(/\+/g, " "));
	},
	// copy text
	/**
	 * Copy text to clipboard
	 * @param {HTMLElement} parentId the id the parent
	 * @param {string} content the content to  be copied to clipboard.
	 */
	copyToClipboard(parentId, content) {
		/** Create element */
		const textField = document.createElement("textarea");
		textField.innerText = content;
		const parentElement = document.getElementById(parentId);
		parentElement.appendChild(textField);
		/** Select text */
		if (isIOS()) {
			let range = document.createRange();
			range.selectNodeContents(textField);
			let selection = window.getSelection();
			selection.removeAllRanges();
			selection.addRange(range);
			textField.setSelectionRange(0, 999999);
		} else {
			textField.select();
		}
		// copy and remove element
		document.execCommand("copy");
		parentElement.removeChild(textField);
		// Clipboard().copy(content);
		toast("Copied text to clipboard!", {
			type: "default",
			position: "top-center",
			draggable: true,
			autoClose: 2000
		});

		// alert("Copied text to clipboard!");
	},
	// get running instance
	getUrl() {
		return process.env.NODE_ENV === "development"
			? "http://localhost:8087"
			: "https://account.tescotrades.com";
	},
	/**
	 * Get the total days between two days
	 * @param {Date} date1 first date
	 * @param {Date} date2 second date
	 * @default date2 if no date is passed to second date to resolves to current date
	 */
	getDateBetween(date1, date2 = new Date()) {
		// a day in milliseconds
		const a_day = 1000 * 60 * 60 * 24;

		// Convert time 1 date to milliseconds
		const time1 = date1.getTime();

		// Convert time 2 date to milliseconds
		const time2 = date2.getTime();

		// Calculate the difference in milliseconds
		const diff_ms = time2 - time1;

		// Convert back to days and return
		return Math.floor(diff_ms / a_day);
	},
	GetAppName() {
		return "Tesco Investment";
	},

	GetRandomCode() {
		var text = "";
		var possible =
			"ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";

		for (var i = 0; i < 6; i++)
			text += possible.charAt(
				Math.floor(Math.random() * possible.length)
			);

		return text;
	}
};
function isIOS() {
	return navigator.userAgent.match(/ipad|iphone/i);
}
