import { configure, observable, runInAction, action, computed } from "mobx";
import auth from "../services/auth.provider";
import { notificationProvider } from "../services/notification.provider";
import { pushNotificationProvider } from "../services/PushNotificationService";
import { toast } from "react-toastify";
import openSocket from "socket.io-client"; // socket
import { utility } from "../utility/core";
import { endpoint } from "../model/events";

// config
configure({
	enforceActions: "observed"
});

class AppStore {
	constructor() {
		if (auth.isAuthenticated()) {
			const { id, email } = auth.getUser();
			pushNotificationProvider.requestPermission();
			this.socket = openSocket(utility.getUrl() + "/app-level");
			// listen
			this.listenToUpdate();

			// join room
			this.socket.emit("join", { id, email });
		}
	}
	listenToUpdate() {
		// on new notification
		this.socket.on(endpoint.app.new_notification, item => {
			runInAction(() => {
				this.notifications.unshift(item.doc);
			});
			pushNotificationProvider.generateNotification(
				"New Notification",
				item.doc.message
			);
		});
	}

	@observable
	notifications = [];

	// loading indicator
	@observable
	loading = false;

	// message
	@observable
	errorMessage = "";

	@observable
	pageProps = {
		page: 1,
		total: 0,
		pages: 0,
		limit: 4
	};

	@action("get notification")
	getNotification = () => {
		runInAction(() => {
			this.loading = true;
			this.errorMessage = "";
		});
		notificationProvider
			.getNotifcation(
				auth.getUser().id,
				this.pageProps.page,
				this.pageProps.limit
			)
			.then(cb => {
				runInAction(() => {
					this.loading = false;
					if (cb.status === 200) {
						this.pageProps.limit = cb.limit;
						this.pageProps.pages = cb.pages;
						this.pageProps.page = cb.page;
						this.pageProps.total = cb.total;
						this.notifications = cb.docs;
					} else {
						toast(cb.message, {
							position: toast.POSITION.TOP_CENTER,
							autoClose: 8000,
							type: toast.TYPE.ERROR
						});
						this.errorMessage = cb.message;
					}
				});
			})
			.catch(() => {
				runInAction(() => {
					this.loading = false;
					this.errorMessage =
						"Failed! Please check your internet connection and try again.";
					toast(this.errorMessage, {
						position: toast.POSITION.TOP_CENTER,
						type: toast.TYPE.ERROR
					});
				});
			});
	};

	@computed
	get hasMore() {
		return this.pageProps.pages > this.pageProps.page;
	}

	@action
	loadMoreRecord = () => {
		runInAction(() => {
			// increase page number
			this.pageProps.page++;
		});
		this.getNotification();
	};

	@computed
	get totalUnSeen() {
		return this.notifications.filter(v => !v.seen).length;
	}

	@action
	updateSeenState = id => {
		runInAction(() => {
			this.loading = true;
			this.errorMessage = "";
		});
		notificationProvider
			.markAsseen(id)
			.then(cb => {
				runInAction(() => {
					this.loading = false;
					if (cb.status !== 200) {
						alert(cb.message);
					} else {
						this.notifications.forEach(c => {
							if (c.id === id) return (c.seen = true);
						});
					}
				});
			})
			.catch(() => {
				runInAction(() => {
					this.loading = false;
					this.errorMessage =
						"Failed! Please check your internet connection and try again.";
					toast(this.errorMessage, {
						position: toast.POSITION.TOP_CENTER,
						type: toast.TYPE.ERROR
					});
				});
			});
	};
}

export const appStore = new AppStore();
