import React, { Component } from "react";
import { observer, inject } from "mobx-react";
import auth from "../../../services/auth.provider";
import NoContent from "../../partials/NoContent";
import { utility } from "../../../utility/core";
import $ from "jquery";
import PageProperty from "../../partials/page-property";
import LoadingIndicator from "../../partials/loading/loading";

@inject("userStore")
@observer
class ReferralList extends Component {
	state = { link: "", amount: 0, date: null, currency: "", percent: 0 };
	componentDidMount() {
		this.props.userStore.getReferral(auth.getUser().id);
		const { origin } = document.location;
		this.setState({ link: `${origin}/signup?ref=${auth.getUser().email}` });
	}

	//check referral
	checkState = user => {
		this.props.userStore.checkAccountStatus(user).then(doc => {
			if (doc) {
				this.setState({ amount: doc.amount });
				this.setState({ date: doc.date });
				this.setState({ currency: doc.currency });
				const amount = this.props.userStore.getExpectedPercent(10, doc.amount);
				this.setState({ percent: amount });
				$("#showStatusBox").click();
			}
		});
	};

	renderStatus = status => {
		return status === false ? (
			<span className="badge-success badge-pill"> Active</span>
		) : (
			<span className="badge-warning badge-pill">Suspended</span>
		);
	};

	// copy
	copyToClipboard = () => {
		utility.copyToClipboard("cont", this.state.link);
	};

	render() {
		const { title, userStore } = this.props;
		return (
			<React.Fragment>
				<div className="col-md-12">
					<div className="card mb-3">
						<div className="card-header">
							{title}
							<button
								data-target="#confirmPayment"
								data-toggle="modal"
								className="btn btn-default pull-right"
							>
								<i className="icon-link icons" /> Referrer Link
							</button>
						</div>
						<div className="card-body">
							<div className="table-responsive">
								{this.props.userStore.referral.list.length === 0 ? (
									<NoContent message={this.props.userStore.errorMessage} />
								) : (
									<table
										className="table table-hover mb-0 table_s1 "
										id="dataTable"
										width="100%"
										cellSpacing="0"
									>
										<thead>
											<tr>
												<th scope="col">#</th>
												<th scope="col">Name</th>
												<th scope="col">Current Plan</th>
												<th className="text-center" scope="col">
													Date of Joining
												</th>
												<th className="text-right" scope="col">
													Status
												</th>
												<th className="text-right" align="right" scope="col">
													Action
												</th>
											</tr>
										</thead>
										<tbody>
											{this.props.userStore.referral.list.map((item, index) => {
												return (
													<tr key={item.id}>
														<th scope="row">{index + 1}</th>
														<td>{item.name}</td>
														<td>
															<b className="text_blue">{item.plan}</b>
														</td>
														<td className="text_info text-center">
															{Intl.DateTimeFormat("en-US", {
																year: "numeric",
																month: "long",
																day: "2-digit"
															}).format(new Date(item.joined))}
														</td>
														<td className="text-right">
															{this.renderStatus(item.suspended)}
														</td>
														<td className="text-right">
															<button
																onClick={() => this.checkState(item.id)}
																className="btn btn-light btn-sm"
															>
																<i className="icon-options icons" /> Status
															</button>
														</td>
													</tr>
												);
											})}
										</tbody>
									</table>
								)}
							</div>
							<PageProperty
								page={userStore.referral.page}
								pages={userStore.referral.pages}
								total={userStore.referral.total}
								limit={userStore.limt}
								length={userStore.referral.list.length}
								onClick={userStore.loadMoreReferral}
							/>
							{userStore.isloading && <LoadingIndicator useBackdrop={true} />}
						</div>
					</div>
				</div>

				<div
					className="modal fade"
					id="confirmPayment"
					tabIndex="-1"
					role="dialog"
					aria-labelledby="cLable"
					aria-hidden="true"
				>
					<div className="modal-dialog" role="document">
						<div className="modal-content">
							<div className="modal-header">
								<h5 className="modal-title" id="cLable">
									Referrer Link
								</h5>
								<button
									className="close"
									type="button"
									data-dismiss="modal"
									aria-label="Close"
								>
									<span aria-hidden="true"></span>
								</button>
							</div>
							<div className="modal-body">
								<div className="text-center m-2">
									<div id="cont" className="copy-button mb-1">
										<input
											type="text"
											value={this.state.link}
											placeholder={this.state.link}
											readOnly={true}
											className="form-control copy_input"
											name="address"
										/>
										<span
											className="copy-link btn-dark"
											onClick={this.copyToClipboard}
											data-toggle="tooltip"
											title="Copy to Clipboard"
										>
											Copy
										</span>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
				<input
					type="hidden"
					data-target="#statusBox"
					data-toggle="modal"
					id="showStatusBox"
				/>
				<div
					className="modal fade"
					id="statusBox"
					tabIndex="-1"
					role="dialog"
					aria-labelledby="cLable5"
					aria-hidden="true"
				>
					<div className="modal-dialog" role="document">
						<div className="modal-content">
							<div className="modal-header">
								<h5 className="modal-title" id="cLable5">
									Referrer Link
								</h5>
								<button
									className="close"
									type="button"
									data-dismiss="modal"
									aria-label="Close"
								>
									<span aria-hidden="true"></span>
								</button>
							</div>
							<div className="modal-body">
								<ul className="list_none p-0 wallet_info">
									<li>
										<span>Amount Funded</span>
										<span>
											{" "}
											{Intl.NumberFormat("en-US", {
												currency: "USD",
												style: "currency"
											}).format(this.state.amount)}
										</span>
									</li>
									<li>
										<span>Currency</span>
										<span>{this.state.currency}</span>
									</li>
									<li>
										<span>Your Percent</span>
										<span>
											{Intl.NumberFormat("en-US", {
												currency: "USD",
												style: "currency"
											}).format(this.state.percent)}
										</span>
									</li>
									<li>
										<span>Date</span>
										<span>
											{new Intl.DateTimeFormat("en-US", {
												year: "numeric",
												month: "long",
												day: "2-digit"
											}).format(new Date(this.state.date))}
										</span>
									</li>
								</ul>
							</div>
							<div className="modal-footer">
								<button
									type="button"
									className="btn btn-light"
									data-dismiss="modal"
								>
									Close
								</button>
							</div>
						</div>
					</div>
				</div>
			</React.Fragment>
		);
	}
}

export default ReferralList;
