import { Observable } from "rxjs";

class PushNotificationService {
	constructor() {
		this.logo = "/assets/images/logo_icon.png";
		this.permission = this.isSupported() ? "default" : "denied";
	}

	isSupported() {
		return "Notification" in window;
	}
	requestPermission() {
		const self = this;
		if ("Notification" in window) {
			Notification.requestPermission(function(status) {
				return (self.permission = status);
			});
		}
	}

	// show notification window
	/**
	 *Create new PushNotification
	 * @param {string} title notifcation title
	 * @param {object} options Push Notification
	 */
	create(title, options) {
		const self = this;
		return new Observable(function(obs) {
			if (!("Notification" in window)) {
				console.log(
					"Notifications are not available in this environment"
				);
				obs.complete();
			}
			if (self.permission !== "granted") {
				console.log(
					"The user hasn't granted you permission to send push notifications"
				);
				obs.complete();
			}
			// new instance of notication service
			const _notify = new Notification(title, options);
			_notify.onshow = function(e) {
				return obs.next({
					notification: _notify,
					event: e
				});
			};
			_notify.onclick = function(e) {
				return obs.next({
					notification: _notify,
					event: e
				});
			};
			_notify.onerror = function(e) {
				return obs.error({
					notification: _notify,
					event: e
				});
			};
			_notify.onclose = function() {
				return obs.complete();
			};
		});
	}
	/**
	 * Compose and send new notification
	 * @param {string} title notifcation title
	 * @param {string} icon icon link
	 * @param {string} body formatted message
	 */
	generateNotification(title, body, icon = this.logo) {
		const self = this;
		const options = {
			body: body,
			icon: icon,
			vibrate: [
				500,
				110,
				500,
				110,
				450,
				110,
				200,
				110,
				170,
				40,
				450,
				110,
				200,
				110,
				170,
				40,
				500
			],
			badge: icon
		};
		self.create(title, options).subscribe();
	}
}

export const Permission = "denied" | "granted" | "default";

export const pushNotificationProvider = new PushNotificationService();
