import React, { PureComponent } from "react";

class UserFilter extends PureComponent {
	state = { email: "" };

	onChange = (name, val) => {
		this.setState({ [name]: val });
	};
	/**
	 * @param {event} ev
	 */
	onEnter = ev => {
		if (ev.key === "Enter") {
			this.props.onSearch(this.state.email);
		}
	};

	render() {
		const { title, active, onClick } = this.props;
		return (
			<div className="card mb-1">
				<div className="card-body">
					<div className="row">
						<div className="col-md-4">
							<strong className="text-uppercase"> {title}</strong>
						</div>
						<div className="col-md-8">
							<div className="row">
								<div className="col-8">
									<div className="input-group">
										<div className="input-group-prepend">
											<span className="input-group-text">
												<i className="icon-magnifier icons" />
											</span>
										</div>
										<input
											value={this.state.email}
											name="email"
											onChange={ev =>
												this.onChange(ev.target.name, ev.target.value)
											}
											placeholder="search user by email..."
											onKeyPress={this.onEnter}
											className="form-control"
											type="search"
										/>
									</div>
								</div>
								<div className="col-md-4">
									<div
										className="btn-group"
										role="group"
										aria-label="Basic example"
									>
										<button
											title="All"
											type="button"
											onClick={() => onClick(null)}
											className={`btn btn-light ${active === null && "active"}`}
										>
											<i className="icon-people text-info icons" />
										</button>
										<button
											title="Active"
											type="button"
											onClick={() => onClick(false)}
											className={`btn btn-light ${!active && "active"}`}
										>
											<i className="icon-emotsmile text-success icons" />
										</button>
										<button
											title="Suspended"
											type="button"
											onClick={() => onClick(true)}
											className={`btn btn-light ${active && "active"}`}
										>
											<i className="icon-ghost text-warning icons" />
										</button>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default UserFilter;
