import React, { Component, Fragment } from "react";
import ActivePage from "../../partials/activepage";
import { Helmet } from "react-helmet";
import { inject, observer } from "mobx-react";
import IncreaseBalanceList from "./list";
import PlanStatistic from "./plans.statistic";
import { CSSTransition } from "react-transition-group";
import { utility } from "../../../utility/core";
import auth from "../../../services/auth.provider";
import PageNotFound from "../notfound";

@inject("transactionStore")
@observer
class IncreaseBalance extends Component {
	// get data
	componentDidMount() {
		if (auth.isAuthenticated() && auth.getUser().admin)
			this.props.transactionStore.getDueInvestment();
	}

	render() {
		if (auth.isAuthenticated() && !auth.getUser().admin)
			return (
				<Fragment>
					<Helmet>
						<title>
							Resources not found | {utility.GetAppName()}
						</title>
					</Helmet>
					<PageNotFound />
				</Fragment>
			);
		const title = "Increase balance";
		return (
			<Fragment>
				<ActivePage currentPath={this.props.match.url} title={title} />
				<div className="row">
					<Helmet>
						<title>
							{title} | {utility.GetAppName()}
						</title>
					</Helmet>
				</div>
				<div className="row justify-content-center">
					<div
						className="col-12"
						style={{ paddingRight: "2px", paddingLeft: "2px" }}
					>
						<CSSTransition
							in={true}
							appear={true}
							timeout={1000}
							classNames="fade"
						>
							<IncreaseBalanceList title={title} />
						</CSSTransition>
					</div>
				</div>
			</Fragment>
		);
	}
}

export default IncreaseBalance;
