import React, { Component } from "react";
import { Helmet } from "react-helmet";
import logo from "../../assets/images/logo_icon_72.png";
import { utility } from "../../utility/core";

// mobx
import { observer, inject } from "mobx-react";

@inject("userStore")
@observer
class NewPasswordComponent extends Component {
	constructor(props) {
		super(props);
		this.state = {
			password: "",
			cpassword: "",
			code: "",
			email: "",
			id: "",
			resetId: ""
		};
	}

	// update state before rendering
	componentWillMount() {
		// get code, id, email and resetId from query string
		this.setState({ email: utility.getParameterByName("email") });
		this.setState({ code: utility.getParameterByName("code") });
		this.setState({ id: utility.getParameterByName("id") });
		this.setState({ resetId: utility.getParameterByName("resetId") });
	}
	onChange = e => {
		this.setState({ [e.target.name]: e.target.value });
	};
	// handle login
	onSubmitForm = e => {
		e.preventDefault();
		if (this.state.password === this.state.cpassword) {
			const { email, id, resetId, code, password } = this.state;
			this.props.userStore.NewPasswordUpdate(
				id,
				email,
				code,
				resetId,
				password
			);
		} else {
			this.props.userStore.setMessage("Password must match!");
		}
	};
	renderButtonText = () => {
		return this.props.userStore.isloading ? (
			<span>
				<i
					className="fa fa-circle-o-notch fa-spin"
					style={{ fontSize: "1.4em" }}
				/>
			</span>
		) : (
			<span>
				<i className="icon-lock icon" /> Update Password
			</span>
		);
	};
	render() {
		return (
			<div className="bg_image h-100">
				<Helmet>
					<title>New Password | {utility.GetAppName()}</title>
				</Helmet>
				<div className="lr_wrap">
					<div className="card-body animate-bottom">
						<div className="lr_icon text-center">
							<a rel="noopener noreferrer" href={utility.getUrl()}>
								<img src={logo} alt="logo" />
							</a>
						</div>
						<h6 className="my-4 text-center text-uppercase">
							New Password Form
						</h6>
						<form onSubmit={event => this.onSubmitForm(event)}>
							<div className="form-group mb-4">
								<label htmlFor="exampleInputEmail1">New Password</label>
								<input
									className="form-control"
									id="exampleInputEmail1"
									type="password"
									name="password"
									onChange={event => this.onChange(event)}
									placeholder="enter password .."
									required
								/>
							</div>
							<div className="form-group mb-4">
								<label htmlFor="exampleInputEmail2">Comfirm Password</label>
								<input
									className="form-control"
									id="exampleInputEmail2"
									type="password"
									name="cpassword"
									onChange={event => this.onChange(event)}
									placeholder="enter password again .."
									required
								/>
							</div>

							<div className="text-center">
								<button
									type="submit"
									className={`btn btn-default btn-block mt-2 ${this.props
										.userStore.isloading && "disabled"}`}
								>
									{this.renderButtonText()}
								</button>
							</div>
							{this.props.userStore.errorMessage && (
								<div className="alert alert-danger mt-2" role="alert">
									{this.props.userStore.errorMessage}
								</div>
							)}
						</form>
					</div>
				</div>
			</div>
		);
	}
}

export default NewPasswordComponent;
