import React, { Component } from "react";
import { Helmet } from "react-helmet";
import { Redirect } from "react-router-dom";
import { utility } from "../../utility/core";
import * as QRCode from "qrcode.react";
import { observer, inject } from "mobx-react";

@inject("transactionStore")
@observer
class SignupVerification extends Component {
	constructor(props) {
		super(props);
		this.state = {
			amount: 0,
			currencyId: "",
			id: "",
			address: "",
			name: ""
		};
	}
	// copyText
	copyText = () => {
		utility.copyToClipboard("cont", this.state.address);
	};

	componentDidMount() {
		this.readLocalStorage();
	}

	renderButtonText = () => {
		return this.props.transactionStore.loading ? (
			<span>
				<i
					className="fa fa-circle-o-notch fa-spin"
					style={{ fontSize: "1.4em" }}
				/>
			</span>
		) : (
			<span>
				continue
				<i className="icon-arrow-right icons" />
			</span>
		);
	};

	readLocalStorage = () => {
		const rawdata = localStorage.getItem("user_pay");
		const data = JSON.parse(atob(rawdata));
		this.setState({ amount: data.amount });
		this.setState({ currencyId: data.currencyId });
		this.setState({ id: data.id });
		this.setState({ address: data.address });
		this.setState({ name: data.name });
	};

	//handlePaid
	handlePaid = () => {
		this.props.transactionStore
			.reviewTransaction(this.state.id)
			.then(cb => {
				if (cb) {
					this.props.history.push("/signup/completed");
				}
			})
			.catch(e => {
				alert(e.message);
			});
	};
	render() {
		if (!localStorage.getItem("user_pay")) {
			const { from } = this.props.location.state || {
				from: { pathname: `/signup/payment` }
			};
			return <Redirect to={from} />;
		}

		return (
			<div className="bg_image h-100">
				<Helmet>
					<title>Payment Confirmation | {utility.GetAppName()}</title>
				</Helmet>
				<div className="lr_wrap signup " style={{ borderTop: "0" }}>
					<div className="card-body animate-bottom">
						<h3>Payment Confirmation</h3>
						<hr />
						<div id="cont" className=" m-4">
							{this.state.currencyId && (
								<p>Goto to your wallet and make a transfer of</p>
							)}
							<div className="row">
								<div className="col">
									<p>
										<b>Amount</b> :{" "}
										{Intl.NumberFormat("en-US", {
											currency: "USD",
											style: "currency"
										}).format(this.state.amount)}
									</p>
									{this.state.currencyId && (
										<p>
											<b>Currency</b> : {this.state.name || "coin name"}
										</p>
									)}

									{this.state.currencyId && (
										<p>
											<b>Wallet Address</b>
										</p>
									)}
								</div>
								{this.state.currencyId && (
									<div className="col">
										<QRCode value={this.state.address} fgColor="#1d2124" />
									</div>
								)}
							</div>
							{this.state.currencyId && (
								<div id="cont" className="copy-button mb-1 mt-3">
									<input
										type="text"
										value={this.state.address}
										placeholder={
											this.state.address ||
											"https://www.citigroupinvestement.com"
										}
										readOnly={true}
										className="form-control copy_input"
										name="address"
									/>
									<span
										className="copy-link btn-dark"
										onClick={this.copyText}
										data-toggle="tooltip"
										title="Copy to Clipboard"
									>
										Copy
									</span>
								</div>
							)}

							{this.state.currencyId && (
								<p>
									After your transfer is successfully completed, click on the
									button below to continue.
								</p>
							)}
							{!this.state.currencyId && (
								<div className="text-center">
									<h5 className="text-muted">Payment is being processed!</h5>
									<p>click on the button below to continue.</p>
								</div>
							)}
						</div>
						<div className="text-right">
							<button
								onClick={this.handlePaid}
								className={`btn btn-success mt-2 `}
							>
								{this.renderButtonText()}
							</button>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default SignupVerification;
