import React, { Component } from "react";
import { inject, observer } from "mobx-react";
import { Helmet } from "react-helmet";
import ActivePage from "../../partials/activepage";
import UserList from "./list";
import { utility } from "../../../utility/core";
import auth from "../../../services/auth.provider";
import PageNotFound from "../notfound";

@inject("userStore")
@observer
class User extends Component {
	componentDidMount() {
		if (auth.isAuthenticated() && auth.getUser().admin)
			this.props.userStore.getUsers();
	}
	render() {
		const title = "Users";
		if (auth.isAuthenticated() && !auth.getUser().admin)
			return <PageNotFound />;
		return (
			<React.Fragment>
				<Helmet>
					<title>
						{title} | {utility.GetAppName()}
					</title>
				</Helmet>
				<ActivePage title={"users information"} />

				<div className="row">
					<UserList
						title={title}
						errorMessage={this.props.userStore.errorMessage}
					/>
				</div>
			</React.Fragment>
		);
	}
}

export default User;
