import React, { Component } from "react";
import { observer, inject } from "mobx-react";
import LoadingIndicator from "../../partials/loading/loading";
import NoContent from "../../partials/NoContent";
import PageProperty from "../../partials/page-property";

@inject("transactionStore")
@observer
class TransactioList extends Component {
	id = "";
	componentDidMount() {
		this.props.transactionStore.getTransactions(this.props.id);
	}
	onCancelTransaction = () => {
		this.props.transactionStore.cancelTransaction(this.id);
	};
	onPromptCancel = e => {
		this.id = e.target.name;
	};
	//
	renderStatus(status) {
		if (status === 1) {
			return <span className="badge-info badge-pill p-1">In Process</span>;
		} else if (status === 2) {
			return <span className="badge-warning badge-pill p-1">Pending</span>;
		} else if (status === 3) {
			return <span className="badge-danger badge-pill p-1">Cancelled</span>;
		} else {
			return <span className="badge-success badge-pill p-1">Completed</span>;
		}
	}
	renderCancelButton = (status, id) => {
		return status === 3 || status === 4 ? null : (
			<button
				onClick={t => this.onPromptCancel(t)}
				type="button"
				data-toggle="modal"
				name={id}
				style={{ paddingTop: 2, paddingBottom: 2 }}
				data-target="#cancelBox"
				className="btn btn-danger"
			>
				Cancel
			</button>
		);
	};
	render() {
		const { transactionStore, title } = this.props;
		if (transactionStore.isLoading) {
			return <LoadingIndicator useBackdrop={true} />;
		} else {
			return (
				<div className="col-md-12">
					<div className="card mb-3 animate-bottom">
						<div className="card-header">{title}</div>
						<div className="card-body">
							<div className="table-responsive">
								{transactionStore.transactionList.length === 0 ? (
									<NoContent message="Please check your internet connectivity and try again later" />
								) : (
									<table
										className="table table-hover mb-0 table_s1 "
										id="dataTable"
										width="100%"
										cellSpacing="0"
									>
										<thead>
											<tr>
												<th className="hidden-sm" scope="col">
													#
												</th>
												<th scope="col">Currency</th>
												<th scope="col">Amount</th>
												<th className="hidden-sm" scope="col">
													Status
												</th>
												<th scope="col">Date</th>
												<th scope="col">Type</th>
												<th />
											</tr>
										</thead>
										<tbody>
											{transactionStore.transactionList.map((doc, index) => {
												return (
													<tr key={doc.id}>
														<th className="hidden-sm" scope="row">
															{index + 1}
														</th>
														<td>{doc.currency}</td>
														<td>
															{Intl.NumberFormat("en-US", {
																currency: "USD",
																style: "currency"
															}).format(doc.amount)}
														</td>
														<td className="hidden-sm">
															{this.renderStatus(doc.status)}
														</td>
														<td>
															{new Intl.DateTimeFormat("en-US", {
																year: "numeric",
																month: "long",
																day: "2-digit"
															}).format(new Date(doc.date))}
														</td>
														<td>
															{doc.type === 1 ? (
																<span className="text-success">Fund</span>
															) : (
																<span className="text-danger">Withdraw</span>
															)}
														</td>
														<td>
															{this.renderCancelButton(doc.status, doc.id)}
														</td>
													</tr>
												);
											})}
										</tbody>
									</table>
								)}
							</div>
							<PageProperty
								page={transactionStore.transactionFetchOption.page}
								pages={transactionStore.transactionFetchOption.pages}
								total={transactionStore.transactionFetchOption.total}
								length={transactionStore.transactionList.length}
								limit={transactionStore.limit}
								onClick={transactionStore.loadMoreTransaction}
							/>
						</div>
					</div>
					<div
						className="modal fade"
						id="cancelBox"
						tabIndex="-1"
						role="dialog"
						aria-labelledby="35_gf"
						aria-hidden="true"
					>
						<div className="modal-dialog" role="document">
							<div className="modal-content">
								<div className="modal-header">
									<h5 className="modal-title" id="35_gf">
										Cancel Transaction ?
									</h5>
								</div>
								<div className="modal-body">
									Are you sure you want to cancel this transaction? This can not
									be undone.
								</div>
								<div className="modal-footer">
									<button
										type="button"
										className="btn btn-light"
										data-dismiss="modal"
									>
										Cancel
									</button>
									<button
										onClick={this.onCancelTransaction}
										type="button"
										data-dismiss="modal"
										className="btn btn-primary"
									>
										Continue <i className="icon-arrow-right icons" />
									</button>
								</div>
							</div>
						</div>
					</div>
				</div>
			);
		}
	}
}

export default TransactioList;
