import React, { Component } from "react";
import { observer, inject } from "mobx-react";
import LoadingIndicator from "../../partials/loading/loading";
import NoContent from "../../partials/NoContent";
import PageProperty from "../../partials/page-property";
import { utility } from "../../../utility/core";

@inject("transactionStore")
@observer
class IncreaseBalanceList extends Component {
	state = { amount: 0, id: "", percent: 100 };
	//
	onChange = e => {
		this.setState({ [e.target.name]: e.target.value });
	};

	//
	updateId = (id, amount) => {
		this.setState({ id, amount });
	};
	getPayable = (amount, _percent) => {
		const percent = _percent / this.state.percent;
		// result
		return Math.floor(percent * amount);
	};
	// handleCredit
	handleCredit = () => {
		this.props.transactionStore.increaseInvestment(
			this.state.id,
			this.state.amount
		);
		this.setState({ amount: 0 });
	};

	getTotaldays = date => {
		return utility.getDateBetween(new Date(date));
	};
	render() {
		const { transactionStore, title } = this.props;
		if (transactionStore.isLoading) {
			return <LoadingIndicator useBackdrop={true} />;
		} else {
			return (
				<div className="col-md-12">
					<div className="card mb-3">
						<div className="card-header">{title}</div>
						<div className="card-body">
							<div className="table-responsive">
								{transactionStore.dueInvestmentObject.list
									.length === 0 ? (
									<NoContent
										message={transactionStore.errorMessage}
									/>
								) : (
									<table
										className="table table-hover mb-0 table_s1 "
										id="dataTable"
										width="100%"
										cellSpacing="0"
									>
										<thead>
											<tr>
												<th scope="col">#</th>
												<th scope="col">Person</th>
												<th
													className="text-right"
													scope="col"
												>
													Investment
												</th>
												<th align="left" scope="col">
													ROI
												</th>
												<th align="left" scope="col">
													Payable Amount
												</th>
												<th align="left" scope="col">
													Service
												</th>
												<th
													align="right"
													className="text-right"
													scope="col"
												>
													Action
												</th>
											</tr>
										</thead>
										<tbody>
											{transactionStore.dueInvestmentObject.list.map(
												(item, index) => {
													return (
														<tr key={item.id}>
															<th scope="row">
																{index + 1}
															</th>
															<td>
																{item.user.name}
																<br />
																<small className="text_blue">
																	{
																		item
																			.user
																			.email
																	}
																</small>
															</td>
															<td align="right">
																<b>
																	{Intl.NumberFormat(
																		"en-US",
																		{
																			currency:
																				"USD",
																			style:
																				"currency"
																		}
																	).format(
																		item.investmentMade
																	)}
																</b>
															</td>
															<td>
																<b>
																	{Intl.NumberFormat(
																		"en-US",
																		{
																			currency:
																				"USD",
																			style:
																				"currency"
																		}
																	).format(
																		item.currentBalance
																	)}
																</b>
																<br />

																<small className="text-bold text-success">
																	{this.getTotaldays(
																		item.lastFund
																	)}{" "}
																	days ago
																</small>
															</td>
															<td>
																<b>
																	{Intl.NumberFormat(
																		"en-US",
																		{
																			currency:
																				"USD",
																			style:
																				"currency"
																		}
																	).format(
																		this.getPayable(
																			item.investmentMade,
																			item.percent
																		)
																	)}
																</b>
															</td>
															<td>
																{
																	item.targetService
																}
															</td>
															<td className="text-right">
																<button
																	data-target="#creditForm"
																	data-toggle="modal"
																	onClick={() =>
																		this.updateId(
																			item.id,
																			this.getPayable(
																				item.investmentMade,
																				item.percent
																			)
																		)
																	}
																	className="btn btn-success"
																>
																	<i className="icon-paypal icons">
																		{" "}
																	</i>{" "}
																	Credit
																</button>
															</td>
														</tr>
													);
												}
											)}
										</tbody>
									</table>
								)}
							</div>
							<PageProperty
								total={
									transactionStore.dueInvestmentObject.total
								}
								pages={
									transactionStore.dueInvestmentObject.pages
								}
								limit={transactionStore.limit}
								length={
									transactionStore.dueInvestmentObject.list
										.length
								}
								onClick={transactionStore.loadMoreDueInvestment}
								page={transactionStore.dueInvestmentObject.page}
							/>
						</div>
					</div>
					<div
						className="modal fade"
						id="creditForm"
						tabIndex="-1"
						role="dialog"
						aria-labelledby="35_gf"
						aria-hidden="true"
					>
						<div className="modal-dialog" role="document">
							<div className="modal-content">
								<div className="modal-header">
									<h5 className="modal-title" id="zwf_4">
										Credit Account Balance
									</h5>
								</div>
								<div className="modal-body">
									<div className="row">
										<div className="form-group col-md-12">
											<label className="col-form-label">
												Amount(USD)
											</label>
											<div className="input-group">
												<input
													type="number"
													name="amount"
													min="0"
													value={this.state.amount}
													placeholder="e.g 1,000.70"
													autoFocus={true}
													onChange={this.onChange}
													className="form-control"
												/>
											</div>
										</div>
									</div>
								</div>
								<div className="modal-footer">
									<button
										data-dismiss="modal"
										className="btn btn-light"
									>
										Cancel
									</button>
									<button
										className="btn btn-success"
										type="button"
										onClick={this.handleCredit}
										data-dismiss="modal"
									>
										Submit{" "}
										<i className="icon-credit-card icons" />
									</button>
								</div>
							</div>
						</div>
					</div>
				</div>
			);
		}
	}
}

export default IncreaseBalanceList;
