import React, { Component } from "react";
import UserOption from "./option";

class SingleUser extends Component {
	// return active or suspended account status
	renderStatus = status => {
		return status === false ? (
			<span className="badge-success badge-pill"> Active</span>
		) : (
			<span className="badge-warning badge-pill">Suspended</span>
		);
	};

	// return  verification status
	renderVerficationState = state => {
		return state ? null : (
			<i
				title="Account not verified"
				style={{ fontSize: "1em", fontWeight: "bold" }}
				className="icon-info icons text-danger"
			/>
		);
	};

	render() {
		const { item, index } = this.props;
		return (
			<tr className="animate-bottom" key={item.id}>
				<th scope="row">{index + 1}</th>
				<td>
					{this.renderVerficationState(item.verified)} {item.name}
					{item.admin && (
						<i
							title="Admin"
							style={{ fontSize: "1.2em" }}
							className="icon-shield icons text_success"
						/>
					)}
					<br />
					<small className="text_blue">
						Date of Joining :{" "}
						{Intl.DateTimeFormat("en-US").format(new Date(item.joined))}
					</small>
				</td>
				<td>{item.email}</td>
				<td>{item.city || "no city"}</td>
				<td align="center">{item.plan}</td>
				<td>{this.renderStatus(item.suspended)}</td>
				<td>
					<UserOption
						onRemove={this.props.onRemove}
						onActivate={this.props.onActivate}
						onSuspend={this.props.onSuspend}
						active={item.suspended}
					/>
				</td>
			</tr>
		);
	}
}

export default SingleUser;
