import { response } from "../model/response";
import api from "../services/auth.provider";

class TransactionProvider {
	constructor() {
		this.base = "/api/transactions/";
	}
	/**
	 * Send a withdrawal request
	 * @param {string}id investment id (user investment not investment plan)
	 * @param {number} amount amount to withdraw
	 */
	async withdrawRequest(id, amount, currency) {
		const res = await fetch(`${this.base}w/new`, {
			method: "POST",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json",
				Authorization: `Bearer ${api.getToken()}`
			},
			body: JSON.stringify({ id, amount ,currency})
		});

		const result = response.asSingleResponse(await res.json());
		return result;
	}
	/**
	 * Cancel single transaction request fund | Withdraw
	 * @param {string} id transaction id
	 */
	async cancelTransaction(id) {
		const res = await fetch(`${this.base}cancel/${id}`, {
			method: "PUT",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json",
				Authorization: `Bearer ${api.getToken()}`
			},
			body: JSON.stringify({ id })
		});

		const result = response.asSingleResponse(await res.json());
		return result;
	}
	/**
	 * Get user's transition
	 * @param {string} id user' id
	 * @param {number} page number
	 * @param {number} limit record per request
	 */
	async getTransactions(id, page, limit) {
		const res = await fetch(`${this.base}${id}?page=${page}&limit=${limit}`);
		const result = response.asListResponse(await res.json());
		return result;
	}
	/**
	 * Get pending | In-proccess fund transaction
	 * @param {number} page number
	 * @param {number} limit record per request
	 */
	async getPendingFundTransactions(page, limit) {
		const res = await fetch(`${this.base}p/fund/?page=${page}&limit=${limit}`, {
			method: "GET",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json"
			}
		});
		const result = response.asListResponse(await res.json());
		return result;
	}
	/**
	 * Get pending | In-proccess fund transaction
	 * @param {number} page number
	 * @param {number} limit record per request
	 */
	async getPendingWithdrawalTransactions(page, limit) {
		const res = await fetch(
			`${this.base}p/withdraw/?page=${page}&limit=${limit}`,
			{
				method: "GET",
				headers: {
					Accept: "application/json",
					"Content-Type": "application/json"
				}
			}
		);
		const result = response.asListResponse(await res.json());
		return result;
	}
	/**
	 * Cancel single transaction request fund | Withdraw
	 * @param {string} id transaction id
	 */
	async approveWithdrawRequest(id, addedBy) {
		const res = await fetch(`${this.base}approve/w`, {
			method: "PUT",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json",
				Authorization: `Bearer ${api.getToken()}`
			},
			body: JSON.stringify({ id, addedBy })
		});

		const result = response.asSingleResponse(await res.json());
		return result;
	}
	/**
	 * Cancel single transaction request fund | Withdraw
	 * @param {string} id transaction id
	 */
	async approveFundRequest(id, addedBy) {
		const res = await fetch(`${this.base}approve/f/`, {
			method: "PUT",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json",
				Authorization: `Bearer ${api.getToken()}`
			},
			body: JSON.stringify({ id, addedBy })
		});

		const result = response.asSingleResponse(await res.json());
		return result;
	}

	async reviewFundRequest(id) {
		const res = await fetch(`${this.base}review/payment/${id}`, {
			method: "PUT",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json"
			},
			body: JSON.stringify({ id })
		});

		const result = response.asSingleResponse(await res.json());
		return result;
	}
	/**
	 * new fund transaction
	 * @param {number} amount new amount to add
	 * @param {string} currency currency name
	 * @param {string} investment investment id
	 */
	async newTransaction(amount, currency, investment) {
		const res = await fetch(`${this.base}new/fund/topup`, {
			method: "POST",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json",
				Authorization: `Bearer ${api.getToken()}`
			},
			body: JSON.stringify({ currency, model: { amount, investment } })
		});
		const result = response.asSingleResponse(await res.json());
		return result;
	}

	async GetApplicationSetting() {
		const res = await fetch(`${this.base}app/setting`, {
			method: "GET",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json",
				Authorization: `Bearer ${api.getToken()}`
			}
		});
		const result = response.asSingleResponse(await res.json());
		return result;
	}

	/**
	 * Update application data
	 * @param {number} transactions daily transaction
	 * @param {number} investments total investment
	 * @param {number} investors total active investor
	 * @param {number} consultants total active consultant
	 */
	async UpdateApplicationData(
		transactions,
		investments,
		investors,
		consultants
	) {
		const res = await fetch(`${this.base}app/setting/data`, {
			method: "PUT",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json",
				Authorization: `Bearer ${api.getToken()}`
			},
			body: JSON.stringify({
				transactions,
				investments,
				investors,
				consultants
			})
		});

		const result = response.asSingleResponse(await res.json());
		return result;
	}

	async UpdateApplicationSetting(
		name,
		validateEmail,
		validateWallet,
		nextFund,
		nextFundReminder,
		percent
	) {
		const res = await fetch(`${this.base}app/setting/update`, {
			method: "PUT",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json",
				Authorization: `Bearer ${api.getToken()}`
			},
			body: JSON.stringify({
				name,
				validateEmail,
				validateWallet,
				percent,
				nextFund,
				nextFundReminder
			})
		});

		const result = response.asSingleResponse(await res.json());
		return result;
	}

	/**
	 * Get application statistics
	 */
	async GetAppStatistics() {
		const res = await fetch(`${this.base}app/statistics/`, {
			method: "GET",
			headers: {
				Accept: "application/json",
				"Content-Type": "application/json",
				Authorization: `Bearer ${api.getToken()}`
			}
		});

		const result = response.asSingleResponse(await res.json());
		return result;
	}
}
const transactionProvider = new TransactionProvider();
export default transactionProvider;
