import React, { Component, Fragment } from "react";
import $ from "jquery";
import auth from "../services/auth.provider";
import { inject, observer } from "mobx-react";

@inject("userStore")
@observer
class VerifyDialogBox extends Component {
	componentDidMount() {
		// check if you is authenticated and if account is not verified yet
		if (auth.isAuthenticated() && !auth.getUser().verified) {
			setTimeout(() => {
				$("#K_w").click();
			});
		}
	}

	//handleVerfy
	handleVerfy = () => {
		const { id, email, name } = auth.getUser();
		this.props.userStore.reSendEmailConfirmation(id, name, email);
	};
	render() {
		const { name, email } = auth.getUser();
		return (
			<Fragment>
				<button
					type="button"
					hidden
					id="K_w"
					className="btn btn-primary"
					data-toggle="modal"
					data-target="#verify"
					data-backdrop="static"
					data-keyboard="false"
				>
					Launch
				</button>

				<div
					className="modal fade"
					id="verify"
					tabIndex="-1"
					role="dialog"
					aria-labelledby="exampleModalLabel"
					aria-hidden="true"
				>
					<div className="modal-dialog" role="document">
						<div className="modal-content">
							<div className="modal-header">
								<h5 className="modal-title" id="exampleModalLabel">
									Account verification
								</h5>
								<button
									type="button"
									className="close"
									data-dismiss="modal"
									aria-label="Close"
								>
									<span aria-hidden="true">&times;</span>
								</button>
							</div>
							<div className="modal-body">
								<p className="h4">Hi {name}!</p>
								<p>
									Your account haven't been verified yet. Check
									<b className="text-primary"> {email}</b> to complete your
									account signup process.
								</p>

								<p>
									<small className="text-muted">
										<b>
											Note: Be sure to check your spam folder and move it to
											inbox if it isn't there already.
										</b>
									</small>
								</p>
							</div>
							<div className="modal-footer">
								<button
									onClick={this.handleVerfy}
									data-dismiss="modal"
									className="btn btn-default"
									type="button"
								>
									Re-send verification link
								</button>
							</div>
						</div>
					</div>
				</div>
			</Fragment>
		);
	}
}

export default VerifyDialogBox;
