import React, { Component, Fragment } from "react";
import logo_icon from "../../assets/images/logo_icon.png";
import dashboard_logo from "../../assets/images/dashboard_logo.png";
import { NavLink } from "react-router-dom";
import $ from "jquery";
import Notification from "./notification";
import ProfileDropdown from "./profileDropdown";

class Header extends Component {
	toggleHeader = () => {
		$("body").toggleClass("sidenav-toggled");
		$(".navbar-sidenav .nav-link-collapse").addClass("collapsed");
		$(
			".navbar-sidenav .sidenav-second-level, .navbar-sidenav .sidenav-third-level"
		).removeClass("show");
	};
	componentDidMount() {
		this.initJquery();
	}
	render() {
		return (
			<Fragment>
				<NavLink className="navbar-brand" to="/dashboard">
					<img className="logo_icon" src={logo_icon} alt="logo" />
					<img
						className="logo small-logo"
						alt="logo_wide"
						src={dashboard_logo}
					/>
				</NavLink>
				<div className="mobile_menu">
					<button
						className="navbar-toggler navbar-toggler-right"
						type="button"
						data-toggle="collapse"
						data-target="#navbarResponsive"
						aria-controls="navbarResponsive"
						aria-expanded="false"
						aria-label="Toggle navigation"
					>
						<span className="icon-menu icons" />
					</button>
					<button
						className="navbar-toggler"
						data-toggle="collapse"
						data-target="#navbarheader"
						aria-controls="navbarheader"
						aria-expanded="false"
						aria-label="Toggle navigation"
					>
						<i className="icon-options-vertical" />
					</button>
				</div>
				<div className="collapse navbar-collapse" id="navbarheader">
					<ul className="navbar-nav">
						<li className="nav-item d-none d-lg-block">
							<a
								onClick={this.toggleHeader}
								className="nav-link text-center menubar"
								id="sidenavToggler"
								href="#!"
							>
								<span className="line" />
								<span className="line" />
								<span className="line" />
							</a>
						</li>
					</ul>
					<ul className="navbar-nav ml-auto align-items-center">
						<Notification />
						<ProfileDropdown
							onLogout={this.props.handleLogout}
							{...this.props}
						/>
					</ul>
				</div>
			</Fragment>
		);
	}
	initJquery = () => {
		$(
			"body.fixed-nav .navbar-sidenav, body.fixed-nav .sidenav-toggler, body.fixed-nav .navbar-collapse"
		).on("mousewheel DOMMouseScroll", function(e) {
			var e0 = e.originalEvent,
				delta = e0.wheelDelta || -e0.detail;
			this.scrollTop += (delta < 0 ? 1 : -1) * 30;
			e.preventDefault();
		});
		// Scroll to top button appear
		$(document).scroll(function() {
			var scrollDistance = $(this).scrollTop();
			if (scrollDistance > 100) {
				$(".scroll-to-top").fadeIn();
			} else {
				$(".scroll-to-top").fadeOut();
			}
		});
	};
}

export default Header;
