import { model } from "../model/user.model";

class AuthProvider {
	constructor(key) {
		this.key = key;
	}

	login(user, token) {
		// convert to string
		const objJson = JSON.stringify({ user, token });
		//encrypt json string
		const encrypted = btoa(objJson);
		// write data
		localStorage.setItem(this.key, encrypted);
		return true;
	}

	updateUser(user) {
		// get encrypted data
		const encrypted = localStorage.getItem(this.key);
		// decrypt data
		const rawString = atob(encrypted);
		// cast  to user object
		const model = JSON.parse(rawString);
		model.user.name = user.name;
		model.user.address = user.address;
		model.user.city = user.city;
		model.user.zipcode = user.zipcode;
		//
		const objJson = JSON.stringify(model);
		//encrypt json string
		const e = btoa(objJson);
		// write data
		localStorage.setItem(this.key, e);
		return true;
	}

	logout() {
		if (localStorage.getItem(this.key)) {
			localStorage.removeItem(this.key);
			return true;
		}
		return false;
	}

	getUser() {
		// get encrypted data
		const encrypted = localStorage.getItem(this.key);
		// decrypt data
		const rawString = atob(encrypted);
		// cast  to user object
		return model.asViewModel(JSON.parse(rawString).user);
	}

	getToken() {
		// get encrypted data
		const encrypted = localStorage.getItem(this.key);
		// decrypt data
		const rawString = atob(encrypted);
		// cast  to user object
		return JSON.parse(rawString).token;
	}

	isAuthenticated() {
		return localStorage.getItem(this.key) ? true : false;
	}
}
const authProvider = new AuthProvider("auth_user");
export default authProvider;
