import React, { Component } from "react";
import { observer, inject } from "mobx-react";
import LoadingIndicator from "../../partials/loading/loading";
import NoContent from "../../partials/NoContent";
import $ from "jquery";
import PageProperty from "../../partials/page-property";
import { utility } from "../../../utility/core";

@inject("transactionStore")
@observer
class FundRequestList extends Component {
	state = { address: "", target: 0, id: "", user: "" };

	// copyAddress
	copyAddress = address => {
		this.setState({ address: address });
		$("#trg").click();
	};
	approveTransaction = (id, user, target) => {
		this.setState({ id: id });
		this.setState({ user: user });
		this.setState({ target: target });
	};
	handleApprove = () => {
		if (this.state.target === 0)
			this.props.transactionStore.approveTransaction(
				this.state.id,
				this.state.user
			);
		else this.props.transactionStore.cancelTransaction(this.state.id);
	};
	componentDidMount() {
		this.props.transactionStore.getFundRequest();
	}
	//
	renderStatus(status) {
		if (status === 1) {
			return <span className="badge-info badge-pill p-1"> In Process</span>;
		} else if (status === 2) {
			return <span className="badge-warning badge-pill p-1"> Pending</span>;
		} else if (status === 3) {
			return <span className="badge-danger badge-pill p-1"> Cancelled</span>;
		} else {
			return <span className="badge-success badge-pill p-1"> Completed</span>;
		}
	}

	// copy
	copyToClipboard = () => {
		utility.copyToClipboard("cont", this.state.address);
	};
	render() {
		const { transactionStore, title } = this.props;
		if (transactionStore.isLoading) {
			return <LoadingIndicator useBackdrop={true} />;
		} else {
			return (
				<div className="col-md-12">
					<div className="card mb-3">
						<div className="card-header">{title}</div>
						<div className="card-body animate-bottom">
							<div className="table-responsive">
								{transactionStore.fundRequests.length === 0 ? (
									<NoContent message={transactionStore.errorMessage} />
								) : (
									<table
										className="table table-hover mb-0 table_s1 "
										id="dataTable"
										width="100%"
										cellSpacing="0"
									>
										<thead className="thead-light">
											<tr>
												<th scope="col">#</th>
												<th scope="col">Person</th>
												<th scope="col">Amount</th>
												<th className="text-center" align="center" scope="col">
													Currency
												</th>
												<th scope="col">Status</th>
												<th scope="col">Date</th>
												<th scope="col">Actions</th>
											</tr>
										</thead>
										<tbody>
											{transactionStore.fundRequests.map((doc, index) => {
												return (
													<tr key={doc.id}>
														<th scope="row">{index + 1}</th>
														<td>
															{doc.user.name} <br />
															<small className="text_info">
																{doc.user.email}
															</small>
														</td>
														<th className="text_blue">
															{Intl.NumberFormat("en-US", {
																currency: "USD",
																style: "currency"
															}).format(doc.amount)}
														</th>
														<td align="center">{doc.currency}</td>
														<td>{this.renderStatus(doc.status)}</td>

														<td>
															{new Intl.DateTimeFormat("en-US", {
																year: "numeric",
																month: "long",
																day: "2-digit"
															}).format(new Date(doc.date))}
														</td>
														<td>
															<div
																className="btn-group btn-group-sm"
																role="group"
																aria-label="Basic example"
															>
																<button
																	title="Approve Transaction"
																	type="button"
																	data-target="#cancelBox"
																	data-toggle="modal"
																	onClick={() =>
																		this.approveTransaction(
																			doc.id,
																			doc.user.id,
																			0
																		)
																	}
																	className="btn btn-dark"
																>
																	<i className="icon-check icons" />
																</button>
																<button
																	title="Decline Transaction"
																	type="button"
																	data-target="#cancelBox"
																	data-toggle="modal"
																	onClick={() =>
																		this.approveTransaction(
																			doc.id,
																			doc.user.id,
																			1
																		)
																	}
																	className="btn btn-light"
																>
																	<i className="icon-close icons" />
																</button>
																<button
																	title="View Wallet Address"
																	onClick={() =>
																		this.copyAddress(doc.user.walletId)
																	}
																	type="button"
																	className="btn btn-info"
																>
																	<i className="icon-eye icons" />
																</button>
															</div>
														</td>
													</tr>
												);
											})}
										</tbody>
									</table>
								)}
							</div>
							<PageProperty
								page={transactionStore.fundRequestOption.page}
								pages={transactionStore.fundRequestOption.pages}
								total={transactionStore.fundRequestOption.total}
								limit={transactionStore.limit}
								length={transactionStore.fundRequests.length}
								onClick={transactionStore.loadMoreFundRequest}
							/>
						</div>
					</div>
					<div
						className="modal fade"
						id="cancelBox"
						tabIndex="-1"
						role="dialog"
						aria-labelledby="CancelTransaction"
						aria-hidden="true"
					>
						<div className="modal-dialog" role="document">
							<div className="modal-content">
								<div className="modal-header">
									<h5 className="modal-title" id="CancelTransaction">
										Transaction Action!
									</h5>
									<button
										type="button"
										className="close"
										data-dismiss="modal"
										aria-label="Close"
									>
										<span aria-hidden="true">&times;</span>
									</button>
								</div>
								<div className="modal-body">
									Are you sure you want to perform this actions? It may not be
									undone.
								</div>
								<div className="modal-footer">
									<button
										type="button"
										className="btn btn-light"
										data-dismiss="modal"
									>
										Cancel
									</button>
									<button
										onClick={this.handleApprove}
										type="button"
										data-dismiss="modal"
										className="btn btn-primary"
									>
										Continue <i className="icon-arrow-right icons" />
									</button>
								</div>
							</div>
						</div>
					</div>
					<input
						id="trg"
						data-target="#confirmPayment"
						data-toggle="modal"
						type="button"
						hidden="hidden"
					/>
					<div
						className="modal fade"
						id="confirmPayment"
						tabIndex="-1"
						role="dialog"
						aria-labelledby="cO_2"
						aria-hidden="true"
					>
						<div className="modal-dialog" role="document">
							<div className="modal-content">
								<div className="modal-header">
									<h5 className="modal-title" id="cO_2">
										User Wallet Address
									</h5>
									<button
										className="close"
										type="button"
										data-dismiss="modal"
										aria-label="Close"
									>
										<span aria-hidden="true"></span>
									</button>
								</div>
								<div className="modal-body">
									<div className="text-center m-2">
										<div id="cont" className="copy-button mb-1">
											<input
												type="text"
												value={this.state.address}
												placeholder={this.state.address}
												readOnly={true}
												className="form-control copy_input"
												name="address"
											/>
											<span
												className="copy-link btn-dark"
												onClick={this.copyToClipboard}
												data-toggle="tooltip"
												title="Copy to Clipboard"
											>
												Copy
											</span>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			);
		}
	}
}

export default FundRequestList;
