import React, { Component } from "react";
import { inject, observer } from "mobx-react";
import NoContent from "../../partials/NoContent";

@inject("transactionStore")
@observer
class TopTransactionList extends Component {
	renderStatus(status) {
		if (status === 1) {
			return <span className="badge-info badge-pill p-1">In Process</span>;
		} else if (status === 2) {
			return <span className="badge-warning badge-pill p-1">Pending</span>;
		} else if (status === 3) {
			return <span className="badge-danger badge-pill p-1">Cancelled</span>;
		} else {
			return <span className="badge-success badge-pill p-1">Completed</span>;
		}
	}

	bodyRender = () => {
		if (this.props.transactionStore.transactionList.length === 0) {
			return <NoContent message="You haven't made any transaction yet." />;
		} else {
			return (
				<div className="table-responsive">
					<table
						className="table table-hover mb-0 table_s1"
						width="100%"
						cellSpacing="0"
					>
						<thead>
							<tr>
								<th className="hidden-sm">#</th>
								<th>Currency</th>
								<th className="hidden-sm">Status</th>
								<th>Amount</th>
								<th>Date</th>
								<th>Type</th>
							</tr>
						</thead>
						<tfoot className="hidden-sm">
							<tr>
								<th>#</th>
								<th>Currency</th>
								<th>Status</th>
								<th>Amount</th>
								<th>Date</th>
								<th>Type</th>
							</tr>
						</tfoot>
						<tbody>
							{this.props.transactionStore.transactionList
								.slice(0, 12)
								.map((item, indx) => (
									<tr key={item.id}>
										<td className="hidden-sm">{indx + 1}</td>
										<td>
											<i className={`cc ${item.currency}-alt`} title="BTC" />
											{item.currency}
										</td>
										<td className="hidden-sm">
											{this.renderStatus(item.status)}
										</td>
										<td>
											{Intl.NumberFormat("en-US", {
												currency: "USD",
												style: "currency"
											}).format(item.amount)}
										</td>
										<td>
											{new Intl.DateTimeFormat("en-US").format(
												new Date(item.date)
											)}
										</td>
										<td>
											{item.type === 1 ? (
												<span className="text-success">Fund</span>
											) : (
												<span className="text-danger">Withdraw</span>
											)}
										</td>
									</tr>
								))}
						</tbody>
					</table>
				</div>
			);
		}
	};

	componentDidMount() {
		this.props.transactionStore.getTransactions(this.props.userid);
	}
	render() {
		return (
			<div className="card mb-3 btn-shadow">
				<div className="card-header">
					Transactions <span className="badge badge-danger">Top 12</span>
				</div>
				<div className="card-body">{this.bodyRender()}</div>
			</div>
		);
	}
}

export default TopTransactionList;
