import React, { PureComponent, Fragment } from "react";
import { NavLink } from "react-router-dom";
class ProfileDropdown extends PureComponent {
	logout = () => {
		this.props.onLogout();
	};

	render() {
		const styles = {
			marginTop: 6
		};
		return (
			<Fragment>
				<li style={styles} className="nav-item dropdown menu_user">
					<a
						className="nav-link dropdown-toggle"
						id="userDropdown"
						data-toggle="dropdown"
						aria-haspopup="true"
						aria-expanded="false"
						href="#!"
					>
						<span className="user_img">
							<i className="icon-user text-danger icons" />
						</span>
						{this.props.username || "No Name"}
					</a>
					<div
						className="dropdown-menu dropdown-menu-right"
						aria-labelledby="userDropdown"
					>
						<ul className="menu-nav list_none">
							<li>
								<NavLink to={{ pathname: `${this.props.currentPath}/profile` }}>
									<i className="icon-user icons" />
									<span>Your Profile</span>
								</NavLink>
							</li>
							<li>
								<NavLink
									to={{ pathname: `${this.props.currentPath}/investment` }}
								>
									<i className="icon-wallet icons" />
									<span>Investment</span>
								</NavLink>
							</li>
							<li>
								<div className="dropdown-divider" />
							</li>
							<li>
								<a
									href="#!"
									className="text-danger"
									data-toggle="modal"
									style={{ cursor: "pointer" }}
									data-target="#exampleModal"
								>
									<i className="icon-logout icons" />
									Logout
								</a>
							</li>
						</ul>
					</div>
				</li>
				<div
					className="modal fade"
					id="exampleModal"
					tabIndex="-1"
					role="dialog"
					aria-labelledby="exampleModalLabel"
					aria-hidden="true"
				>
					<div className="modal-dialog" role="document">
						<div className="modal-content">
							<div className="modal-header">
								<h5 className="modal-title" id="exampleModalLabel">
									Ready to Leave?
								</h5>
							</div>
							<div className="modal-body">
								Select "Logout" below if you are ready to end your current
								session.
							</div>
							<div className="modal-footer">
								<button
									className="btn btn-light"
									type="button"
									data-dismiss="modal"
								>
									Cancel
								</button>
								<button
									onClick={this.logout}
									data-dismiss="modal"
									className="btn btn-default"
								>
									<i className="icon-logout icons" /> Logout
								</button>
							</div>
						</div>
					</div>
				</div>
			</Fragment>
		);
	}
}

export default ProfileDropdown;
