import React, { Component } from "react";
import { Link } from "react-router-dom";
import { Helmet } from "react-helmet";
import { utility } from "../utility/core";
import logo from "../assets/images/dashboard_logo.png";
import { inject, observer } from "mobx-react";

@inject("userStore")
@observer
class AccountVerification extends Component {
	state = { id: "", email: "", message: "" };
	constructor(props) {
		super(props);
		// check if account has been verified
		const id = utility.getParameterByName("id") || "5bcc8910e1d4d227dc9e269d";
		this.props.userStore
			.getSingleUser(id)
			.then(user => {
				if (user.verified) {
					alert("Your account has been verified already!");
					this.props.history.push("/login");
				}
				// verify here
				this.props.userStore.vAccount(id);
			})
			.catch(er => {
				this.setState({ message: er.message });
			});
	}
	//
	componentDidMount() {
		this.setState({
			email: utility.getParameterByName("email") || "test@test.com"
		});
		this.setState({
			id: utility.getParameterByName("id") || "5bcc8910e1d4d227dc9e269d"
		});
	}

	render() {
		return (
			<div className="bg_image h-100'">
				<Helmet>
					<title>Account verification | {utility.GetAppName()}</title>
				</Helmet>
				<div
					className="lr_wrap signup"
					style={{ top: "35%", maxWidth: "540px" }}
				>
					<div className="card-body">
						{this.state.message ? (
							<h5 className="my-4 text-center text-danger text-uppercase ">
								{this.state.message}
							</h5>
						) : (
							<React.Fragment>
								<div className="lr_icon text-center">
									<img style={{ maxWidth: "180px" }} src={logo} alt="logo" />
								</div>
								<h6 className="my-4 text-center text-success">
									Account Verification Successful!
								</h6>

								<div className="text-center mb-5">
									<p>
										Thank you for joining the Most Trusted Cryptocurrency
										Investment Company.
									</p>
									<p>
										Invest your Cryptocurrency with us, the world's truest
										Cryptocurrency Investment platform
									</p>
								</div>

								<div className="text-right">
									<Link
										to={{ pathname: `/login` }}
										className="btn btn-default mb-5"
									>
										Continue <i className="icon-arrow-right icons" />
									</Link>
								</div>
							</React.Fragment>
						)}
					</div>
				</div>
			</div>
		);
	}
}

export default AccountVerification;
