import React, { Component, Fragment } from "react";
import Header from "../partials/header";
import Sidebar from "../partials/sidebar";
import { Helmet } from "react-helmet";
import Footer from "../partials/footer";
import routes from "../../utility/routes.content";
import authProvider from "../../services/auth.provider";
import VerifyDialogBox from "../verifyBox";
import AdminRoute from "../partials/admin.router";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.min.css";
import { CSSTransition, TransitionGroup } from "react-transition-group";
import { utility } from "../../utility/core";

class Auth extends Component {
	constructor(props) {
		super(props);
		// update html body
		document.body.classList.add("fixed-nav");
		document.body.classList.add("sticky-footer");
		document.body.id = "page-top";
	}

	// logout
	handleLogout = () => {
		// Clear localStorage
		authProvider.logout();
		setTimeout(() => {
			document.location.href = utility.getUrl();
		}, 200);
	};
	render() {
		return (
			<Fragment>
				<Helmet>
					<title>Dashborad | {utility.GetAppName()}</title>
				</Helmet>
				<nav
					className="navbar navbar-expand-lg navbar-light bg-light fixed-top"
					id="mainNav"
				>
					<Header
						username={authProvider.getUser().name}
						handleLogout={this.handleLogout}
						currentPath={this.props.match.url}
					/>
					<Sidebar currentPath={this.props.match.url} />
				</nav>
				<div className="content-wrapper">
					<TransitionGroup className="container-fluid">
						{routes.map((route, index) => (
							// Render more <Route>s with the same paths as
							// above, but different components this time.
							<CSSTransition
								key={index}
								appear={true}
								timeout={400}
								classNames="fade"
							>
								<AdminRoute
									key={index}
									path={this.props.match.url + route.path}
									exact={route.exact}
									component={route.component}
									admin={route.admin}
									active={this.props.match.url}
								/>
							</CSSTransition>
						))}
					</TransitionGroup>
					<Footer />
					<VerifyDialogBox />
				</div>
				<ToastContainer />
			</Fragment>
		);
	}
}

export default Auth;
