import React, { Component } from "react";
import ActivePage from "../partials/activepage";
import { Helmet } from "react-helmet";
import { inject, observer } from "mobx-react";
import LoadingIndicator from "../partials/loading/loading";
import NoContent from "../partials/NoContent";
import $ from "jquery";
import { CSSTransition, TransitionGroup } from "react-transition-group";
import { utility } from "../../utility/core";
import auth from "../../services/auth.provider";
import PageNotFound from "./notfound";

@inject("planStore")
@observer
class CryptoCurrency extends Component {
	state = {
		state: 0,
		name: "",
		icon: "",
		address: "",
		id: ""
	};
	updateOrAdd = (state, model) => {
		this.setState({ state: state });
		this.setState({ name: model.name });
		this.setState({ icon: model.icon });
		this.setState({ address: model.address });
		this.setState({ id: model.id });
		$("#target").click();
	};
	componentDidMount() {
		if (auth.isAuthenticated() && auth.getUser().admin)
			this.props.planStore.getCurrencies();
	}
	// copy text
	copyToClipboard = (id, address) => {
		utility.copyToClipboard(id, address);
	};

	content = () => {
		if (this.props.planStore.currency.loading) {
			return <LoadingIndicator useBackdrop={true} />;
		} else {
			if (this.props.planStore.currency.currencies.length === 0) {
				return (
					<NoContent message={this.props.planStore.currency.errorMessage} />
				);
			} else {
				return (
					<TransitionGroup className="row">
						{this.props.planStore.currency.currencies.map(item => (
							<CSSTransition
								key={item.id}
								appear={true}
								timeout={100}
								classNames="fade"
							>
								<div key={item.id} className="col-xl-4 col-sm-6">
									<div className="card mb-4 btn-shadow">
										<div className="card-sm-header">
											<span className="wallet-heading">
												<i
													className={`cc ${item.icon} text_primary`}
													title={item.name}
												/>
												<h4>{item.name}</h4>
											</span>
										</div>
										<div id={item.id} className="card-body">
											<h6 className="mb-2">Wallet Address</h6>
											<div className="copy-button mb-4">
												<input
													type="text"
													value={item.address}
													placeholder={item.address}
													readOnly={true}
													className="form-control copy_input"
													name="address"
												/>
												<span
													className="copy-link btn-dark"
													data-toggle="tooltip"
													onClick={() =>
														this.copyToClipboard(item.id, item.address)
													}
													title="Copy to Clipboard"
												>
													Copy
												</span>
											</div>

											<button
												onClick={() => this.updateOrAdd(1, item)}
												type="button"
												className="btn btn-light"
											>
												Update <i className="icon-note icons" />
											</button>
											<button
												type="button"
												onClick={() => this.onRemove(item.id)}
												className="btn btn-default pull-right"
											>
												Remove <i className="icon-trash icons" />
											</button>
										</div>
									</div>
								</div>
							</CSSTransition>
						))}
					</TransitionGroup>
				);
			}
		}
	};

	// handle value change
	onChange = e => {
		this.setState({ [e.target.name]: e.target.value });
	};

	//handle submit
	handleSubmit = () => {
		if (this.state.state === 0)
			this.props.planStore.addNewCurrency(
				this.state.name,
				this.state.icon,
				this.state.address
			);
		else
			this.props.planStore.updateCurrency(
				this.state.id,
				this.state.name,
				this.state.icon,
				this.state.address
			);
		// reset input
		this.setState({ name: "" });
		this.setState({ icon: "" });
		this.setState({ address: "" });
		this.setState({ id: "" });
	};

	onRemove = id => {
		this.setState({ id: id });
		$("#removeTarget").click();
	};

	// remove
	handleApprove = () => {
		this.props.planStore.deleteCurrency(this.state.id);
	};

	// render view
	render() {
		if (auth.isAuthenticated() && !auth.getUser().admin)
			return (
				<React.Fragment>
					<Helmet>
						<title>Resources not found | {utility.GetAppName()}</title>
					</Helmet>
					<PageNotFound />
				</React.Fragment>
			);
		const title = "Crytocurrency";
		const action = this.state.state === 0 ? "Create New" : "Update";
		return (
			<React.Fragment>
				<ActivePage currentPath={this.props.match.url} title={title} />
				<Helmet>
					<title>
						{title} | {utility.GetAppName()}
					</title>
				</Helmet>
				<div className="row page_title" style={{ textAlign: "right" }}>
					<div className="col-sm-12">
						<button
							type="button"
							onClick={() =>
								this.updateOrAdd(0, { name: "", icon: "", address: "" })
							}
							className="btn btn-default"
						>
							<i className="icon-pencil icons" /> Add new currency
						</button>
						<input
							type="hidden"
							data-target="#add-currency"
							data-toggle="modal"
							id="target"
						/>
					</div>
				</div>
				{this.content()}
				{/* new currency */}
				<div
					className="modal fade"
					id="add-currency"
					tabIndex="-1"
					role="dialog"
					aria-labelledby="hgk_08"
					aria-hidden="true"
				>
					<div className="modal-dialog" role="document">
						<div className="modal-content">
							<div className="modal-header">
								<h5 className="modal-title" id="hgk_08">
									{action} Currency
								</h5>
							</div>
							<div className="modal-body">
								<form>
									<div className="row">
										<div className="form-group col-md-6">
											<label className="col-form-label">Name</label>
											<div className="input-group">
												<input
													type="text"
													autoFocus={true}
													name="name"
													required
													value={this.state.name}
													onChange={this.onChange}
													placeholder="enter name"
													className="form-control"
												/>
											</div>
										</div>
										<div className="form-group col-md-6">
											<label className="col-form-label">Icon</label>
											<div className="input-group">
												<input
													type="text"
													placeholder="enter icon"
													name="icon"
													value={this.state.icon}
													onChange={this.onChange}
													required
													className="form-control"
												/>
											</div>
										</div>
									</div>
									<div className="row">
										<div className="form-group col-md-12">
											<label className="col-form-label">Wallet Address</label>
											<div className="input-group">
												<input
													type="text"
													name="address"
													required
													onChange={this.onChange}
													value={this.state.address}
													placeholder="enter address"
													className="form-control"
												/>
											</div>
										</div>
									</div>
								</form>
							</div>
							<div className="modal-footer">
								<button
									type="button"
									className="btn btn-light "
									data-dismiss="modal"
								>
									Cancel
								</button>
								<button
									className="btn btn-default"
									onClick={this.handleSubmit}
									type="button"
									data-dismiss="modal"
								>
									{action} Now <i className="icon-arrow-right icons" />
								</button>
							</div>
						</div>
					</div>
				</div>
				{/* Confirm box */}
				<input
					type="hidden"
					data-target="#cancelBox"
					data-toggle="modal"
					id="removeTarget"
				/>
				<div
					className="modal fade"
					id="cancelBox"
					tabIndex="-1"
					role="dialog"
					aria-labelledby="CancelTransaction"
					aria-hidden="true"
				>
					<div className="modal-dialog" role="document">
						<div className="modal-content">
							<div className="modal-header">
								<h5 className="modal-title" id="CancelTransaction">
									Remove Currency!
								</h5>
							</div>
							<div className="modal-body">
								Are you sure you want to remove this currency? It can't not be
								undone.
							</div>
							<div className="modal-footer">
								<button
									type="button"
									className="btn btn-default"
									data-dismiss="modal"
								>
									Cancel
								</button>
								<button
									onClick={this.handleApprove}
									type="button"
									data-dismiss="modal"
									className="btn btn-light"
								>
									Continue <i className="icon-arrow-right icons" />
								</button>
							</div>
						</div>
					</div>
				</div>
			</React.Fragment>
		);
	}
}

export default CryptoCurrency;
