import React, { Component } from "react";
import { inject, observer } from "mobx-react";

@inject("userStore")
@observer
class Timeline extends Component {
	componentDidMount() {
		this.props.userStore.getTimeLineList(this.props.userid);
	}
	render() {
		return (
			<div className="card mb-4 btn-shadow">
				<div className="card-header text_md_center">
					Timeline{" "}
					<span className="badge badge-warning">Most Recent</span>
				</div>
				<div className="card-body">
					<ul className="timeline_wrap list_none fc-scroll p-0">
						{this.props.userStore.timelines.map((item, index) => (
							<li key={item.id}>
								<div
									className={`timeline_icon ${(index === 0 ||
										index === 1) &&
										"tm_complete"}`}
								/>
								<div className="timeline_content">
									<h6>{item.phrase}</h6>
									<span>
										{new Intl.DateTimeFormat("en-US", {
											year: "numeric",
											month: "long",
											day: "2-digit"
										}).format(new Date(item.date))}
									</span>
								</div>
							</li>
						))}
					</ul>
				</div>
			</div>
		);
	}
}

export default Timeline;
