import React, { Component } from "react";
import { Helmet } from "react-helmet";
import logo from "../../assets/images/logo_icon_72.png";
import { Link } from "react-router-dom";
import { utility } from "../../utility/core";

// mobx
import { observer, inject } from "mobx-react";

@inject("userStore")
@observer
class ForgotPassword extends Component {
	constructor(props) {
		super(props);
		this.state = {
			email: ""
		};
	}

	onChange = e => {
		this.setState({ [e.target.name]: e.target.value });
	};
	// handle login
	resetForm = e => {
		e.preventDefault();
		const { email } = this.state;
		this.props.userStore.SendPasswordResetLink(email);
	};
	renderButtonText = () => {
		return this.props.userStore.isloading ? (
			<span>
				<i
					className="fa fa-circle-o-notch fa-spin"
					style={{ fontSize: "1.4em" }}
				/>
			</span>
		) : (
			<span>
				<i className="icon-refresh icon" /> Reset Password
			</span>
		);
	};
	render() {
		return (
			<div className="bg_image h-100">
				<Helmet>
					<title>Reset Password | {utility.GetAppName()}</title>
				</Helmet>
				<div className="lr_wrap">
					<div className="card-body animate-bottom">
						<Link
							title="Login"
							className="btn btn-light round btn-shadow"
							to="/login"
						>
							<i className="icon-arrow-left icon" />
						</Link>
						<div className="lr_icon text-center">
							<a rel="noopener noreferrer" href={utility.getUrl()}>
								<img src={logo} alt="logo" />
							</a>
						</div>
						<h6 className="my-4 text-center text-uppercase">Password Reset</h6>
						<form onSubmit={event => this.resetForm(event)}>
							<div className="form-group mb-4">
								<label htmlFor="exampleInputEmail1">Email Address</label>
								<input
									className="form-control"
									id="exampleInputEmail1"
									type="email"
									name="email"
									onChange={event => this.onChange(event)}
									placeholder="email address"
									required
								/>
								<small className="form-text text-muted">
									Enter the same email you signed up with.
								</small>
							</div>

							<div className="text-center">
								<button
									type="submit"
									className={`btn btn-default btn-block mt-2 ${this.props
										.userStore.isloading && "disabled"}`}
								>
									{this.renderButtonText()}
								</button>
							</div>
							{this.props.userStore.errorMessage && (
								<div className="alert alert-danger mt-2" role="alert">
									{this.props.userStore.errorMessage}
								</div>
							)}
						</form>
						<div className="text-right mt-4 mb-3 d-md-flex">
							<Link className="d-block small" to="/signup">
								Register an account <i className="icon-arrow-right icon" />
							</Link>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default ForgotPassword;
