import React, { Component, Fragment } from "react";
import ActivePage from "../../partials/activepage";
import { Helmet } from "react-helmet";
import { inject, observer } from "mobx-react";
import LoadingIndicator from "../../partials/loading/loading";
import PageProperty from "../../partials/page-property";
import { CSSTransition } from "react-transition-group";
import NoContent from "../../partials/NoContent";
import PageNotFound from "../notfound";
import auth from "../../../services/auth.provider";
import { utility } from "../../../utility/core";

@inject("investmentStore")
@observer
class TrailAccount extends Component {
	componentDidMount() {
		const { investmentStore } = this.props;
		if (!investmentStore.trailAccounts.length) {
			investmentStore.GetTrailAccount();
		}
	}

	render() {
		const { investmentStore } = this.props;
		const title = "Trial Investment Accounts";
		if (auth.isAuthenticated() && !auth.getUser().admin)
			return <PageNotFound />;
		if (investmentStore.loading) return <LoadingIndicator useBackdrop={true} />;
		else
			return (
				<Fragment>
					<Helmet>
						<title>
							{title} | {utility.GetAppName()}
						</title>
					</Helmet>
					<ActivePage title={title} />

					<div className="row">
						{investmentStore.trailAccounts.map(doc => (
							<CSSTransition
								in={true}
								key={doc.id}
								appear={true}
								timeout={200}
								classNames="slide"
							>
								<div className="col-xl-4">
									<div className="card mb-4">
										<div className="card-sm-header">
											<span className="wallet-heading">
												<h4 title="Person">
													{doc.user.verified && (
														<i
															title="Account verified"
															class="icon-check icons text-success"
														/>
													)}
													{!doc.user.verified && (
														<i
															title="Account not verified"
															class="icon-close icons text-danger"
														/>
													)}
													{doc.user.name}
												</h4>
												<span className="text-small text-muted">
													{doc.user.email}
												</span>
											</span>
										</div>
										<div className="card-body">
											<ul className="list_none p-0 wallet_info">
												<li>
													<span className="text-muted">Investment Made</span>
													<span className="h5 text-success">
														{Intl.NumberFormat("en-US", {
															currency: "USD",
															style: "currency"
														}).format(doc.investmentMade)}
													</span>
												</li>

												<li>
													<span className="text-muted">Current Balance</span>
													<span className="h6">
														{Intl.NumberFormat("en-US", {
															currency: "USD",
															style: "currency"
														}).format(doc.currentBalance)}
													</span>
												</li>
												<li>
													<span className="text-muted">Selected Service</span>
													<span>{doc.targetService}</span>
												</li>
												<li>
													<span className="text-muted">Payment Source</span>

													<span className="badge-success badge-pill">
														{(doc.currency && doc.currency.name) ||
															"Credit Card"}
													</span>
												</li>
											</ul>
											<div className="text-center">
												{/* {this.optionButton(
												doc.currentBalance,
												model.id,
												model.currentBalance,
												(model.currency && model.currency.name) || "Credit Card"
											)} */}
											</div>
										</div>
									</div>
								</div>
							</CSSTransition>
						))}
					</div>
					{!investmentStore.trailAccounts.length && (
						<NoContent message={investmentStore.message} />
					)}
					<PageProperty
						page={investmentStore.props.page}
						pages={investmentStore.props.pages}
						total={investmentStore.props.total}
						limit={investmentStore.props.limit}
						onClick={investmentStore.LoadMore}
						length={investmentStore.trailAccounts.length}
					/>
				</Fragment>
			);
	}
}

export default TrailAccount;
