import auth from "../services/auth.provider";
import { response } from "../model/response";

class CurrencyService {
    constructor() {
        this.base = "/api/investments/currency/";
    }
    /**
     * Create new currency
     * @param {string} name the new currency' name
     * @param {string} address the wallet address of the new currency
     * @param {string} icon the currency css icon class
     */
    async NewCurrency(name, address, icon) {
        const res = await fetch(`${this.base}new`, {
            method: "POST",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
                Authorization: `Bearer ${auth.getToken()}`,
            },
            body: JSON.stringify({ name, address, icon }),
        });

        const result = response.asSingleResponse(await res.json());
        return result;
    }
    /**
     * Get all currency withon the system
     */
    async getCurrencies() {
        const res = await fetch(`${this.base}all`);
        const result = response.asListResponse(await res.json());
        return result;
    }

    async updateCurrency(id, name, icon, address) {
        const res = await fetch(`${this.base}update/${id}`, {
            method: "PUT",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
                Authorization: `Bearer ${auth.getToken()}`,
            },
            body: JSON.stringify({ name, address, icon }),
        });

        const result = response.asSingleResponse(await res.json());
        return result;
    }

    async deleteCurrency(id) {
        const res = await fetch(`${this.base}remove?id=${id}`, {
            method: "DELETE",
            headers: {
                Accept: "application/json",
                "Content-Type": "application/json",
                Authorization: `Bearer ${auth.getToken()}`,
            },
        });

        const result = response.asSingleResponse(await res.json());
        return result;
    }
}
export const currencyService = new CurrencyService();
