import { response } from "../model/response";
class NotificationProvider {
	constructor() {
		this.base = "/api/notification/";
	}

	/**
	 * get user notification
	 * @param {string} userid user's id
	 * @param {number} page page number
	 * @param {number} limit page size per request
	 */
	async getNotifcation(userid, page, limit) {
		const res = await fetch(
			`${this.base}/${userid}?page=${page}&limit=${limit}`
		);
		let body;
		if (res.ok) {
			body = response.asListResponse(await res.json());
		}
		return body;
	}

	/**
	 * Update notification seen state
	 * @param {string} id notification id
	 */
	async markAsseen(id) {
		const res = await fetch(`${this.base}/seen?id=${id}`, {
			method: "PUT",
			headers: {
				accept: "application/json",
				"content-Type": "application/json"
			}
		});
		let body;
		if (res.ok) {
			body = response.asSingleResponse(await res.json());
		}
		return body;
	}
}

export const notificationProvider = new NotificationProvider();
