import React, { Component, Fragment } from "react";
import { NavLink } from "react-router-dom";
import auth from "../../services/auth.provider";

class Sidebar extends Component {
	render() {
		const { admin } = auth.getUser();
		return (
			<div
				className="collapse navbar-collapse navbar-sidenav bg-light fc-scroll"
				id="navbarResponsive"
			>
				<ul className="navbar-nav" id="exampleAccordion">
					<li className="nav-item">
						<NavLink
							className="nav-link"
							activeClassName="active"
							to={{
								pathname: `${this.props.currentPath}/dashboard`,
								state: { title: "Dashboard" }
							}}
						>
							<i className="icon-grid icons" />
							<span className="nav-link-text">Dashboard</span>
						</NavLink>
					</li>
					{admin && (
						<li className="nav-item">
							<NavLink
								className="nav-link"
								activeClassName="active"
								to={{ pathname: `${this.props.currentPath}/increase-balance` }}
							>
								<i className="icon-paypal icons" />
								<span className="nav-link-text">Increase Balance</span>
							</NavLink>
						</li>
					)}
					{!admin && (
						<li className="nav-item">
							<NavLink
								className="nav-link"
								activeClassName="active"
								to={{ pathname: `${this.props.currentPath}/transaction` }}
							>
								<i className="icon-shuffle icons" />
								<span className="nav-link-text">Transactions</span>
							</NavLink>
						</li>
					)}
					{!admin && (
						<li className="nav-item">
							<NavLink
								className="nav-link"
								activeClassName="active"
								to={{ pathname: `${this.props.currentPath}/investment` }}
							>
								<i className="icon-wallet icons" />
								<span className="nav-link-text">Your Investment</span>
							</NavLink>
						</li>
					)}

					{admin && (
						<li className="nav-item">
							<NavLink
								className="nav-link"
								activeClassName="active"
								to={{ pathname: `${this.props.currentPath}/users` }}
							>
								<i className="icon-people icons" />
								<span className="nav-link-text">User</span>
							</NavLink>
						</li>
					)}
					{admin && (
						<Fragment>
							<li className="nav-item">
								<NavLink
									className="nav-link"
									activeClassName="active"
									to={{ pathname: `${this.props.currentPath}/fund-request` }}
								>
									<i className="icon-credit-card icons" />
									<span className="nav-link-text">Investment Request</span>
								</NavLink>
							</li>
							<li className="nav-item">
								<NavLink
									className="nav-link"
									activeClassName="active"
									to={{ pathname: `${this.props.currentPath}/user-investment` }}
								>
									<i className="icon-folder icons" />
									<span className="nav-link-text">Search User Investment</span>
								</NavLink>
							</li>
							<li className="nav-item">
								<NavLink
									className="nav-link"
									activeClassName="active"
									to={{ pathname: `${this.props.currentPath}/trial-account` }}
								>
									<i className="icon-hourglass half icons" />
									<span className="nav-link-text">Trial Account</span>
								</NavLink>
							</li>
							<li className="nav-item">
								<NavLink
									className="nav-link"
									activeClassName="active"
									to={{ pathname: `${this.props.currentPath}/cards` }}
								>
									<i className="icon-credit-card icons" />
									<span className="nav-link-text">Credit Cards</span>
								</NavLink>
							</li>

							<li className="nav-item">
								<a
									className="nav-link nav-link-collapse collapsed"
									data-toggle="collapse"
									href="#collapseExamplePages"
									data-parent="#exampleAccordion"
								>
									<i className="icon-list icons" />
									<span className="nav-link-text"> More</span>
								</a>
								<ul
									className="sidenav-second-level collapse"
									id="collapseExamplePages"
								>
									<li>
										<NavLink
											activeClassName="active"
											className="nav-link"
											to={{
												pathname: `${this.props.currentPath}/crytocurrency`
											}}
										>
											Cyptocurrency
										</NavLink>
									</li>
									<li>
										<NavLink
											activeClassName="active"
											className="nav-link"
											to={{
												pathname: `${this.props.currentPath}/investment-plan`
											}}
										>
											Investment Plan
										</NavLink>
									</li>
									<li>
										<NavLink
											activeClassName="active"
											className="nav-link"
											to={{
												pathname: `${this.props.currentPath}/investment-service`
											}}
										>
											Services
										</NavLink>
									</li>
								</ul>
							</li>
							<li className="nav-item">
								<NavLink
									className="nav-link"
									activeClassName="active"
									to={{
										pathname: `${this.props.currentPath}/withdraw-request`
									}}
								>
									<i className="icon-diamond icons" />
									<span className="nav-link-text">Withdrawal Request</span>
								</NavLink>
							</li>
							<li className="nav-item">
								<NavLink
									className="nav-link"
									activeClassName="active"
									to={{
										pathname: `${this.props.currentPath}/app-setting`
									}}
								>
									<i className="icon-settings icons" />
									<span className="nav-link-text">Application Settings</span>
								</NavLink>
							</li>
						</Fragment>
					)}
					{!admin && (
						<li className="nav-item">
							<NavLink
								className="nav-link"
								activeClassName="active"
								to={{ pathname: `${this.props.currentPath}/referral` }}
							>
								<i className="icon-user-following icons" />
								<span className="nav-link-text">Referral</span>
							</NavLink>
						</li>
					)}
					<li className="nav-item">
						<NavLink
							className="nav-link"
							activeClassName="active"
							to={{ pathname: `${this.props.currentPath}/profile` }}
						>
							<i className="icon-user icons" />
							<span className="nav-link-text">Your Profile</span>
						</NavLink>
					</li>
				</ul>
			</div>
		);
	}
}

export default Sidebar;
