import React, { Component, Fragment } from "react";
import ActivePage from "../../partials/activepage";
import { Helmet } from "react-helmet";
import { utility } from "../../../utility/core";
import { inject, observer } from "mobx-react";
import LoadingIndicator from "../../partials/loading/loading";
import auth from "../../../services/auth.provider";
import PageNotFound from "../notfound";

@inject("transactionStore")
@observer
class AppSettingComponent extends Component {
	constructor() {
		super();
		this.state = {
			setting: {
				name: "",
				validateEmail: false,
				validateWallet: false,
				next_fund_hour: 0,
				next_fund_hour_reminder: 0,
				percent: 0.1
			},
			data: { transactions: 0, investments: 0, investors: 0, consultants: 0 }
		};
	}
	componentDidMount() {
		if (auth.isAuthenticated() && auth.getUser().admin)
			this.props.transactionStore
				.GetApplicationSettingsData()
				.then(cb => {
					this.setState({ setting: cb.setting });
					this.setState({ data: cb.data });
				})
				.catch(() => {});
	}

	onDataChange = e => {
		const target = e.target;
		const value = target.type === "checkbox" ? target.checked : target.value;
		const name = target.name;

		this.setState({
			data: {
				...this.state.data,
				[name]: value
			}
		});
	};
	onSettingChange = e => {
		const target = e.target;
		const value = target.type === "checkbox" ? target.checked : target.value;
		const name = target.name;

		this.setState({
			setting: {
				...this.state.setting,
				[name]: value
			}
		});
	};
	updateSetting = e => {
		e.preventDefault();
		const {
			name,
			validateEmail,
			validateWallet,
			next_fund_hour,
			next_fund_hour_reminder,
			percent
		} = this.state.setting;
		this.props.transactionStore.UpdateAppSetting(
			name,
			validateEmail,
			validateWallet,
			next_fund_hour,
			next_fund_hour_reminder,
			percent
		);
	};

	updateData = e => {
		e.preventDefault();
		const {
			consultants,
			investments,
			investors,
			transactions
		} = this.state.data;
		this.props.transactionStore.UpdateAppData(
			transactions,
			consultants,
			investors,
			investments
		);
	};
	render() {
		const title = "Application Settings";
		if (auth.isAuthenticated() && !auth.getUser().admin)
			return (
				<Fragment>
					<Helmet>
						<title>Resources not found | {utility.GetAppName()}</title>
					</Helmet>
					<PageNotFound />
				</Fragment>
			);
		return (
			<Fragment>
				<div className="row">
					<Helmet>
						<title>
							{title} | {utility.GetAppName()}
						</title>
					</Helmet>
				</div>
				<ActivePage currentPath={this.props.match.url} title={title} />

				<div className="card mb-4">
					<div className="card-header">Application Setting</div>
					<div className="card-body">
						{this.props.transactionStore.loading && <LoadingIndicator />}
						<form className="form-horizontal row" onSubmit={this.updateSetting}>
							<div className="col-xl-12 col-lg-12  col-sm-12 form-group">
								<label>Application Name</label>
								<input
									type="text"
									className="form-control"
									value={this.state.setting.name}
									required
									onChange={this.onSettingChange}
									autoFocus
									name="name"
								/>
							</div>
							<div className="col-xl-4 col-lg-4  col-sm-12">
								<div className="form-group">
									<label>Next fund hour</label>
									<input
										type="number"
										className="form-control"
										value={this.state.setting.next_fund_hour}
										required
										onChange={this.onSettingChange}
										autoFocus
										name="next_fund_hour"
									/>
								</div>
							</div>
							<div className="col-xl-4 col-lg-4  col-sm-12">
								<div className="form-group">
									<label>Next fund hour reminder</label>
									<input
										type="number"
										className="form-control"
										value={this.state.setting.next_fund_hour_reminder}
										onChange={this.onSettingChange}
										required
										name="next_fund_hour_reminder"
									/>
								</div>
							</div>
							<div className="col-xl-4 col-lg-4  col-sm-12">
								<div className="form-group">
									<label>Percent for referral</label>
									<input
										type="number"
										className="form-control"
										value={this.state.setting.percent}
										onChange={this.onSettingChange}
										required
										name="percent"
									/>
								</div>
							</div>

							<div className="col-xl-6 col-lg-12">
								<div className="switch_box float-right pl-3">
									<label className="switch">
										<input
											checked={this.state.setting.validateWallet}
											name="validateWallet"
											onChange={this.onSettingChange}
											className="switch_checkbox"
											type="checkbox"
										/>
										<span className="switch_slide" />
									</label>
								</div>
								<h6>Bitcoin Wallet Verification</h6>
								<p className="small text-muted">
									Bitcoin wallet verfication adds extra data validation and
									authenticity. Note this only work with Bitcoin wallet address.
								</p>
							</div>
							<div className="col-xl-6 col-lg-12">
								<div className="switch_box float-right pl-3">
									<label className="switch">
										<input
											checked={this.state.setting.validateEmail}
											className="switch_checkbox"
											type="checkbox"
											name="validateEmail"
											onChange={this.onSettingChange}
										/>
										<span className="switch_slide" />
									</label>
								</div>
								<h6>Email Address Verification</h6>
								<p className="small text-muted">
									Email Authentication adds an extra layer of security to users'
									account. As well as your password.
								</p>
							</div>

							<div className="col-12 text-right">
								<button type="submit" className="btn btn-default mt-2">
									Save Changes <i className="icon-arrow-right icons" />
								</button>
							</div>
						</form>
					</div>
				</div>
				<div className="card mb-4">
					<div className="card-header">Data Setting</div>
					<div className="card-body">
						{this.props.transactionStore.loading && <LoadingIndicator />}
						<form
							className="form-horizontal row mt-2"
							onSubmit={this.updateData}
						>
							<div className="col-xl-6 col-lg-12  col-sm-12">
								<div className="form-group">
									<label>Daily Transaction</label>
									<input
										type="number"
										className="form-control"
										value={this.state.data.transactions}
										required
										onChange={this.onDataChange}
										autoFocus
										name="transactions"
									/>
								</div>
							</div>
							<div className="col-xl-6 col-lg-12  col-sm-12">
								<div className="form-group">
									<label>Total Active Investor</label>
									<input
										type="number"
										className="form-control"
										value={this.state.data.investors}
										onChange={this.onDataChange}
										required
										name="investors"
									/>
								</div>
							</div>
							<div className="col-xl-6 col-lg-12  col-sm-12">
								<div className="form-group">
									<label>Total Active Consultant</label>
									<input
										type="number"
										className="form-control"
										value={this.state.data.consultants}
										onChange={this.onDataChange}
										required
										name="consultants"
									/>
								</div>
							</div>

							<div className="col-xl-6 col-lg-12  col-sm-12">
								<div className="form-group">
									<label htmlFor="investments">
										Total Investment of your company
									</label>
									<input
										type="number"
										name="investments"
										id="investments"
										required
										className="form-control"
										value={this.state.data.investments}
										onChange={this.onDataChange}
									/>
								</div>
							</div>
							<div className="col-12 text-right">
								<button type="submit" className="btn btn-default mt-2">
									Save Changes <i className="icon-arrow-right icons" />
								</button>
							</div>
						</form>
					</div>
				</div>
			</Fragment>
		);
	}
}

export default AppSettingComponent;
