import React, { PureComponent } from "react";

/**
 * @requires message Message to display for no record
 */
class NoContent extends PureComponent {
	render() {
		const styles = {
			marginTop: "4em",
			marginBottom: "6em",
			textAlign: "center"
		};
		const { message } = this.props;
		return (
			<div style={styles}>
				<i
					style={{ fontSize: "4em", color: "#DC2027" }}
					className="icon-drop icons"
				/>
				<br />
				<h5>No record found.</h5>
				<p>{message}</p>
			</div>
		);
	}
}

export default NoContent;
