import React, { Component } from "react";
import ActivePage from "../../partials/activepage";
import { Helmet } from "react-helmet";
import auth from "../../../services/auth.provider";
import TransactioList from "./list";
import { CSSTransition } from "react-transition-group";
import { utility } from "../../../utility/core";

class Transaction extends Component {
	render() {
		const title = "Transactions";
		const { id } = auth.getUser();
		return (
			<React.Fragment>
				<Helmet>
					<title>{title} | {utility.GetAppName()}</title>
				</Helmet>
				<ActivePage title={title} />
				<div className="row">
					{/* list comes here! */}
					<CSSTransition
						in={true}
						appear={true}
						timeout={1000}
						classNames="fade"
					>
						<TransactioList id={id} title="Transactions List" />
					</CSSTransition>
				</div>
			</React.Fragment>
		);
	}
}

export default Transaction;
