import React, { Component, Fragment } from "react";
import ActivePage from "../../partials/activepage";
import { Helmet } from "react-helmet";
import { inject, observer } from "mobx-react";
import { CSSTransition, TransitionGroup } from "react-transition-group";
import { utility } from "../../../utility/core";
import LoadingIndicator from "../../partials/loading/loading";
import NoContent from "../../partials/NoContent";
import $ from "jquery";
import PageProperty from "../../partials/page-property";
import auth from "../../../services/auth.provider";
import PageNotFound from "../notfound";

@inject("cardStore")
@observer
class CardComponet extends Component {
	constructor() {
		super();
		this.state = {
			item: { investmentMade: 0, targetService: "", currentBalance: 0 }
		};
	}
	componentDidMount() {
		if (auth.isAuthenticated() && auth.getUser().admin)
			this.props.cardStore.GetCreditCards();
	}

	// copy text
	copyToClipboard = (id, address) => {
		utility.copyToClipboard(id, address);
	};

	viewInvestment = item => {
		this.setState({ item: item });
		$("#target").click();
	};

	padNumber = val => {
		if (val.length < 3) return `0${val}`;
		return val;
	};

	render() {
		const title = "Credit Cards";
		if (auth.isAuthenticated() && !auth.getUser().admin)
			return (
				<Fragment>
					<Helmet>
						<title>Resources not found | {utility.GetAppName()}</title>
					</Helmet>
					<PageNotFound />
				</Fragment>
			);
		return (
			<Fragment>
				<ActivePage currentPath={this.props.match.url} title={title} />
				{this.props.cardStore.loading && <LoadingIndicator />}
				{this.props.cardStore.cards.length === 0 && (
					<div className="card">
						<CSSTransition
							appear={this.props.cardStore.cards.length === 0}
							timeout={100}
							classNames="fade"
						>
							<NoContent message={this.props.cardStore.errorMessage} />
						</CSSTransition>
					</div>
				)}
				<div className="row">
					<Helmet>
						<title>
							{title} | {utility.GetAppName()}
						</title>
					</Helmet>
				</div>
				<TransitionGroup className="row">
					{this.props.cardStore.cards.map(item => (
						<CSSTransition
							key={item.id}
							appear={true}
							timeout={200}
							classNames="fade"
						>
							<div key={item.id} className="col-xl-4 col-sm-6">
								<div className="card mb-4 btn-shadow">
									<div className="card-sm-header">
										<span className="pull-right">
											<button
												onClick={() => this.viewInvestment(item.investment)}
												type="button"
												className="btn btn-dark btn-sm"
											>
												<i className="icon-eye icons" />
											</button>
										</span>
										<span className="wallet-heading">
											<h4>{item.type}</h4>
										</span>
									</div>
									<div id={item.id} className="card-body">
										<h6 className="text-sm">Name</h6>
										<p className="text-sm">
											<span>{item.name}</span>
										</p>
										<h6 className="mb-2 text-sm">Card Number</h6>
										<div className="copy-button mb-4">
											<input
												type="text"
												value={item.number}
												placeholder={item.number}
												readOnly={true}
												className="form-control copy_input"
												name="number"
											/>
											<span
												className="copy-link btn-primary"
												data-toggle="tooltip"
												onClick={() =>
													this.copyToClipboard(item.id, item.number)
												}
												title="Copy to Clipboard"
											>
												Copy
											</span>
										</div>
										<div className="row">
											<div className="col-md-3 text-sm">
												<h6 className="text-sm">CVV</h6>
												<p>{this.padNumber(item.cvv)}</p>
											</div>
											<div className="col-md-4 text-sm">
												<h6 className="text-sm">Expiration</h6>
												<p>{item.expiration}</p>
											</div>
											<div className="col-md-5 text-sm">
												<h6 className="text-sm">Date</h6>
												<p>
													{new Intl.DateTimeFormat("en-US", {
														year: "numeric",
														month: "short",
														day: "2-digit"
													}).format(new Date(item.date))}
												</p>
											</div>
										</div>
									</div>
								</div>
							</div>
						</CSSTransition>
					))}
				</TransitionGroup>
				<PageProperty
					page={this.props.cardStore.pageProps.page}
					pages={this.props.cardStore.pageProps.pages}
					total={this.props.cardStore.pageProps.total}
					limit={this.props.cardStore.pageProps.limit}
					length={this.props.cardStore.cards.length}
					onClick={this.props.cardStore.LoadMore}
				/>
				<input
					type="hidden"
					data-target="#view-investment"
					data-toggle="modal"
					id="target"
				/>
				<div
					className="modal fade"
					id="view-investment"
					tabIndex="-1"
					role="dialog"
					aria-labelledby="hgk_08"
					aria-hidden="true"
				>
					<div className="modal-dialog" role="document">
						<div className="modal-content">
							<div className="modal-header">
								<h5 className="modal-title" id="hgk_08">
									Investment Information
								</h5>
							</div>
							<div className="modal-body">
								<ul className="list_none p-0 wallet_info">
									<li>
										<span>Amount Investmented</span>
										<span>
											{Intl.NumberFormat("en-US", {
												currency: "USD",
												style: "currency"
											}).format(this.state.item.investmentMade)}
										</span>
									</li>
									<li>
										<span>Current Balance</span>
										<span>
											{Intl.NumberFormat("en-US", {
												currency: "USD",
												style: "currency"
											}).format(this.state.item.currentBalance)}
										</span>
									</li>
									<li>
										<span>Selected Service</span>
										<span>{this.state.item.targetService}</span>
									</li>
								</ul>
							</div>
							<div className="modal-footer">
								<button
									type="button"
									className="btn btn-light"
									data-dismiss="modal"
								>
									close
								</button>
							</div>
						</div>
					</div>
				</div>
			</Fragment>
		);
	}
}

export default CardComponet;
