export const model = {
  asViewModel: model => {
    return {
      id: model.id,
      name: model.name,
      email: model.email,
      address: model.address,
      city: model.city,
      zipcode: model.zipcode,
      admin: model.admin,
      walletId: model.walletId,
      suspended: model.suspended,
      verified: model.verified,
      joined: model.joined,
      plan: model.plan && model.plan.plan,
      option: {
        notifyOnLogin: model.option.notifyOnLogin,
        useEmailLogin: model.option.useEmailLogin
      },
      referrer: model.referrer
    };
  }
};
