import React, { Component } from "react";
import { inject, observer } from "mobx-react";

@inject("transactionStore")
@observer
class TotalInvestment extends Component {
	render() {
		return (
			<div className="card mb-4 btn-shadow">
				<div className="card-header text_md_center">INVESTMENT STATISTICS</div>
				<div className="card-body">
					<div className="row align-items-center text_md_center">
						<div className="col-xl-6 col-lg-12">
							<h5 className="text-muted small">TOTAL INVESTMENT</h5>
							<h2 className="balance_text">
								{Intl.NumberFormat("en-US", {
									currency: "USD",
									style: "currency"
								}).format(this.props.transactionStore.getSumInvestment)}
							</h2>
						</div>
						<div className="col-xl-6 col-lg-12">
							<h5 className="text-muted small"># OF INVESTMENT</h5>
							<h2 className="balance_text">
								{Intl.NumberFormat("en-US", {}).format(
									this.props.transactionStore.investments.length
								)}
							</h2>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default TotalInvestment;
