import React, { Component } from "react";
import { Helmet } from "react-helmet";
import ActivePage from "../../partials/activepage";
import LoadingIndicator from "../../partials/loading/loading";
import auth from "../../../services/auth.provider";
import { inject, observer } from "mobx-react";
import ProfileInfo from "./info";
import TotalInvestment from "../dashboard/totalInvestment";
import TotalROI from "../dashboard/totalROI";
import { CSSTransition } from "react-transition-group";
import { utility } from "../../../utility/core";

@inject("userStore", "transactionStore")
@observer
class Profile extends Component {
	componentWillMount() {
		const user = auth.getUser();
		this.setState({ ...user });
	}

	componentDidMount() {
		this.props.transactionStore.getInvestment(this.state.id);
	}
	// update information
	updateInfo = () => {
		this.props.userStore.updateInformation(
			this.state.id,
			this.state.name,
			this.state.address,
			this.state.zipcode,
			this.state.city
		);
	};
	// onChange
	onChange = ev => {
		this.setState({ [ev.target.name]: ev.target.value });
	};

	renderContent = () => {
		if (this.props.userStore.isloading) {
			return <LoadingIndicator useBackdrop={true} />;
		} else {
			return (
				<div className="row">
					<div className="col-xl-4 col-md-5">
						<CSSTransition
							in={true}
							appear={true}
							timeout={500}
							classNames="fade"
						>
							<ProfileInfo />
						</CSSTransition>
						<CSSTransition
							timeout={500}
							appear={true}
							in={true}
							classNames="fade"
						>
							<TotalInvestment />
						</CSSTransition>
						<CSSTransition
							timeout={500}
							appear={true}
							in={true}
							classNames="fade"
						>
							<TotalROI />
						</CSSTransition>
					</div>

					<div className="col-xl-8 col-md-7">
						<div className="card mb-4 btn-shadow">
							<div className="card-header">General Setting</div>
							<div className="card-body">
								<form
									className="form-horizontal row mt-2"
									onSubmit={this.updateInfo}
								>
									<div className="col-xl-6 col-lg-12  col-sm-12">
										<div className="form-group">
											<label>Name</label>
											<input
												type="text"
												className="form-control"
												value={this.state.name}
												required
												onChange={this.onChange}
												autoFocus
												name="name"
											/>
										</div>
									</div>
									<div className="col-xl-6 col-lg-12  col-sm-12">
										<div className="form-group">
											<label>Email</label>
											<input
												type="text"
												className="form-control"
												value={this.state.email}
												required=""
												readOnly
												name="email"
											/>
										</div>
									</div>
									<div className="col-xl-6 col-lg-12  col-sm-12">
										<div className="form-group">
											<label>Address</label>
											<input
												type="text"
												className="form-control"
												value={this.state.address}
												onChange={this.onChange}
												required
												name="address"
											/>
										</div>
									</div>

									<div className="col-xl-6 col-lg-12  col-sm-12">
										<div className="form-group">
											<label htmlFor="oldwalletId">
												Wallet Address
											</label>
											<input
												type="text"
												name="walletId"
												id="walletId"
												className="form-control"
												value={this.state.walletId}
												readOnly
											/>
										</div>
									</div>
									<div className="col-xl-6 col-lg-12  col-sm-12">
										<div className="form-group">
											<label>Enter city</label>
											<input
												type="text"
												name="city"
												onChange={this.onChange}
												className="form-control"
												value={this.state.city}
											/>
										</div>
									</div>
									<div className="col-xl-6 col-lg-12  col-sm-12">
										<div className="form-group">
											<label>Postcode</label>
											<input
												type="text"
												name="zipcode"
												onChange={this.onChange}
												className="form-control"
												placeholder="Enter postcode"
												value={this.state.zipcode}
												required=""
											/>
										</div>
									</div>

									<div className="col-12 text-right">
										<button
											type="submit"
											className="btn btn-default mt-2"
										>
											Save Changes{" "}
											<i className="icon-arrow-right icons" />
										</button>
									</div>
									{this.props.userStore.errorMessage && (
										<div
											className="alert alert-danger"
											role="alert"
										>
											{this.props.userStore.errorMessage}
										</div>
									)}
								</form>
							</div>
						</div>
						<div className="card btn-shadow">
							<div className="card-header">Update Password</div>
							<div className="card-body">
								<div className="row">
									<div className="col-lg-12 text-center">
										<button
											type="submit"
											onClick={this.sendResetPassword}
											className={`btn btn-default  mt-2 ${this
												.props.userStore.isloading &&
												"disabled"}`}
										>
											{this.props.userStore.isloading ? (
												<span>Please wait ..</span>
											) : (
												<span>
													Request Password Change{" "}
													<i className="icon-key icons" />
												</span>
											)}
										</button>
										<br />
										<p className="small text-muted mt-2">
											You will receive password change
											link in your email
										</p>
									</div>
								</div>
							</div>
						</div>
						<div className="card btn-shadow">
							<div className="card-header">Security Setting</div>
							<div className="card-body">
								<div className="row">
									<div className="col-xl-6 col-lg-12">
										<div className="switch_box float-right pl-3">
											<label className="switch">
												<input //   checked={this.state.option.useEmailLogin}
													value={
														this.state.option
															.useEmailLogin
													}
													name="useEmailLogin"
													onChange={e =>
														this.onCheck(e)
													}
													className="switch_checkbox"
													type="checkbox"
												/>
												<span className="switch_slide" />
											</label>
										</div>
										<h6>Email Authorization</h6>
										<p className="small text-muted ">
											Email Authentication adds an extra
											layer of security to your account.
											As well as your password.
										</p>
									</div>
									<div className="col-xl-6 col-lg-12">
										<div className="switch_box float-right pl-3">
											<label className="switch">
												<input //   checked={this.state.option.notifyOnLogin}
													value={
														this.state.option
															.notifyOnLogin
													}
													className="switch_checkbox"
													type="checkbox"
													name="notifyOnLogin"
													onChange={e =>
														this.onCheck(e)
													}
												/>
												<span className="switch_slide" />
											</label>
										</div>
										<h6>Login Notification</h6>
										<p className="small text-muted ">
											Notifications may include alerts,
											sounds and icon badges, These can be
											configured in Settings.
										</p>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			);
		}
	};

	sendResetPassword = () => {
		this.props.userStore.SendPasswordResetLink(this.state.email);
	};

	render() {
		const title = "Profile";
		return (
			<React.Fragment>
				<Helmet>
					<title>
						{title} | {utility.GetAppName()}
					</title>
				</Helmet>
				<ActivePage title={title} />
				{this.renderContent()}
			</React.Fragment>
		);
	}
}

export default Profile;
