import { configure, observable, runInAction, action } from "mobx";
import { utility } from "../utility/core";
import transactionProvider from "../services/transaction.provider";
import { toast } from "react-toastify";
import openSocket from "socket.io-client"; // socket
import { endpoint } from "../model/events";

// config
configure({
	enforceActions: "observed"
});

class StatisticsStore {
	constructor() {
		this.usersocket = openSocket(utility.getUrl() + "/users", () => {});
		this.requestsocket = openSocket(utility.getUrl() + "/request", () => {});
		this.ListenToEvent();
	}
	ListenToEvent() {
		// new user
		this.usersocket.on(endpoint.user.new_user, () => {
			runInAction(() => {
				this.users += 1;
			});
		});

		//new fund request
		this.requestsocket.on(endpoint.new_fund_request, () => {
			runInAction(() => {
				this.fundRequests += 1;
				this.investments += 1;
			});
		});

		// new withdraw request
		this.requestsocket.on(endpoint.new_withdraw_request, () => {
			runInAction(() => {
				this.withdrawalRequest += 1;
			});
		});
	}

	@observable
	users = 0;

	@observable
	investments = 0;

	@observable
	fundRequests = 0;

	@observable
	withdrawalRequest = 0;

	@observable
	dueFund = 0;

	@observable
	loading = false;

	@action("get record")
	GetData = () => {
		runInAction(() => {
			this.loading = true;
		});
		transactionProvider
			.GetAppStatistics()
			.then(cb => {
				runInAction(() => {
					this.loading = false;
					if (cb.status === 200) {
						this.users = cb.doc.users;
						this.dueFund = cb.doc.dueFund;
						this.withdrawalRequest = cb.doc.withdrawalRequest;
						this.fundRequests = cb.doc.fundRequests;
						this.investments = cb.doc.investments;
					} else {
						toast(cb.message, {
							type: "error",
							position: "bottom-center"
						});
					}
				});
			})
			.catch(err => {
				toast(err.message, {
					type: "error",
					position: "bottom-center"
				});
			});
	};
}
export const statisticsStore = new StatisticsStore();
