import React, { Component } from "react";
import SingleUser from "./single";
import NoContent from "../../partials/NoContent";
import { observer, inject } from "mobx-react";
import PageProperty from "../../partials/page-property";
import UserFilter from "./filter";
import LoadingIndicator from "../../partials/loading/loading";

/**
 * @requires title page title
 * @requires errorMessage message to display if no record was found
 */
@inject("userStore")
@observer
class UserList extends Component {
	// remove account
	onRemoveAccount = id => {
		this.props.userStore.removeUser(id);
	};
	// remove account
	handleActivate = id => {
		this.props.userStore.updateAccountSuspensionState(id, false);
	};

	//
	handleASuspend = id => {
		this.props.userStore.updateAccountSuspensionState(id, true);
	};

	handleOnClick = state => {
		this.props.userStore.updateFilter(state);
	};

	handleSearch = email => {
		this.props.userStore.searchUserUsingEmail(email);
	};
	render() {
		const { title, errorMessage, userStore } = this.props;
		return (
			<div className="col-md-12">
				<UserFilter
					onSearch={this.handleSearch}
					active={userStore.filter}
					onClick={this.handleOnClick}
					title={title}
				/>
				<div className="card mb-3">
					<div className="card-body">
						<div className="table-responsive">
							{this.props.userStore.getUsersRecord.length === 0 ? (
								<NoContent message={errorMessage} />
							) : (
								<table
									className="table table-hover mb-0 table_s1 "
									id="dataTable"
									width="100%"
									cellSpacing="0"
								>
									<thead>
										<tr>
											<th scope="col">#</th>
											<th scope="col">Name</th>
											<th scope="col">Email</th>
											<th scope="col">City</th>
											<th scope="col">Current Plan</th>
											<th scope="col">Status</th>
											<th align="right" scope="col">
												Actions
											</th>
										</tr>
									</thead>
									<tbody>
										{this.props.userStore.getUsersRecord.map((doc, index) => {
											return (
												<SingleUser
													onRemove={() => this.onRemoveAccount(doc.id)}
													onActivate={() => this.handleActivate(doc.id)}
													onSuspend={() => this.handleASuspend(doc.id)}
													index={index}
													key={doc.id}
													item={doc}
												/>
											);
										})}
									</tbody>
								</table>
							)}
						</div>
						{this.props.userStore.isloading && (
					<LoadingIndicator useBackdrop={false} />
				)}
					
						<PageProperty
							page={userStore.page}
							pages={userStore.pages}
							total={userStore.total}
							limit={userStore.limit}
							onClick={userStore.loadMoreUser}
							length={userStore.getUsersRecord.length}
						/>
					</div>
				</div>
				<div
					className="modal fade"
					id="cancelBox"
					tabIndex="-1"
					role="dialog"
					aria-labelledby="g_3"
					aria-hidden="true"
				>
					<div className="modal-dialog" role="document">
						<div className="modal-content">
							<div className="modal-header">
								<h5 className="modal-title" id="g_3">
									Cancel Transaction ?
								</h5>
								<button
									type="button"
									className="close"
									data-dismiss="modal"
									aria-label="Close"
								>
									<span aria-hidden="true">&times;</span>
								</button>
							</div>
							<div className="modal-body">
								Are you sure you want to cancel this transaction? This can not
								be undone.
							</div>
							<div className="modal-footer">
								<button
									type="button"
									className="btn btn-light"
									data-dismiss="modal"
								>
									Cancel
								</button>
								<button
									onClick={this.onCancelTransaction}
									type="button"
									data-dismiss="modal"
									className="btn btn-default"
								>
									Continue
								</button>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default UserList;
