import React, { Component } from "react";
import { inject, observer } from "mobx-react";
import { Helmet } from "react-helmet";
import ActivePage from "../../partials/activepage";
import ReferralList from "./list";
import { utility } from "../../../utility/core";

@inject("userStore")
@observer
class Referral extends Component {
	state = {};
	render() {
		const title = "Referrals";
		return (
			<React.Fragment>
				<Helmet>
					<title>{title} | {utility.GetAppName()}</title>
				</Helmet>
				<ActivePage title={title} />

				<div className="row">
					<ReferralList title={title} />
				</div>
			</React.Fragment>
		);
	}
}

export default Referral;
