import { observable, action, configure, computed, runInAction } from "mobx";
import { userApi } from "../services/user.provider";
import auth from "../services/auth.provider";
import UserStoreModel from "./model/user.model.store";
import { toast } from "react-toastify";
import openSocket from "socket.io-client";
import { pushNotificationProvider } from "../services/PushNotificationService";
import { utility } from "../utility/core";
import { endpoint } from "../model/events";
import authProvider from "../services/auth.provider";

configure({ enforceActions: "observed" });

class UserStore {
	constructor() {
		const { id, admin, email } = auth.isAuthenticated() && auth.getUser();
		this.push = pushNotificationProvider;

		this.id = id;
		this.email = email;
		this.admin = admin;
		this.socket = openSocket(utility.getUrl() + "/users");
		if (admin) {
			this.push.requestPermission();
			this.renderEvents();
		}
	}

	renderEvents() {
		// on new user
		this.socket.on(endpoint.user.new_user, item => {
			this.push.generateNotification(
				"New Client Alert;",
				`${item.name} just ${utility.GetAppName()}; `
			);
			runInAction(() => {
				this.users.unshift(item);
				this.total++;
			});
		});

		// on suspend toggle
		this.socket.on(endpoint.user.suspended, item => {
			if (
				authProvider.isAuthenticated() &&
				authProvider.getUser().id === item.to
			) {
				const message = item.state
					? "Your account has been suspended. Contact the adminstrator for review."
					: "Your account suspend has been lifted.";
				this.push.generateNotification("Account Suspension", message);
				auth.logout();
				setTimeout(() => {
					document.location.href = utility.getUrl();
				}, 1000);
			}
			// remove user from list
			this.users.find(c => c.id === item.id).suspended = item.state;
		});

		// remove user
		this.socket.on(endpoint.user.removed, item => {
			runInAction(() => {
				// remove user from list
				this.users.remove(this.users.findIndex(c => c.id === item.id));
			});
		});
	}

	@observable
	userid = "";

	@observable
	users = [];

	@observable
	timelines = [];

	@observable
	isloading = false;

	page = 1;
	limt = 25;
	pages = 0;
	total = 0;
	@observable
	referral = {
		page: 1,
		pages: 0,
		total: 0,
		list: []
	};

	// users filter
	@observable
	filter = null;

	@observable
	errorMessage = "";
	@action
	getUsers = () => {
		// set loading
		runInAction(() => {
			this.errorMessage = "";
			this.isloading = true;
		});
		userApi.getUsers(this.page, this.limt).then(doc => {
			// update loading state
			runInAction(() => {
				this.isloading = false;
				if (doc.status === 200) {
					this.users = doc.docs.map(item => {
						return new UserStoreModel(item);
					});
					this.page = doc.page;
					this.limit = doc.limit;
					this.pages = doc.pages;
					this.total = doc.total;
				} else {
					this.errorMessage = doc.message;
				}
			});
		});
	};

	@action
	getSingleUser = async userId => {
		runInAction(() => {
			this.isloading = true;
			this.errorMessage = "";
		});
		const { doc, message, status } = await userApi.getUser(userId);
		runInAction(() => {
			this.isloading = false;
			if (status !== 200) {
				throw new Error(message);
			}
		});
		return doc;
	};

	// check if their more users to load
	@computed
	get hasMore() {
		return this.pages > this.page;
	}

	//load more
	@action
	loadMoreUser = page => {
		if (this.hasMore) {
			runInAction(() => {
				this.page = page;
				this.getUsers();
			});
		} else {
			this.errorMessage = "No more users to fetch.";
			toast(this.errorMessage, {});
		}
	};
	@action
	login = (email, password) => {
		runInAction(() => {
			this.isloading = true;
			this.errorMessage = "";
		});
		userApi.login(email, password).then(cb => {
			this.setLoading(false);
			if (cb.status === 200) {
				runInAction(() => {
					auth.login(cb.user, cb.token);
					this.isAuthenticated = true;
					this.setMessage("");
				});
			} else {
				this.setMessage(cb.message);
				this.setLoading(false);
			}
		});
	};

	@action
	checkAuthentication = () => {
		if (auth.isAuthenticated()) {
			runInAction(() => {
				this.isAuthenticated = true;
			});
		} else {
			runInAction(() => {
				this.isAuthenticated = false;
			});
		}
	};

	@computed
	get hasLoggedIn() {
		return auth.isAuthenticated();
	}

	@observable
	isAuthenticated = false;

	/**
	 * Update error message on store level
	 * @param {string} message new error message
	 */
	@action
	setMessage = message => {
		this.errorMessage = message;
	};
	@action
	setLoading = state => {
		this.isloading = state;
	};

	// remove user
	@action
	removeUser = id => {
		runInAction(() => {
			this.isloading = true;
			this.errorMessage = "";
		});
		userApi
			.removeAccount(id)
			.then(cb => {
				runInAction(() => {
					this.isloading = false;
					if (cb.status === 200) {
						this.socket.emit(endpoint.user.removed, { id });
						toast(cb.message, { type: "success" });
					} else {
						this.errorMessage = cb.message;
						toast(cb.message, {
							position: toast.POSITION.TOP_CENTER,
							type: toast.TYPE.ERROR
						});
					}
				});
			})
			.catch(() => {
				runInAction(() => {
					this.isloading = false;
					const message =
						"Failed! Check your internet connection and try again.";
					this.errorMessage = message;
					toast(message, {
						position: toast.POSITION.TOP_CENTER,
						type: toast.TYPE.ERROR
					});
				});
			});
	};

	// toggle suspension
	@action("Toggle account suspension state")
	updateAccountSuspensionState = (id, state) => {
		runInAction(() => {
			this.isloading = true;
			this.errorMessage = "";
			userApi
				.toggleAccount(id, state)
				.then(cb => {
					runInAction(() => {
						this.isloading = false;
						if (cb.status === 200) {
							this.socket.emit(endpoint.user.suspended, { id, state, to: id });
						} else {
							this.errorMessage = cb.message;
							toast(cb.message, {
								position: toast.POSITION.TOP_CENTER,
								type: toast.TYPE.ERROR
							});
						}
					});
				})
				.catch(() => {
					runInAction(() => {
						this.isloading = false;
						const message =
							"Failed! Check your internet connection and try again.";
						this.errorMessage = message;
						toast(message, {
							position: toast.POSITION.TOP_CENTER,
							type: toast.TYPE.ERROR
						});
					});
				});
		});
	};

	// get user referrals
	@action("Get user referral")
	getReferral = user => {
		runInAction(() => {
			this.isloading = true;
			this.errorMessage = "";
			this.userid = user || this.userid;
		});

		userApi
			.getReferrals(user, this.referral.page, this.limit)
			.then(cb => {
				runInAction(() => {
					this.isloading = false;
					if (cb.status === 200) {
						this.referral.list = cb.docs;
						this.referral.page = cb.page;
						this.referral.pages = cb.pages;
						this.referral.total = cb.total;
						if (cb.total === 0)
							this.errorMessage = "You've not referred anyone yet!";
					} else {
						this.isloading = false;
						this.errorMessage = cb.message;
					}
				});
			})
			.catch(() => {
				runInAction(() => {
					this.isloading = false;
					this.errorMessage =
						"Failed! Please check your internet connection and try again.";
				});
			});
	};

	@action
	checkAccountStatus = async user => {
		runInAction(() => {
			this.isloading = true;
			this.errorMessage = "";
		});
		const { doc, status, message } = await userApi.checkAccountFundStatus(user);
		if (status === 200) {
			runInAction(() => {
				this.isloading = false;
			});
			if (status === 200) {
				return doc;
			} else {
				this.errorMessage = message;
				toast(message, {
					position: toast.POSITION.TOP_CENTER,
					type: toast.TYPE.ERROR
				});
			}
		}
	};
	@action
	getExpectedPercent = (percent, amount) => {
		return Math.floor((percent / 100) * amount).toFixed(1);
	};

	@action("Update user account information")
	updateInformation = (id, name, address, zip, city) => {
		runInAction(() => {
			this.isloading = true;
			this.errorMessage = "";
		});
		userApi
			.UpdateInformation(id, name, address, zip, city)
			.then(cb => {
				runInAction(() => {
					this.isloading = false;
					if (cb.status === 200) {
						auth.updateUser({ name, address, zip, city });
						toast(cb.message, {
							position: toast.POSITION.TOP_CENTER,
							type: toast.TYPE.SUCCESS
						});
					} else {
						toast(cb.message, {
							position: toast.POSITION.TOP_CENTER,
							type: toast.TYPE.ERROR
						});
					}
				});
			})
			.catch(() => {
				runInAction(() => {
					this.isloading = false;
					this.errorMessage =
						"Failed! Please check your internet connection and try again later.";
				});
			});
	};

	@action("get user recent activity")
	getTimeLineList = id => {
		runInAction(() => {
			this.isloading = true;
			this.errorMessage = "";
		});
		userApi
			.getRecentActivities(id, 1, 10)
			.then(cb => {
				runInAction(() => {
					this.isloading = false;
					if (cb.status === 200) {
						this.timelines = cb.docs;
					} else {
						toast(cb.message, {
							position: toast.POSITION.TOP_CENTER,
							type: toast.TYPE.ERROR
						});
					}
				});
			})
			.catch(() => {
				runInAction(() => {
					this.isloading = false;
					this.errorMessage =
						"Failed! Please check your internet connection and try again later.";
				});
			});
	};

	@action
	signup = async (ref, name, pwd, email, wallet, plan) => {
		runInAction(() => {
			this.isloading = true;
			this.errorMessage = "";
		});
		const { status, doc, message } = await userApi.createUser(
			ref,
			name,
			email,
			wallet,
			plan,
			pwd
		);
		if (status !== 200) throw new Error(message);
		runInAction(() => {
			this.isloading = false;
		});
		// notify all admin.
		this.socket.emit(endpoint.user.new_user, doc);
		return doc;
	};
	/** verify user's account
	 * @param {string} user user's id
	 */
	@action
	vAccount = user => {
		runInAction(() => {
			this.isloading = false;
			this.errorMessage = "";
		});
		userApi
			.verifyUserAccount(user)
			.then(cb => {
				runInAction(() => {
					this.isloading = false;
					if (cb.status !== 200) {
						this.errorMessage = cb.message;
					} else {
						toast(cb.message, { type: "success" });
					}
				});
			})
			.catch(err => {
				runInAction(() => {
					this.isloading = false;
					this.errorMessage =
						err.message ||
						"Failed! Check your internet connection and try again.";
				});
			});
	};

	@action
	reSendEmailConfirmation = (id, name, email) => {
		runInAction(() => {
			this.isloading = true;
			this.errorMessage = "";
		});
		userApi
			.sendVerificationLink(id, name, email)
			.then(cb => {
				runInAction(() => {
					this.isloading = false;
				});
				if (cb.status === 200) {
					toast(cb.message, {
						position: toast.POSITION.TOP_RIGHT,
						type: toast.TYPE.SUCCESS
					});
				} else {
					toast(cb.message, {
						position: toast.POSITION.TOP_RIGHT,
						type: toast.TYPE.ERROR
					});
				}
				// update existing account
			})
			.catch(err => {
				runInAction(() => {
					this.isloading = false;
					toast(err.message, {
						position: toast.POSITION.TOP_CENTER,
						type: toast.TYPE.ERROR
					});
				});
			});
	};

	@action
	loadMoreReferral = page => {
		runInAction(() => {
			this.referral.page = page;
		});
		this.getReferral(null);
	};

	@computed
	get getUsersRecord() {
		if (this.filter !== null) {
			return this.users.filter(c => c.suspended === this.filter);
		} else {
			return this.users;
		}
	}

	@action
	updateFilter = state => {
		this.filter = state;
	};
	@action
	searchUserUsingEmail = email => {
		runInAction(() => {
			this.isloading = true;
			this.errorMessage = "";
		});
		this.users.clear();
		userApi
			.searchUser(email)
			.then(cb => {
				this.setLoading(false);
				if (cb.status === 200) {
					runInAction(() => {
						this.users = cb.docs.map(item => {
							return new UserStoreModel(item);
						});
						this.page = cb.page;
						this.limit = cb.limit;
						this.pages = cb.pages;
						this.total = cb.total;
					});
				} else {
					toast(cb.message, {
						type: "error"
					});
					this.errorMessage = cb.message;
				}
			})
			.catch(this.handleFailed);
	};

	handleFailed = error => {
		runInAction(() => {
			this.isloading = false;
			toast(error.message, {
				type: "error"
			});
		});
	};

	@action("Password reset link")
	SendPasswordResetLink = email => {
		runInAction(() => {
			this.isloading = true;
			this.errorMessage = "";
		});
		userApi
			.SendPasswordResetLink(email)
			.then(cb => {
				this.setLoading(false);
				if (cb.status === 200) {
					alert(cb.message);
					document.location.href = "/login";
				} else {
					this.setMessage(cb.message);
				}
			})
			.catch(this.handleFailed);
	};

	@action("new password")
	NewPasswordUpdate = (id, email, code, resetId, password) => {
		runInAction(() => {
			this.isloading = true;
			this.errorMessage = "";
		});
		userApi
			.NewPassword(id, email, code, resetId, password)
			.then(cb => {
				this.setLoading(false);
				if (cb.status === 200) {
					alert(cb.message);
					document.location.href = "/login";
				} else {
					this.setMessage(cb.message);
				}
			})
			.catch(this.handleFailed);
	};
}

// new instance of user store
export const userStore = new UserStore();
