import React, { PureComponent } from "react";
import { toast } from "react-toastify";

/**
 * @requires pages total page in the document
 * @requires page current page number
 * @requires limit page size
 * @requires total total document number
 * @requires length the total document
 * @requires onClick a function that get triggered each time the next, previous or page number in clicked
 */
class PageProperty extends PureComponent {
	toArray = () => {
		let new_a = [];
		for (let i = 0; i < this.props.pages; i++) {
			new_a.push(i + 1);
		}
		return new_a;
	};
	hasMore = () => {
		return this.props.pages > this.props.page || this.props.page > 1;
	};
	loadMore = i => {
		if (this.props.page !== i) {
			if (this.hasMore()) {
				this.props.onClick(i);
			} else {
				toast("No more record to load!", {
					type: "info",
					position: "bottom-right"
				});
			}
		}
	};
	getPageNumber = (page, limit) => {
		return (page - 1) * limit + 1;
	};
	render() {
		const { page, length, limit, total, pages } = this.props;
		if (length === 0) {
			return null;
		}
		return (
			<div className="row mt-3 mb-3">
				<div className="col-sm-12 col-md-5 ">
					<div
						className="dataTables_info"
						id="dataTable_info"
						role="status"
						aria-live="polite"
					>
						Showing {this.getPageNumber(page, limit)} to{" "}
						{this.getPageNumber(page, limit) + length - 1} of {total} entries
					</div>
				</div>
				<div className="col-sm-12 col-md-7">
					<div
						className="dataTables_paginate paging_simple_numbers"
						id="dataTable_paginate"
					>
						<ul className="pagination justify-content-end">
							<li
								className={`paginate_button page-item previous ${page === 1 &&
									"disabled"}`}
								id="dataTable_previous"
							>
								<button
									type="button"
									aria-controls="dataTable"
									onClick={() => this.loadMore(page - 1)}
									data-dt-idx="0"
									tabIndex="0"
									className="page-link"
								>
									<i className="icon-arrow-left icons" /> Previous
								</button>
							</li>
							{this.toArray().map(i => (
								<li
									key={i}
									className={`paginate_button page-item ${i === page &&
										"active"}`}
								>
									<button
										type="button"
										onClick={() => this.loadMore(i)}
										aria-controls="dataTable"
										data-dt-idx="2"
										tabIndex="0"
										className="page-link"
									>
										{i}
									</button>
								</li>
							))}

							<li
								className={`paginate_button page-item next ${pages === page &&
									"disabled"}`}
								id="dataTable_next"
							>
								<button
									type="button"
									onClick={() => this.loadMore(page + 1)}
									aria-controls="dataTable"
									data-dt-idx="3"
									tabIndex="0"
									className="page-link"
								>
									Next <i className="icon-arrow-right icons" />
								</button>
							</li>
						</ul>
					</div>
				</div>
			</div>
		);
	}
}

export default PageProperty;
